import axios from "axios";

declare var window: any

const CHECK_TOKEN_VALIDITY_URL = `${process.env.REACT_APP_API_URL}/check-auth-token-validity.php`

export function checkAuthTokenValidity(authToken: string, userID: string) {

  return axios({
    method: 'post',
    url: CHECK_TOKEN_VALIDITY_URL,
    headers: {
      'content-type': 'application/x-www-form-urlencoded',
    },
    data: {
      authToken,
      userID
    }
  })
}

