import {useState} from "react";
import { Button } from '@mui/material';
import { Dialog } from '@mui/material';
import { DialogActions } from '@mui/material';
import { DialogContent } from '@mui/material';
import { DialogTitle } from '@mui/material';
import { toAbsoluteUrl } from "_metronic/helpers";
import { useSelector } from "react-redux";
import { RootState } from "setup";

const dialoHeaderMap: any = {
    '0' : 'GOLD TIER',
    '1' : 'PLATINUM TIER',
    '2' : 'DIAMOND TIER'
}

const blockchainMap: any = {
    'ethereum': 'Ethereum (+ ETH for gas)',
    'klaytn': 'Klaytn (+ KLAY for gas)'
} 

export const ReadMoreDialog = (props: any) => {

    const [loading, setLoading] = useState<boolean>(false)
    const [openConfirmPopup, setOpenConfirmPopup] = useState(false)

    const { walletAddress, userID }: any = useSelector<RootState>(
        ({auth}) => ({
            walletAddress: auth.walletAddress,
            userID: auth.user?.userID
        })
    )

    const shrinkWalletAddress = (walletAddress: string) => {
        const walletStart = walletAddress.slice(0, 6)
        const walletEnd = walletAddress.slice(35)
    
        return walletStart + '......' + walletEnd
    }

    const getOrderSuffix = (walletAddress: string) => {
        const walletEnd = walletAddress.slice(35)
    
        return walletEnd
    }

    const showPopup = () => {
        var popup = document.getElementById("connectWallet")!;
        popup.classList.toggle("show");
    }

    const openDialog = (id: string) => {
        props.setDialogOpen(false)
        const element: HTMLElement = document.getElementById(id)!
        element.style.display='block'
    }

    const closeDialog = (id: string) => {
        const element: HTMLElement = document.getElementById(id)!
        element.style.display='none'
    }

    const renderGoldSection = () => {
        return (
            <figcaption 
                className="px-4 pt-2" 
                style={{ background: 'linear-gradient(87.65deg, rgb(234, 209, 168) -1.34%, rgba(234, 209, 168, 0.2) 73.33%, rgb(234, 209, 168) 104.02%)' }}
            >
                <div className="d-flex justify-content-between">
                    <h6>GOLD</h6>
                    <h6>SINGLE</h6>
                </div>
            </figcaption>
        )
    }

    const renderPlatinumSection = () => {
        return (
            <figcaption 
                className="px-4 pt-2" 
                style={{ background: 'linear-gradient(87.65deg, rgb(207, 216, 226) -1.34%, rgba(207, 216, 226, 0.2) 73.33%, rgb(207, 216, 226) 104.02%)' }}
            >
                <div className="d-flex justify-content-between">
                    <h6>PLATINUM</h6>
                    <h6>SINGLE</h6>
                </div>
            </figcaption>
        )
    }

    const renderDiamondSection = () => {
        return (
            <figcaption 
                className="px-4 pt-2" 
                style={{ background: 'linear-gradient(87.65deg, rgb(193, 222, 228) -1.34%, rgba(193, 222, 228, 0.2) 73.33%, rgb(193, 222, 228) 104.02%)' }}
            >
                <div className="d-flex justify-content-between">
                <h6>Diamond</h6>
                <h6>SINGLE</h6>
                </div>
            </figcaption>
        )
    }

    return (
        <>
            <Dialog open={props.readMoreDialogOpen} onClose={() => props.setReadMoreDialogOpen(false)} aria-labelledby="form-dialog-title">
                <DialogTitle className='px-5 text-center fw-bolder text-muted' id="form-dialog-title">
                    {dialoHeaderMap[props.readMoreTier]}
                </DialogTitle>
                <div className='separator'></div>
                <DialogContent>
                    <div className="d-flex flex-column justify-content-center">
                        <div 
                            className="d-flex flex-column mx-auto"
                            style={{ 
                                marginTop: '0px', 
                                marginBottom: '20px', 
                                maxWidth: '200px', 
                                height: 'auto',
                            }}
                        >
                            <div className="shadow-xl">
                                <figure 
                                    className="" 
                                    style={{ width: '100%', marginBottom: '0px' }}
                                >
                                    <img 
                                        className="w-100" 
                                        src={`${process.env.REACT_APP_API_TOKEN_IMAGE_URL}/${props.readMoreImage}`} 
                                        // srcSet={dummyData[index].srcSet}
                                    />
                                    {props.readMoreTier == '0' && 
                                        renderGoldSection()
                                    }
                                    {props.readMoreTier == '1' && 
                                        renderPlatinumSection()
                                    }
                                    {props.readMoreTier == '2' && 
                                        renderDiamondSection()
                                    }
                                </figure>
                            </div>
                        </div>

                        <div className="flex flex-column mt-5">
                            <div className="mb-3">
                                <span className="fw-bold">PAYMENT METHODS</span>
                            </div>
                            
                            <div className="d-flex">
                                <div className="d-flex">
                                    <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi text-primary bi-credit-card-fill" viewBox="0 0 16 16">
                                        <path d="M0 4a2 2 0 0 1 2-2h12a2 2 0 0 1 2 2v1H0V4zm0 3v5a2 2 0 0 0 2 2h12a2 2 0 0 0 2-2V7H0zm3 2h1a1 1 0 0 1 1 1v1a1 1 0 0 1-1 1H3a1 1 0 0 1-1-1v-1a1 1 0 0 1 1-1z"/>
                                    </svg>
                                    <span className="ms-3">{"Credit & Debit Card"}</span>
                                </div>

                                <div className="d-flex ms-10">
                                    <svg xmlns="http://www.w3.org/2000/svg"  viewBox="0 0 48 48" width="18px" height="18px" className="mb-2">
                                        <path fill="#7880e7" d="M11,24L25,2l14,22l-14,8L11,24z"/>
                                        <path fill="#5c64c7" d="M25,2l14,22l-14,8V2z"/>
                                        <path fill="#7880e7" d="M11,27l14,8l14-8L25,46L11,27z"/>
                                        <path fill="#5c64c7" d="M25,35l14-8L25,46V35z M11,24l14-6l14,6l-14,8L11,24z"/>
                                        <path fill="#2a3192" d="M25,18l14,6l-14,8V18z"/>
                                    </svg>
                                    <span className="ms-2">{blockchainMap[props.readMoreBlockchain]}</span>
                                </div>
                            </div>
                        </div>

                        <div className='separator my-5'></div>

                        <div className="d-flex flex-column w-500px">
                            <span className="fw-bold mb-3">TOKEN BENEFITS:</span>

                            {props.readMoreBenefits.map((data: any) => (
                                <>
                                    <div className="d-flex">
                                        <span></span>
                                        <span className="ms-4">{data}</span>
                                    </div>
                                </>
                            ))}
                        </div>

                    </div>
                </DialogContent>

                {/* <div className='separator mb-5'></div>

                <div className="me-5 mt-5 mb-5 text-center">
                    <span 
                        onClick={() => props.setDialogOpen(false)}
                        className="cursor-pointer"
                        style={{ fontWeight: '500' }}
                    >
                        Cancel
                    </span>
                </div> */}
            </Dialog>

            {/* <div id={`checkout-confirmation`} style={{ display: 'none', zIndex: '2' }} className="modal">
                <span onClick={() => closeDialog(`checkout-confirmation`)} className="close" title="Close Modal">&times;</span>
                <form className="modal-content">
                    <div className="px-7 py-7">
                        <h3>Proceed to checkout</h3>
                        <p className='font-size-15'>
                            Are you sure you want to proceed to the checkout page?
                        </p>

                        <div className="d-flex">
                            <button onClick={() => handleCheckoutSession()} type="button" className="btn btn-primary">
                                Proceed
                                {loading && 
                                    <span className='spinner-border spinner-border-sm align-middle ms-2'></span>
                                }
                            </button>
                            <button onClick={() => closeDialog(`checkout-confirmation`)} type="button" className="btn btn-danger ms-3">
                                Cancel
                            </button>
                        </div>
                    </div>
                </form>
            </div> */}
        </>
    )
}