import axios from 'axios'

const API_URL = process.env.REACT_APP_API_URL || 'api'

export const LOGIN_URL = `${API_URL}/login.php`
export const REGISTER_URL = `${API_URL}/create-user.php`
export const REQUEST_PASSWORD_URL = `${API_URL}/forgot-password.php`
export const PASSWORD_RESET_URL = `${API_URL}/reset-password.php`
export const VERIFY_USER_URL = `${API_URL}/verify-user.php`
export const RESEND_VERIF_EMAIL_URL = `${API_URL}/send-verification-email.php`;
export const CHANGE_NOTIFICATION_SETTING_URL = `${API_URL}/update-notification-settings.php`;

export function login(email: string, password: string) {
  return axios({
    method: 'post',
    url: LOGIN_URL,
    headers: {
      'content-type': 'application/x-www-form-urlencoded',
    },
    data: {
      email,
      password
    }
  })
}

export function register(email: string, fullname: string, username: string, password: string) {
  return axios({
    method: 'post',
    url: REGISTER_URL,
    headers: {
      'content-type': 'application/x-www-form-urlencoded',
    },
    data: {
      email,
      fullname,
      username,
      password
    }
  })
}

export function requestPassword(email: string) {
  return axios({
    method: 'post',
    url: REQUEST_PASSWORD_URL,
    headers: {
      'content-type': 'application/x-www-form-urlencoded',
    },
    data: {
      email
    }
  })
}

export function resetPassword(password: string, cpassword: string, token: string) {
  return axios({
    method: 'post',
    url: PASSWORD_RESET_URL,
    headers: {
      'content-type': 'application/x-www-form-urlencoded',
    },
    data: {
      password,
      cpassword,
      token
    }
  })
}

export function verifyUser(token: string | null, authToken: string) {
  return axios({
    method: 'post',
    url: VERIFY_USER_URL,
    headers: {
      'content-type': 'application/x-www-form-urlencoded',
      'Authorization': authToken

    },
    data: {
      token,
      authToken
    }
  })
}

export function resendVerificationEmail(email: string, authToken: string) {
  return axios({
    method: 'post',
    url: RESEND_VERIF_EMAIL_URL,
    headers: {
      'content-type': 'application/x-www-form-urlencoded',
      'Authorization': authToken
    },
    data: {
      email,
      authToken
    }
  })
}

export function changeNotificationSettings(
  dataString: string, 
  minBidThreshold: string, 
  authToken: string, 
  userID: string,
  language: string,
  timezone: string
  ) {
  return axios({
    method: 'post',
    url: CHANGE_NOTIFICATION_SETTING_URL,
    headers: {
      'content-type': 'application/x-www-form-urlencoded',
      'Authorization': authToken
    },
    data: {
      dataString,
      minBidThreshold,
      userID,
      authToken,
      language,
      timezone
    }
  })
}