/* eslint-disable jsx-a11y/anchor-is-valid */
import { FC, useState } from 'react'
import clsx from 'clsx'
import { getAllOrderData } from '../api'
import { FormattedMessage } from 'react-intl'
import { KTSVG } from '_metronic/helpers'

type Props = {
    totalNumberOfPages: number
    fetchNextData: Function
    selectedPage: any
    setSelectedPage: any
    currentPage: any
    setCurrentPage: any
}

const OrderListPagination: FC<Props> = ({ 
    totalNumberOfPages, 
    setSelectedPage, 
    fetchNextData, 
    selectedPage, 
    currentPage,
    setCurrentPage
}) => {

    const handleChange = (event: any) => {
        setSelectedPage(event.target.value)
    }

    return (
        <div className='row'>
            <div className='d-flex'>
                {currentPage > 1 && 
                    <button 
                        style={{ 
                            border: '#0000',
                            background: '#ffd600',
                            borderRadius: '5px',
                            color: '#000',
                            height: '30px',
                            fontSize: '15px'
                        }}
                        onClick={() => fetchNextData(selectedPage - 1)}
                        disabled={selectedPage == 1}
                    >
                        <FormattedMessage id='PAGINATION.PREV' />
                    </button>
                }
                <div className='d-flex my-auto'>
                    <div className='ms-4 d-flex flex-column'>
                        <input 
                            type="text" 
                            style={{ width: '40px', height: '28px', borderColor: '#0000', textAlign: 'center' }}
                            value={ selectedPage }
                            onChange={(event) => handleChange(event)}
                            disabled={totalNumberOfPages <= 1}
                        />
                    </div>
                    
                    <span style={{ marginTop: '5px' }} className='ms-2 me-3'>Of</span>
                    <span style={{ marginTop: '5px' }} className='ms-1 me-4'>{totalNumberOfPages}</span>
                </div>
                {currentPage < totalNumberOfPages && 
                    <button 
                        style={{ 
                            border: '#0000',
                            background: '#ffd600',
                            borderRadius: '5px',
                            color: '#000',
                            height: '30px',
                            fontSize: '15px'
                        }}
                        onClick={() => fetchNextData(parseInt(selectedPage) + 1)}
                        disabled={selectedPage == totalNumberOfPages}
                    >
                        <FormattedMessage id='PAGINATION.NEXT' />
                    </button>
                }

                {totalNumberOfPages > 1 && 
                    <button 
                        className='ms-4'
                        style={{ 
                            border: '#0000',
                            background: '#ffd600',
                            borderRadius: '5px',
                            color: '#000',
                            height: '30px',
                            fontSize: '15px'
                        }}
                        onClick={() => fetchNextData(selectedPage)}
                    >
                        <FormattedMessage id='PAGINATION.GO_TO' />
                    </button>
                }
            </div>
        </div>
    )
}

export {OrderListPagination}
