import React, {useEffect, useState} from "react";
import {useSelector, shallowEqual, useDispatch, connect, } from 'react-redux'
import CircularProgress from '@mui/material/CircularProgress';
import {Route, Navigate} from 'react-router-dom'
import { ExplorePageLarge } from "./ExplorePageLarge";
import { ExplorePageSmall } from "./ExplorePageSmall";
import {IProductModel} from './models/ProductModel'
import {RootState} from '../../../../setup'
import {get_patents_categories, get_patents_and_metas} from "../redux/ecomCrud"
import {actions} from "../redux/ecomRedux"
import * as auth from "../../auth/redux/AuthRedux"
import { getCollectionMeta } from "../redux/ecomCrud";
import { getArtistsData } from 'app/modules/artistWizard/api'


function ProductsOverview() {

    const [loading, setLoading] = useState<boolean>(true)
    const [filtering, setFiltering] = useState<boolean>(false)
    const [artistList, setArtistList] = useState<Array<any>>([])
    const [selectedArtist, setSelectedArtist] = useState<string | null>(null)

    const [blockchainFilterVal, setBlockchainFilterVal] = useState<string | null>(null)
    const [artistFilterVal, setArtistFilterVal] = useState<string | null>(null)

    const [blockchainFilterAdded, setBlockchainFilterAdded] = useState<boolean>(false)
    const [artistFilterAdded, setArtistFilterAdded] = useState<boolean>(false)
    
    const { isVerified, authToken }: any = useSelector<RootState>(
        ({auth}) => ({
            authToken: auth.accessToken,
            isVerified: auth.user?.isVerified
        }), shallowEqual
    )

    const { collectionsMeta }: any = useSelector<RootState>(
        ({ecom}) => ({
            collectionsMeta: ecom.collectionsMeta,
        })
    )

    const dispatch = useDispatch()

    useEffect(() => {
        getCollectionMeta(artistFilterVal, blockchainFilterVal)
          .then((response) => {
              const collectionMeta = response.data.collectionsMeta
              dispatch(actions.setCollections(collectionMeta))
              setLoading(false)
          }).catch((error) => {
              localStorage.setItem('sessionTimeOut', 'Session timed out')
              dispatch(auth.actions.logout())
          })
    }, [blockchainFilterVal, artistFilterVal])

    useEffect(() => {
        getArtistsData()
          .then((response) => {
              const _artistData = response.data.artist_data
              setArtistList(_artistData)
          }).catch((error) => {
              localStorage.setItem('sessionTimeOut', 'Session timed out')
              // dispatch(auth.actions.logout())
          })
      }, [])


    return (
        <>
        {collectionsMeta &&
        <>
            <ExplorePageLarge
                loading={loading}
                setLoading={setLoading}
                collectionsMeta={collectionsMeta}
                artistList={artistList}
                selectedArtist={selectedArtist}
                setSelectedArtist={setSelectedArtist}
                blockchainFilterVal={blockchainFilterVal}
                setBlockchainFilterVal={setBlockchainFilterVal}
                artistFilterVal={artistFilterVal}
                setArtistFilterVal={setArtistFilterVal}
                blockchainFilterAdded={blockchainFilterAdded}
                setBlockchainFilterAdded={setBlockchainFilterAdded}
                artistFilterAdded={artistFilterAdded}
                setArtistFilterAdded={setArtistFilterAdded}
            />
            <ExplorePageSmall 
                loading={loading}
                setLoading={setLoading}
                collectionsMeta={collectionsMeta}
                artistList={artistList}
                selectedArtist={selectedArtist}
                setSelectedArtist={setSelectedArtist}
                blockchainFilterVal={blockchainFilterVal}
                setBlockchainFilterVal={setBlockchainFilterVal}
                artistFilterVal={artistFilterVal}
                setArtistFilterVal={setArtistFilterVal}
                blockchainFilterAdded={blockchainFilterAdded}
                setBlockchainFilterAdded={setBlockchainFilterAdded}
                artistFilterAdded={artistFilterAdded}
                setArtistFilterAdded={setArtistFilterAdded}
            />
        </>
        }

        {/* {loading && 
            <div className='d-flex w-100 h-100'>
                <div className='my-auto mx-auto'>
                    <div className='d-flex flex-column justify-content-center'>
                        <span className='text-primary fs-3'>Loading</span> <br />
                        <span className='ms-4'>
                            <CircularProgress />
                        </span>
                    </div>
                </div>
            </div>
        } */}
        </>
    )
}

export default ProductsOverview;