import {useState} from 'react'
import { useSelector, shallowEqual } from 'react-redux'
import { useNavigate } from 'react-router'
import {toAbsoluteUrl} from '../../../../../_metronic/helpers'
import { RootState } from '../../../../../setup'
import {FormattedMessage} from 'react-intl'

export interface UserListModel {
    id: string
    userName: string
    imageUrl: string
    email: string
    checked?: boolean
    isVerified: any
    isAdmin: any
    joinedDate: string
    noOfOrders: any
    handleChange?: any
}

export const UserListRow = (props: UserListModel) => {
    
    const navigate = useNavigate()

    const {isAdmin}: any = useSelector<RootState>(
        ({auth}) => ({
            isAdmin: auth.user?.isAdmin
        })
    )

    const handleClick = (id: string) => {
        navigate('/user-detail', {state: id})
    }

    return (
        <>
            <tr>
                <td>
                    <div className="ms-3 form-check form-check-sm form-check-custom form-check-solid">
                        <input 
                            id='kt_table_users' 
                            onChange={(e) => props.handleChange(e)} 
                            className="form-check-input" 
                            type="checkbox" 
                            checked={props.checked}
                            value={props.id} 
                            />
                    </div>
                </td>
                <td className="d-flex align-items-center">
                    <div className="symbol symbol-circle symbol-50px overflow-hidden me-3">
                        <a href="#">
                            <div className="symbol-label">
                                <img src={`${process.env.REACT_APP_API_USER_IMAGE_URL}/${props.imageUrl}`} alt={props.userName} className="w-100" />
                            </div>
                        </a>
                    </div>
                    <div className="d-flex flex-column">
                        <div onClick={() => handleClick(props.id)} className="text-gray-800 text-hover-primary mb-1 cursor-pointer">{props.userName}</div>
                        <span>{props.email}</span>
                    </div>
                </td>
                <td className="text-gray-800 text-center">{props.noOfOrders}</td>
                <td>
                    <span className='text-center text-dark fw-bolder text-hover-primary d-block mb-1 fs-6'>
                        {props.isVerified == '1' ? (
                            <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-check-circle-fill text-success" viewBox="0 0 16 16">
                                <path d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0zm-3.97-3.03a.75.75 0 0 0-1.08.022L7.477 9.417 5.384 7.323a.75.75 0 0 0-1.06 1.06L6.97 11.03a.75.75 0 0 0 1.079-.02l3.992-4.99a.75.75 0 0 0-.01-1.05z"/>
                            </svg>
                        ) : (
                            <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-x-circle-fill text-danger" viewBox="0 0 16 16">
                                <path d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0zM5.354 4.646a.5.5 0 1 0-.708.708L7.293 8l-2.647 2.646a.5.5 0 0 0 .708.708L8 8.707l2.646 2.647a.5.5 0 0 0 .708-.708L8.707 8l2.647-2.646a.5.5 0 0 0-.708-.708L8 7.293 5.354 4.646z"/>
                            </svg>
                        )}
                    </span>
                </td>
                <td>
                    <span className='text-center text-dark fw-bolder text-hover-primary d-block mb-1 fs-6'>
                        {props.isAdmin == '1' ? (
                            <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-check-circle-fill text-success" viewBox="0 0 16 16">
                                <path d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0zm-3.97-3.03a.75.75 0 0 0-1.08.022L7.477 9.417 5.384 7.323a.75.75 0 0 0-1.06 1.06L6.97 11.03a.75.75 0 0 0 1.079-.02l3.992-4.99a.75.75 0 0 0-.01-1.05z"/>
                            </svg>
                        ) : (
                            <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-x-circle-fill text-danger" viewBox="0 0 16 16">
                                <path d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0zM5.354 4.646a.5.5 0 1 0-.708.708L7.293 8l-2.647 2.646a.5.5 0 0 0 .708.708L8 8.707l2.646 2.647a.5.5 0 0 0 .708-.708L8.707 8l2.647-2.646a.5.5 0 0 0-.708-.708L8 7.293 5.354 4.646z"/>
                            </svg>
                        )}
                    </span>
                </td>
                {/* <td>
                    <div className="badge badge-light fw-bolder">{props.lastLogin}</div>
                </td> */}
                <td className="text-gray-800 text-center">{props.joinedDate}</td>
                <td className="px-3 text-gray-800 text-center">
                    <div 
                        className="btn btn-light btn-active-light-primary btn-sm" 
                    >
                        <div className="px-3">
                            <span onClick={() => handleClick(props.id)}  className="px-3">View and Edit</span>
                        </div>
                    </div>
                </td>
            </tr>
        </>
    )
}