export const LatestDrops = () => {

    const [style, changeStyle] = useState(customStyle)
    const [collectionData, setCollectionData] = useState<Array<any>>([])
    const [loading, setLoading] = useState<boolean>(true)

    const handleMouseLeave = () => {
        changeStyle(customStyle);
    }

    const handleMouseEnter = () => {
        const newStyle = {
            borderColor: '#ca8837',
            color: '#ffffff',
            backgroundColor: '#ca8837'
        }
        changeStyle(newStyle)
    }

    useEffect(() => {
        getCollectionDataForHomePage()
        .then((response) => {
            if(response.data.success) {
                setCollectionData(response.data.collectionsMeta)
                setLoading(false)
            }
        })
    }, [])
    
    return (
        <>
        <div id="bio" className="vc_row wpb_row vc_row-fluid">
                <div className="wpb_column vc_column_container vc_col-sm-12">
                    <div className="vc_column-inner">
                        <div className="wpb_wrapper">
                            <h2 
                                style={{ textAlign: 'center', color: '#000' }} 
                                className="vc_custom_heading vc_custom_1505769344824 fit_bg_text  fit_bg_center"
                            >
                                <FormattedMessage id="HOME_PAGE.LATEST_DROPS" />
                            </h2>
                            {!loading && 
                                <div className="d-flex justify-content-center">
                                    <div 
                                        style={{ width: '80%' }}
                                        className="d-flex flex-wrap justify-content-evenly"
                                    >
                                        {collectionData.map((data, index) => (
                                            <DropCard key={index} {...data} />
                                        ))}
                                    </div>
                                </div>
                            }
                            {loading && 
                                <div className='d-flex w-100 h-100'>
                                    <div className='my-auto mx-auto'>
                                        <div className='d-flex flex-column justify-content-center'>
                                            <span className='text-primary fs-3'>Loading</span> <br />
                                            <span className='ms-4'>
                                                <CircularProgress />
                                            </span>
                                        </div>
                                    </div>
                                </div>
                            }
                            <div className="vc_btn3-container  indie-button vc_btn3-center vc_custom_1505768276260 ">
                                <a  
                                    style={{
                                        borderColor: '#ffd600',
                                        color: '#000',
                                        backgroundColor: '#ffd600',
                                        width: '15%'
                                    }} 
                                    className="py-3 px-10 rounded mx-auto" 
                                    href="explore" 
                                    title=""
                                >
                                    <FormattedMessage id="HOME_PAGE.VIEW_IN_EXP" />
                                    {/* View in Explorer */}
                                </a>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}