/* eslint-disable jsx-a11y/anchor-is-valid */
import React, {useState, useEffect} from 'react'
import {useDispatch} from 'react-redux'
import * as Yup from 'yup'
import clsx from 'clsx'
import {Link} from 'react-router-dom'
import {useFormik} from 'formik'
import * as auth from '../redux/AuthRedux'
import {login} from '../redux/AuthCRUD'
import {AlertSuccess, AlertDanger} from '../../alerts/Alerts'
import { FormattedMessage, useIntl } from 'react-intl'

const initialValues = {
  email: '',
  password: '',
}

const API_KEY = `${process.env.REACT_APP_EXCHANGE_RATES_API_KEY}`;

/*
  Formik+YUP+Typescript:
  https://jaredpalmer.com/formik/docs/tutorial#getfieldprops
  https://medium.com/@maurice.de.beijer/yup-validation-and-typescript-and-formik-6c342578a20e
*/

export function Login() {
  const [loading, setLoading] = useState(false)
  const [checked, setChecked] = useState(true)
  let msg: any = localStorage.getItem("message");
  const [message, setMessage] = useState("");

  const intl = useIntl()

  const loginSchema = Yup.object().shape({
    email: Yup.string()
      .email(intl.formatMessage({id: 'PROFILE_DETAIL_SCHEMA.EMAIL.WRONG_FORMAT'}))
      .min(3, intl.formatMessage({id: 'PROFILE_DETAIL_SCHEMA.EMAIL.MIN'}))
      .max(50, intl.formatMessage({id: 'PROFILE_DETAIL_SCHEMA.EMAIL.MAX'}))
      .required(intl.formatMessage({id: 'PROFILE_DETAIL_SCHEMA.EMAIL.REQUIRED'})),
    password: Yup.string()
      .min(3, intl.formatMessage({id: 'PROFILE_DETAIL_SCHEMA.EMAIL.MIN'}))
      .max(50, intl.formatMessage({id: 'PROFILE_DETAIL_SCHEMA.EMAIL.MAX'}))
      .required(intl.formatMessage({id: 'PROFILE_DETAIL_SCHEMA.PASSWORD.REQUIRED'})),
  })

  useEffect(() => {
    setMessage(msg)
  }, [])

  let sesMessage = localStorage.getItem('sessionTimeOut');
  const [sessionMessage, setSessionMessage] = useState(sesMessage);

  const [responseMessage, setResponseMessage] = useState("");

  if(msg) {
    setTimeout(() => {
      localStorage.removeItem("message");
      setChecked(false);
      setTimeout(() => {
        setMessage("");
      }, 200);
    }, 5000);
  }

  if(sesMessage) {
    setTimeout(() => {
      localStorage.removeItem("sessionTimeOut");
      setChecked(false);
      setTimeout(() => {
        setSessionMessage("");
      }, 200);
    }, 5000);
  }

  if(responseMessage) {
    setTimeout(() => {
      setChecked(false);
      setTimeout(() => {
        setResponseMessage("");
      }, 200);
    }, 5000);
  }

  const dispatch = useDispatch()
  const formik = useFormik({
    initialValues,
    validationSchema: loginSchema,
    onSubmit: (values, {setStatus, setSubmitting}) => {
      setLoading(true)
      setTimeout(() => {
        login(values.email, values.password)
          .then((response) => {
            if(response.data.auth_success) {
              const authToken = response.data.token;
              const user = {
                userID: response.data.user.id,
                userImage: response.data.user.avatar,
                fullname: response.data.user.full_name,
                username: response.data.user.username,
                email: response.data.user.email,
                isVerified: response.data.is_verified,
                userCoverImage: response.data.cover_image,
                description: response.data.user.description,
                isAdmin: response.data.user.is_admin
              }
              const userImages = {
                coverImage: response.data.user.cover_image,
                userImage: response.data.user.avatar
              }
              const userWalletList = {
                ethCustodialWallet: response.data.user.custWalletEth,
                klayCustodialWallet: response.data.user.custWalletKlay,
                metamaskWallet: response.data.user.metamask_wallet,
                kaikasWallet: response.data.user.kaikas_wallet
              }
              dispatch(auth.actions.login(authToken, user, userImages, userWalletList))

              setTimeout(() => {
                dispatch(auth.actions.logout())
              }, 1000 * 60 * 60 * 12);
              // dispatch(auth.actions.setUserImages(userImages))
              setLoading(false)
            } else {
              if(response.data.message) {
                setChecked(true);
                setResponseMessage(response.data.message)
                setLoading(false)
                setSubmitting(false)
              } else {
                setChecked(true);
                setResponseMessage(intl.formatMessage({ id: "AUTH.SIGNIN.FAILED" }));
                setLoading(false)
                setSubmitting(true)
              }
            }
          })
          .catch(() => {
            setLoading(false)
            setSubmitting(false)
            setStatus(intl.formatMessage({ id: "AUTH.SIGNIN.INCORRECT_CREDENTIAL" }))
          })
      }, 1000)
    },
  })

  return (
    <form
      className='form w-100'
      onSubmit={formik.handleSubmit}
      noValidate
      id='kt_login_signin_form'
    >
      {/* begin::Heading */}
      <div className='text-center mb-10'>
        <h1 className='text-dark mb-3'><FormattedMessage id='AUTH.SIGNIN.SIGNIN' /></h1>
        <div className='text-gray-400 fw-bold fs-4'>
          <FormattedMessage id='AUTH.SIGNIN.NEW_HERE' />{' '}
          <Link to='/auth/registration' className='link-primary fw-bolder'>
            <FormattedMessage id='AUTH.SIGNIN.CREATE_ACCOUNT' />
          </Link>
        </div>
      </div>
      {/* begin::Heading */}

        {message !== undefined && message !== null && message !== "" ? (
          <AlertSuccess message={message} checked={checked} />
          ) : null }

        {sessionMessage !== undefined && sessionMessage !== null && sessionMessage !== "" ? (
          <AlertDanger message={sessionMessage} checked={checked} />
          ) : null }

        {responseMessage !== "" && responseMessage !== undefined && responseMessage !== null ? (
          <AlertDanger message={responseMessage} checked={checked} />
        ): null}

      {/* begin::Form group */}
      <div className='fv-row mb-10'>
        <label className='form-label fs-6 fw-bolder text-dark'>
            <FormattedMessage id='AUTH.SIGNIN.EMAIL' />
        </label>
        <input
          placeholder='Email'
          {...formik.getFieldProps('email')}
          className={clsx(
            'form-control form-control-lg form-control-solid',
            {'is-invalid': formik.touched.email && formik.errors.email},
            {
              'is-valid': formik.touched.email && !formik.errors.email,
            }
          )}
          type='email'
          name='email'
          autoComplete='off'
        />
        {formik.touched.email && formik.errors.email && (
          <div className='fv-plugins-message-container'>
            <span role='alert'>{formik.errors.email}</span>
          </div>
        )}
      </div>
      {/* end::Form group */}

      {/* begin::Form group */}
      <div className='fv-row mb-10'>
        <div className='d-flex justify-content-between mt-n5'>
          <div className='d-flex flex-stack mb-2'>
            {/* begin::Label */}
            <label className='form-label fw-bolder text-dark fs-6 mb-0'>
              <FormattedMessage id='AUTH.SIGNIN.PASSWORD' />
            </label>
            {/* end::Label */}
            {/* begin::Link */}
            <Link
              to='/auth/forgot-password'
              className='link-primary fs-6 fw-bolder'
              style={{marginLeft: '5px'}}
            >
              <FormattedMessage id='AUTH.SIGNIN.FORGOT_PASSWORD' />
            </Link>
            {/* end::Link */}
          </div>
        </div>
        <input
          type='password'
          autoComplete='off'
          {...formik.getFieldProps('password')}
          className={clsx(
            'form-control form-control-lg form-control-solid',
            {
              'is-invalid': formik.touched.password && formik.errors.password,
            },
            {
              'is-valid': formik.touched.password && !formik.errors.password,
            }
          )}
        />
        {formik.touched.password && formik.errors.password && (
          <div className='fv-plugins-message-container'>
            <div className='fv-help-block'>
              <span role='alert'>{formik.errors.password}</span>
            </div>
          </div>
        )}
      </div>
      {/* end::Form group */}

      {/* begin::Action */}
      <div className='text-center'>
        <button
          type='submit'
          id='kt_sign_in_submit'
          className='btn btn-lg custom-btn w-100 mb-5'
          disabled={formik.isSubmitting || !formik.isValid}
        >
          {!loading && <span className='indicator-label'><FormattedMessage id='AUTH.SIGNIN.CONTINUE' /></span>}
          {loading && (
            <span className='indicator-progress' style={{display: 'block'}}>
              <FormattedMessage id='SETTINGS_PAGE.PLEASE_WAIT' />...
              <span className='spinner-border spinner-border-sm align-middle ms-2'></span>
            </span>
          )}
        </button>
      </div>
      {/* end::Action */}
    </form>
  )
}
