import { collectionType } from '../../yourCollections/common/common'


export const DropCard = (props: any) => {
    return (
        <>
           <div 
                className="card card-custom gutter-b card-stretch shadow"
                style={{ 
                    width: '330px', 
                    marginBottom: '50px',
                    borderRadius: '5px'
                }}
            >
                <a 
                href={`drop-detail?id=${props.id}`}
                style={{ padding: '0px' }}
                className="card-body d-flex flex-column bg-white rounded"
                >
                    <div className="text-center h-100 w-100">
                        <img 
                        src={`${process.env.REACT_APP_API_COLLECTION_IMAGE_URL}/${props.collection_image}`} 
                        className="h-100 w-100" 
                        style={{ 
                            borderTopLeftRadius: '5px',
                            borderTopRightRadius: '5px',
                            textAlign: 'center'
                        }}
                        />
                    </div>
                    <div className="bg-dark py-2 ps-2">
                        <span style={{ color: 'white' }}>
                            {props.tiersData.length}
                            <span className="ms-2">Tiers</span>
                        </span>
                    </div>
                    <div className="d-flex justify-content-around py-3">
                        <div>
                            <img 
                                className="w-50px"
                                style={{ borderRadius: '50%' }}
                                src={`${process.env.REACT_APP_API_ARTIST_IMAGE_URL}/${props.artistImage}`} 
                                alt="" 
                            />
                        </div>
                        <div className="mt-2">
                            <span style={{ color: 'black' }}>{props.collection_name}</span><br/>
                            <span className="text-muted">{props.artistName}</span>
                        </div>
                        <div className="mt-2">
                            <span className="text-muted">{collectionType[props.collection_type]}</span>
                        </div>
                    </div>
                </a>
            </div>
        </>
    )
}