import {useState, useEffect} from 'react'
import { useSelector, shallowEqual, useDispatch } from 'react-redux'
import { useNavigate } from 'react-router'
import {UserListRow} from './components/UserListRow'
import {DeleteConfirmationBox} from './components/DeleteConfirmationBox'
import { RootState } from '../../../../setup'
import {AlertDanger, AlertSuccess} from '../../alerts/Alerts'
import { FormattedMessage, useIntl } from 'react-intl'
import { getUsersData, searchUserData, deleteUsers } from '../api'
import { PasswordConfirmDialog } from './components/PasswordConfirmDialog'
import { UserPagination } from './components/UserPagination'
import { AddUserDialogBox } from './components/AddUserDialogBox'
import { OrderListPagination } from 'app/modules/order-management/components/Pagination'


const UserListView = () => {
    const [checked, setChecked] = useState(true)
    const [loading, setLoading] = useState<boolean>(false)
    const [deleting, setDeleting] = useState<boolean>(false)
    const [deleteRecord, setDeleteRecord] = useState<Array<string>>([])
    const dispatch = useDispatch()
    const navigate = useNavigate()
    const intl = useIntl()
    const [selectedAll, setSelectedAll] = useState<boolean>(false)
    const [userList, setUserList] = useState<Array<any>>([]);
    const [showPassConfirmDialog, setShowPassConfirmDialog] = useState<boolean>(false)
    const [password, setPassword] = useState<string>('')
    const [totalNumberOfPages, setTotalNumberOfPages] = useState<any>(0)
    const [pageNumList, setPageNumList] = useState<Array<any>>([])
    const [selectedPage, setSelectedPage] = useState<any>(1)
    const [currentPage, setCurrentPage] = useState<any>(1)
    const [searchString, setSearchString] = useState<string>('')
    const [addUserDialogOpen, setAddUserDialogOpen] = useState<boolean>(false)

    const successMsg = localStorage.getItem('userSuccessMsg')
    const [resSuccessMessage, setResSuccessMessage] = useState<string | null>(successMsg)

    const failureMsg = localStorage.getItem('userFailureMsg')
    const [resFailureMessage, setResFailureMessage] = useState<string | null>(failureMsg)

    const [warnings, setWarnings] = useState<string>("")
    const [showWarnings, setShowWarnings] = useState<boolean>(false)

    const {isAdmin, userID}: any = useSelector<RootState>(
        ({auth}) => ({
            isAdmin: auth.user?.isAdmin,
            userID: auth.user?.userID
        }), shallowEqual
    )

    if(resSuccessMessage !== null && resSuccessMessage !== undefined) {
        setTimeout(() => {
          setChecked(false);
          localStorage.removeItem('userSuccessMsg')
          setTimeout(() => {
            setResSuccessMessage(null);
          }, 300);
        }, 5000);
    }

    if(warnings != "") {
        setTimeout(() => {
          setShowWarnings(false);
          setTimeout(() => {
            setWarnings("");
          }, 300);
        }, 5000);
    }
    
    if(resFailureMessage !== null && resFailureMessage !== undefined) {
        setTimeout(() => {
            setChecked(false);
            localStorage.removeItem('userFailureMsg')
            setTimeout(() => {
                setResFailureMessage(null);
            }, 300);
        }, 5000);
    }

    useEffect(() => {
        if(deleteRecord.length !== userList.length) {
            setSelectedAll(false)
        }
        if(deleteRecord.length === userList.length && userList.length > 0) {
            setSelectedAll(true)
        }
    }, [deleteRecord])

    useEffect(() => {
        console.log(searchString)
        console.log(searchString.length)

        if(searchString.length == 0) {
            getUsersData(0, 10)
            .then((response) => {
                if(response.data.success) {
                    setUserList(response.data.result)
                    setTotalNumberOfPages(response.data.total_no_of_pages)
                    setPageNumList(preparePageNumList(response.data.total_no_of_pages))
                }
            })
        }
    }, [searchString])

    const preparePageNumList = (size: any) => {
        let pageNumList = []

        for (let index = 1; index <= size; index++) {
            pageNumList.push(index)
        }

        console.log(pageNumList)

        return pageNumList
    }

    const handleChangeSelection = (id: string, selectOption: boolean) => {
        const findUser = userList.find((user) => {
            return user.id === id
        })
        findUser.selected = selectOption
        return findUser
    }

    const fetchNextData = (pageNum: any) => {
        if(pageNum > 0 && pageNum <= totalNumberOfPages) {
            setLoading(true)
            const skip = (parseInt(pageNum) - 1) * 10
            setSelectedPage(pageNum)
            setCurrentPage(pageNum)
            setSelectedAll(false)
            setDeleteRecord([])

            getUsersData(skip, 10)
            .then((response) => {
                if(response.data.success) {
                    setUserList(response.data.result)
                    setTotalNumberOfPages(response.data.total_no_of_pages)
                    setPageNumList(preparePageNumList(response.data.total_no_of_pages))
                    setLoading(false)
                }
            })
        } else {
            setWarnings('Invalid page number provided, please check it.')
            setShowWarnings(true)
        }
    }

    useEffect(() => {
        getUsersData(0, 10)
        .then((response) => {
            if(response.data.success) {
                setUserList(response.data.result)
                setTotalNumberOfPages(response.data.total_no_of_pages)
                setPageNumList(preparePageNumList(response.data.total_no_of_pages))
            }
        })
    }, [])

    type currentUserExists = () => (boolean)

    const currentUserExistsInDeleteRecord: currentUserExists = () => {
        for(let user of deleteRecord) {
            if(user === userID) {
                return true
            } else {
                continue
            }
        }
        return false
    }

    const passwordConfirmPopup = () => {
        setDeleting(true)
        setShowPassConfirmDialog(true)
    }

    const handleDeleteUsers = () => {
        if(deleteRecord.length > 0) {
            setLoading(true)
            deleteUsers(deleteRecord, userID, password)
            .then((response) => {
                if(response.data.success){
                    setDeleteRecord([]);
                    localStorage.setItem('userSuccessMsg', response.data.message)
                    setLoading(false)
                    window.location.reload()
                } else {
                    if(response.data.message) {
                        localStorage.setItem('userFailureMsg', response.data.message)
                        window.location.reload()
                    } else {
                        localStorage.setItem('userFailureMsg', 'Failed to delete users, please try again.')
                        window.location.reload()
                    }
                }
            })
        }
    }

    const handleChangeSelected = async (id: string) => {
        let newUserList: Array<any> = []
        userList.map((user) => {
            if(user.id !== id) {
                newUserList = [...newUserList, user]
            } else {
                const newUser = user
                const selected = !newUser.selected
                newUser.selected = selected
                newUserList.push(newUser)
            }
        })
        setUserList(newUserList)
    }

    const handleChange = async (e: any) => {
        await handleChangeSelected(e.target.value)
        if(e.target.checked) {
            setDeleteRecord(record => {
                return [
                    ...record,
                    e.target.value
                ]
            })
        } else {
            setDeleteRecord(record => {
                const newRecord = record.filter((rec) => {
                    return rec !== e.target.value
                })
                return newRecord
            })
        }
    }

    const openDialog = (id: string) => {
        if(!currentUserExistsInDeleteRecord()) {
            const element: HTMLElement = document.getElementById(id)!
            element.style.display='block'
        } else {
            localStorage.setItem('userFailureMsg', 'Unable to process deletion request. Cannot delete the current signed in user.')
            window.location.reload()
        }
    }

    const closeDialog = (id: string) => {
        const element: HTMLElement = document.getElementById(id)!
        element.style.display='none'
    }


    const getPrefix = (text: string, length: number) => {
        let newText = ''
        for(let i=0; i<length; i++) {
            newText += text[i]
        }
        return newText
    }

    const handleSearch = (e: any) => {
        
        let _searchString = e.target.value

        if(e.code == 'Enter' && _searchString.length > 0) {
            setSearchString(_searchString);

            searchUserData(_searchString, 0, 10)
            .then((response) => {
                if(response.data.success) {
                    setUserList(response.data.search_result)
                    setTotalNumberOfPages(response.data.total_no_of_pages)
                    setPageNumList(preparePageNumList(response.data.total_no_of_pages))
                }
            })
        } else {
            setSearchString(_searchString)
        }
    }

    const handleSelectAll = (event: any) => {
        if(event.target.checked) {
            setSelectedAll(true)
            const newUserList: Array<any> = []
            const newDeleteRecord: Array<string> = []
            userList.map((user) => {
                newUserList.push(handleChangeSelection(user.id, true))
                newDeleteRecord.push(user.id)
            })
            setUserList(newUserList)
            setDeleteRecord(newDeleteRecord)
        } else {
            setSelectedAll(false)
            const newUserList: Array<any> = []
            userList.map((user) => {
                newUserList.push(handleChangeSelection(user.id, false))
            })
            setUserList(newUserList)
            setDeleteRecord([])
        }
    }  
    
    return (
        <>
            {isAdmin == '1' && 
                <>
                    <div id="user-list-card" className="card user-list-card-width" style={{ overflowX: 'auto'}}>
                        {resSuccessMessage !== null && resSuccessMessage !== undefined && resSuccessMessage !== ""  ? (
                            <AlertSuccess message={resSuccessMessage} checked={checked} />
                        ) : null}

                        {resFailureMessage !== null && resFailureMessage !== undefined && resFailureMessage !== ""  ? (
                            <AlertDanger message={resFailureMessage} checked={checked} />
                        ) : null}

                        {warnings != "" && 
                            <AlertDanger message={warnings} checked={showWarnings} />
                        }
                        <div className="card-header border-0 pt-6">
                            <div className="card-title">
                                <div className="d-flex align-items-center position-relative my-1">
                                    <span className="svg-icon svg-icon-1 position-absolute ms-6">
                                        <svg xmlns="http://www.w3.org/2000/svg" xmlnsXlink="http://www.w3.org/1999/xlink" width="24px" height="24px" viewBox="0 0 24 24" version="1.1">
                                            <g stroke="none" strokeWidth="1" fill="none" fillRule="evenodd">
                                                <rect x="0" y="0" width="24" height="24" />
                                                <path d="M14.2928932,16.7071068 C13.9023689,16.3165825 13.9023689,15.6834175 14.2928932,15.2928932 C14.6834175,14.9023689 15.3165825,14.9023689 15.7071068,15.2928932 L19.7071068,19.2928932 C20.0976311,19.6834175 20.0976311,20.3165825 19.7071068,20.7071068 C19.3165825,21.0976311 18.6834175,21.0976311 18.2928932,20.7071068 L14.2928932,16.7071068 Z" fill="#000000" fillRule="nonzero" opacity="0.3" />
                                                <path d="M11,16 C13.7614237,16 16,13.7614237 16,11 C16,8.23857625 13.7614237,6 11,6 C8.23857625,6 6,8.23857625 6,11 C6,13.7614237 8.23857625,16 11,16 Z M11,18 C7.13400675,18 4,14.8659932 4,11 C4,7.13400675 7.13400675,4 11,4 C14.8659932,4 18,7.13400675 18,11 C18,14.8659932 14.8659932,18 11,18 Z" fill="#000000" fillRule="nonzero" />
                                            </g>
                                        </svg>
                                    </span>
                                    <input 
                                        onKeyPress={(e) => handleSearch(e)} 
                                        type="text" 
                                        className="form-control form-control-solid w-250px ps-14" 
                                        placeholder={intl.formatMessage({id: 'ADMIN.USER.SEARCH_USER'})} 
                                    />
                                </div>
                            </div>
                            <div className="card-toolbar">
                                <div className="d-flex justify-content-end">
                                    {deleteRecord.length === 0 &&
                                        <>

                                            <button 
                                                type="button" 
                                                className="btn btn-primary" 
                                                onClick={() => setAddUserDialogOpen(true)}
                                            >
                                                <FormattedMessage id="ADMIN.USER.ADD_USER" />
                                            </button>
                                            
                                            <AddUserDialogBox 
                                                setResSuccessMessage={setResSuccessMessage}
                                                setResFailureMessage={setResFailureMessage}
                                                setAddUserDialogOpen={setAddUserDialogOpen}
                                                addUserDialogOpen={addUserDialogOpen}
                                            />
                                        </>
                                    }
                                </div>
                                {deleteRecord.length > 0 && 
                                    <div className="d-flex justify-content-end align-items-center">
                                        <div className="fw-bolder me-5">
                                        <span className="me-2">{deleteRecord.length}</span>Selected</div>
                                        <button type="button" onClick={() => openDialog('delete-users')} className="btn btn-danger">
                                            <FormattedMessage id="ADMIN.USER.DELETE_SELECTED" />
                                            {loading === true && 
                                                <span className="spinner-border spinner-border-sm align-middle ms-2"></span>
                                            }
                                        </button>
                                    </div>
                                }
                                <DeleteConfirmationBox
                                    closeDialog={closeDialog}
                                    passwordConfirmPopup={passwordConfirmPopup}
                                    deleting={deleting}
                                />
                            </div>
                        </div>
                        <div className="card-body">
                            <table className="table align-middle table-row-dashed fs-6 gy-5 px-3" id="kt_table_users">
                                <thead className="pe-5">
                                    <tr className="text-start text-muted fw-bolder fs-7 text-uppercase gs-0">
                                        <th className="w-10px pe-2">
                                            <div className="ms-3 form-check form-check-sm form-check-custom form-check-solid me-3">
                                                <input className="form-check-input" onChange={(e) => handleSelectAll(e)} type="checkbox" checked={selectedAll} />
                                            </div>
                                        </th>
                                        
                                        <th className="min-w-125px"><FormattedMessage id="ADMIN.USER.USER_TABLE.HEADER.USER" /></th>
                                        <th className="text-center min-w-125px"><FormattedMessage id="ADMIN.USER.USER_TABLE.TOKENS" /></th>
                                        <th className="text-center min-w-125px"><FormattedMessage id="ADMIN.USER.USER_TABLE.VERIFIED" /></th>
                                        <th className="text-center min-w-125px"><FormattedMessage id="ADMIN.USER.USER_TABLE.ADMIN" /></th>
                                        <th className="text-center min-w-125px"><FormattedMessage id="ADMIN.USER.USER_TABLE.HEADER.CREATION_DATE" /></th>
                                        <th className="text-center min-w-100px"><FormattedMessage id="ADMIN.USER.USER_TABLE.HEADER.ACTIONS" /></th>
                                    </tr>
                                </thead>
                                <tbody className="text-gray-600 fw-bold">
                                    {userList.map((user) => (
                                        <UserListRow 
                                            key={user.id}
                                            id={user.id}
                                            userName={user.full_name}
                                            email={user.email}
                                            checked={user.selected}
                                            isVerified={user.status}
                                            isAdmin={user.is_admin}
                                            imageUrl={user.user_image}
                                            joinedDate={user.created}
                                            noOfOrders={user.no_of_orders}
                                            handleChange={handleChange}
                                        />
                                    ))}
                                </tbody>
                            </table>
                        </div>
                    </div>
                    <div className='d-flex justify-content-center mt-5'>
                        {/* <UserPagination 
                            pageList={pageNumList}
                            fetchNextData={fetchNextData}
                            selectedPage={selectedPage}
                        /> */}
                        <OrderListPagination 
                            totalNumberOfPages={totalNumberOfPages}
                            fetchNextData={fetchNextData}
                            selectedPage={selectedPage}
                            setSelectedPage={setSelectedPage}
                            currentPage={currentPage}
                            setCurrentPage={setCurrentPage}
                        />
                    </div>
                    <PasswordConfirmDialog 
                        showPassConfirmDialog={showPassConfirmDialog}
                        setShowPassConfirmDialog={setShowPassConfirmDialog}
                        password={password}
                        setPassword={setPassword}
                        handleDeleteUsers={handleDeleteUsers}
                        setDeleting={setDeleting}
                    />
                </>
            }
        </>
    )
}

export {UserListView}