/* eslint-disable jsx-a11y/anchor-is-valid */
import React, {useState} from 'react'
import {useFormik} from 'formik'
import * as Yup from 'yup'
import clsx from 'clsx'
import {Link} from 'react-router-dom'
import {AlertDanger, AlertSuccess} from '../../alerts/Alerts'
import {resetPassword} from '../redux/AuthCRUD'
import { setTimeout } from 'timers'
import { FormattedMessage, useIntl } from 'react-intl'

const initialValues: any = {
  password: '',
  cpassword: '',
}

const params = new URLSearchParams(window.location.search)

const token: any = params.get("token");

export function ResetPassword() {
  const [loading, setLoading] = useState(false)
  const [errorMessage, setErrorMessage] = useState("");
  const [checked, setChecked] = useState(true);
  const [message, setMessage] = useState("")

  const intl = useIntl()

  const resetPasswordSchema = Yup.object().shape({
    password: Yup.string()
      .min(3, intl.formatMessage({id: 'PROFILE_DETAIL_SCHEMA.EMAIL.MIN'}))
      .max(50, intl.formatMessage({id: 'PROFILE_DETAIL_SCHEMA.EMAIL.MAX'}))
      .required(intl.formatMessage({id: 'AUTH.REGISTER.SCHEMA.PASSWORD.REQUIRED'})),
    cpassword: Yup.string()
      .required(intl.formatMessage({ id: "AUTH.REGISTER.SCHEMA.CPASSWORD.REQUIRED" }))
      .when('password', {
        is: (val: string) => (val && val.length > 0 ? true : false),
        then: Yup.string().oneOf([Yup.ref('password')], intl.formatMessage({id: 'AUTH.REGISTER.SCHEMA.CPASS.MISMATCH'})),
      }),
  })

  if(errorMessage !== "") {
    setTimeout(() => {
      setChecked(false);
      setTimeout(() => {
        setErrorMessage("");
      }, 200);
    }, 5000);
  }

  if(message !== "") {
    setTimeout(() => {
      setChecked(false);
      setTimeout(() => {
        setMessage("");
      }, 200);
    }, 5000);
  }
  
  const formik = useFormik({
    initialValues,
    validationSchema: resetPasswordSchema,
    onSubmit: (values, {setStatus, setSubmitting, resetForm}) => {
      setLoading(true)
      setTimeout(() => {
        resetPassword(values.password, values.cpassword, token)
        .then((response) => {
          if(response.data.success) {
            setChecked(true)
            setMessage(response.data.message)
            resetForm(initialValues)
          } else {
            if(response.data.message) {
              setChecked(true);
              setErrorMessage(response.data.message);
            } else {
              setChecked(true);
              setErrorMessage(intl.formatMessage({ id: "AUTH.PASSWORD_RESET.FAILED" }));
            }
          }
          setLoading(false);
          setSubmitting(false);
        })
        .catch(() => {
          setChecked(true);
          setSubmitting(false);
          setErrorMessage(intl.formatMessage({ id: "AUTH.PASSWORD_RESET.FAILED" }));
          setLoading(false)
        });
      }, 1000)
    },
  })

  return (
    <form
      className='form w-100 fv-plugins-bootstrap5 fv-plugins-framework'
      noValidate
      id='kt_password_reset_form'
      onSubmit={formik.handleSubmit}
    >
      {/* begin::Heading */}
      <div className='mb-10 text-center'>
        {/* begin::Title */}
        <h1 className='text-dark mb-3'><FormattedMessage id='AUTH.PASSWORD_RESET.TITLE' /></h1>
        {/* end::Title */}
      </div>
      {/* end::Heading */}
      {message !== "" ? (
          <AlertSuccess message={message} checked={checked} />
        ) : null}

      {errorMessage !== "" ? (
          <AlertDanger message={errorMessage} checked={checked} />
        ) : null}

      {/* begin::Form group Password */}
      <div className='mb-10 fv-row' data-kt-password-meter='true'>
        <div className='mb-1'>
          <label className='form-label fw-bolder text-dark fs-6'><FormattedMessage id='AUTH.PASSWORD_RESET.NEW_PASS' /></label>
          <div className='position-relative mb-3'>
            <input
              type='password'
              placeholder='Password'
              autoComplete='off'
              {...formik.getFieldProps('password')}
              className={clsx(
                'form-control form-control-lg form-control-solid',
                {
                  'is-invalid': formik.touched.password && formik.errors.password,
                },
                {
                  'is-valid': formik.touched.password && !formik.errors.password,
                }
              )}
            />
            {formik.touched.password && formik.errors.password && (
              <div className='fv-plugins-message-container'>
                <div className='fv-help-block'>
                  <span role='alert'>{formik.errors.password}</span>
                </div>
              </div>
            )}
          </div>
        </div>
      </div>
      {/* end::Form group */}

      {/* begin::Form group Confirm password */}
      <div className='fv-row mb-5'>
        <label className='form-label fw-bolder text-dark fs-6'><FormattedMessage id='AUTH.PASSWORD_RESET.CPASS' /></label>
        <input
          type='password'
          placeholder='Password confirmation'
          autoComplete='off'
          {...formik.getFieldProps('cpassword')}
          className={clsx(
            'form-control form-control-lg form-control-solid',
            {
              'is-invalid': formik.touched.cpassword && formik.errors.cpassword,
            },
            {
              'is-valid': formik.touched.cpassword && !formik.errors.cpassword,
            }
          )}
        />
        {formik.touched.cpassword && formik.errors.cpassword && (
          <div className='fv-plugins-message-container'>
            <div className='fv-help-block'>
              <span role='alert'>{formik.errors.cpassword}</span>
            </div>
          </div>
        )}
      </div>
      {/* end::Form group */}

      {/* begin::Form group */}
      <div className='text-center'>
        <button
          type='submit'
          id='kt_pass_reset_submit'
          className='btn btn-lg custom-btn w-100 mb-5'
          disabled={formik.isSubmitting || !formik.isValid}
        >
          {!loading && <span className='indicator-label'><FormattedMessage id='AUTH.PASSWORD_RESET.RESET' /></span>}
          {loading && (
            <span className='indicator-progress' style={{display: 'block'}}>
              <FormattedMessage id='SETTINGS_PAGE.PLEASE_WAIT' />...{' '}
              <span className='spinner-border spinner-border-sm align-middle ms-2'></span>
            </span>
          )}
        </button>
        <Link to='/auth/login'>
          <button
            type='button'
            id='kt_pass_reset_cancel_button'
            className='btn btn-lg btn-light-primary w-100 mb-5'
          >
            <FormattedMessage id='AUTH.PASSWORD_RESET.GO_TO_LOGIN' />
          </button>
        </Link>
      </div>
      {/* end::Form group */}
    </form>
  )
}
