import axios from "axios";

const CREATE_ARTIST = `${process.env.REACT_APP_API_URL}/create-artist.php`
const GET_ARTIST_DATA = `${process.env.REACT_APP_API_URL}/get-artist-data.php`
const GET_ARTIST_DATA_BY_ID = `${process.env.REACT_APP_API_URL}/get-artist-detail-data.php`
const DELETE_ARTIST = `${process.env.REACT_APP_API_URL}/delete-artist.php`
const UPDATE_ARTIST = `${process.env.REACT_APP_API_URL}/update-artist.php`

export function createArtist(
    formData: any
    ) {
      return axios({
        method: 'post',
        url: CREATE_ARTIST,
        headers: {
          'content-type': 'multipart/form-data',
        },
        data: formData
      })
}

export function updateArtist(
    formData: any
    ) {
      return axios({
        method: 'post',
        url: UPDATE_ARTIST,
        headers: {
          'content-type': 'multipart/form-data',
        },
        data: formData
      })
}

export function getArtistsData() {
    return axios({
        method: 'get',
        url: GET_ARTIST_DATA,
    })
}

export function getArtistsDataById(artistID: string | null) {
    console.log(artistID)
    return axios({
        method: 'post',
        url: GET_ARTIST_DATA_BY_ID,
        headers: {
            'content-type': 'application/x-www-form-urlencoded',
        },
        data: {
            artistID
        }
    })
}

export function deleteArtist(id: any) {
    return axios({
        method: 'post',
        url: DELETE_ARTIST,
        headers: {
            'content-type': 'application/x-www-form-urlencoded',
        },
        data: {
            id
        }
    })
}