import axios from "axios";

const CREATE_PATENTS_URL = `${process.env.REACT_APP_API_URL}/create-patent.php`;
const UPDATE_PATENTS_URL = `${process.env.REACT_APP_API_URL}/update-patent.php`;
const DELETE_PATENT_URL = `${process.env.REACT_APP_API_URL}/delete-patent.php`;
const GET_PATENTS_URL = `${process.env.REACT_APP_API_URL}/get-patents.php`;
const GET_PATENT_CATEGORY_URL = `${process.env.REACT_APP_API_URL}/get-music-category.php`;
const GET_PATENT_EVENTS_URL = `${process.env.REACT_APP_API_URL}/get-events.php`;

const CREATE_COLLECTION_URL = `${process.env.REACT_APP_API_URL}/create-new-collection.php`;
const GET_COLLECTIONS_META_URL = `${process.env.REACT_APP_API_URL}/get-collection-metadata.php`;

export function createCollection(
  formData: any
  ) {
    return axios({
      method: 'post',
      url: CREATE_COLLECTION_URL,
      headers: {
        'content-type': 'multipart/form-data',
      },
      data: formData
    })
}

export function createPatent(
  formData: any
  ) {
    return axios({
      method: 'post',
      url: CREATE_PATENTS_URL,
      headers: {
        'content-type': 'multipart/form-data',
      },
      data: formData
    })
}

export function get_patents_and_metas() {
    return axios({
      method: 'post',
      url: GET_PATENTS_URL,
      headers: {
        'content-type': 'application/x-www-form-urlencoded',
      },
    })
}

export function get_patents_categories() {
    return axios({
      method: 'post',
      url: GET_PATENT_CATEGORY_URL,
      headers: {
        'content-type': 'application/x-www-form-urlencoded',
      }
    })
}

export function get_patent_events(authToken: string) {
  return axios({
    method: 'post',
    url: GET_PATENT_EVENTS_URL,
    headers: {
      'content-type': 'application/x-www-form-urlencoded',
      'Authorization': authToken
    }
  })
}

export function updatePatent(formData: any) {
  return axios({
    method: 'post',
    url: UPDATE_PATENTS_URL,
    headers: {
      'content-type': 'multipart/form-data',
    },
    data: formData
  })
}

export function deletePatent(authToken: string, userID: string, patent_id: string) {
  return axios({
    method: 'post',
    url: DELETE_PATENT_URL,
    headers: {
      'content-type': 'application/x-www-form-urlencoded',
      'Authorization': authToken
    },
    data: {
      authToken,
      userID,
      patent_id
    }
  })
}

export function getCollectionMeta(artistFilterVal: string | null, blockchainFilterVal: string | null) {
  return axios({
      method: 'post',
      url: GET_COLLECTIONS_META_URL,
      headers: {
        'content-type': 'application/x-www-form-urlencoded',
      },
      data: {
        artistFilterVal,
        blockchainFilterVal
      }
  })
}