import React, {useState} from 'react'
import * as Yup from 'yup'
import clsx from 'clsx'
import {Link} from 'react-router-dom'
import {useFormik} from 'formik'
import {requestPassword} from '../redux/AuthCRUD'
import {AlertSuccess, AlertDanger} from '../../alerts/Alerts'
import { FormattedMessage, useIntl } from 'react-intl'

const initialValues: any = {
  email: '',
}

export function ForgotPassword() {
  const [loading, setLoading] = useState(false)
  const [checked, setChecked] = useState(true);

  const [message, setMessage] = useState("");
  const [isError, setIsError] = useState(false);

  const intl = useIntl()

  const forgotPasswordSchema = Yup.object().shape({
    email: Yup.string()
      .email(intl.formatMessage({id: 'PROFILE_DETAIL_SCHEMA.EMAIL.WRONG_FORMAT'}))
      .min(3, intl.formatMessage({id: 'PROFILE_DETAIL_SCHEMA.EMAIL.MIN'}))
      .max(50, intl.formatMessage({id: 'PROFILE_DETAIL_SCHEMA.EMAIL.MAX'}))
      .required(intl.formatMessage({id: 'PROFILE_DETAIL_SCHEMA.EMAIL.REQUIRED'})),
  })

  const closeAlert = () => {
    setTimeout(() => {
      setChecked(false);
      setTimeout(() => {
        setMessage("");
      }, 200);
    }, 5000);
  }

  const formik = useFormik({
    initialValues,
    validationSchema: forgotPasswordSchema,
    onSubmit: (values, {setStatus, setSubmitting, resetForm}) => {
      setLoading(true)
      setTimeout(() => {
        requestPassword(values.email)
          .then((response) => {
            const message = response.data.message;
            setMessage(message);
            if(response.data.sent_email) {
              setIsError(false)
              resetForm(initialValues);
              setChecked(true);
            } else {
              if(message) {
                setIsError(true);
                setChecked(true);
              } else {
                setMessage(intl.formatMessage({ id: "AUTH.FORGOT_PASSWORD.REQUEST_FAILED" }));
                setIsError(true);
                setChecked(true);
              }
            } 
            setSubmitting(false);
            setLoading(false)
            closeAlert();
          })
      }, 1000)
    },
  })
 
  return (
    <>
      <form
        className='form w-100 fv-plugins-bootstrap5 fv-plugins-framework'
        noValidate
        id='kt_login_password_reset_form'
        onSubmit={formik.handleSubmit}
      >
        <div className='text-center mb-10'>
          {/* begin::Title */}
          <h1 className='text-dark mb-3'><FormattedMessage id='AUTH.FORGOT_PASSWORD' /></h1>
          {/* end::Title */}

          {/* begin::Link */}
          <div className='text-gray-400 fw-bold fs-4'><FormattedMessage id='AUTH.FORGOT_PASSWORD.ENTER_EMAIL' /></div>
          {/* end::Link */}
        </div>

        {message !== "" ? (
          <>
          {isError ? (
          <AlertDanger message={message} checked={checked} />
          ) : (
            <AlertSuccess message={message} checked={checked} />
          )}
          </>
        ) : (
          null
        )}

        {/* begin::Form group */}
        <div className='fv-row mb-10'>
          <label className='form-label fw-bolder text-gray-900 fs-6'><FormattedMessage id='SETTINGS_PAGE.EMAIL' /></label>
          <input
            type='email'
            placeholder=''
            autoComplete='off'
            {...formik.getFieldProps('email')}
            className={clsx(
              'form-control form-control-lg form-control-solid',
              {'is-invalid': formik.touched.email && formik.errors.email},
              {
                'is-valid': formik.touched.email && !formik.errors.email,
              }
            )}
          />
          {formik.touched.email && formik.errors.email && (
            <div className='fv-plugins-message-container'>
              <div className='fv-help-block'>
                <span role='alert'>{formik.errors.email}</span>
              </div>
            </div>
          )}
        </div>
        {/* end::Form group */}

        {/* begin::Form group */}
        <div className='d-flex flex-wrap justify-content-center pb-lg-0'>
          <button
            type='submit'
            id='kt_password_reset_submit'
            className='btn btn-lg custom-btn fw-bolder me-4'
          >
            <span className='indicator-label'><FormattedMessage id='HOME_PAGE.SUBMIT' /></span>
            {loading && (
              <span className='spinner-border spinner-border-sm align-middle ms-2'></span>
            )}
          </button>
          <Link to='/auth/login'>
            <button
              type='button'
              id='kt_login_password_reset_form_cancel_button'
              className='btn btn-lg btn-light-primary fw-bolder'
              disabled={formik.isSubmitting || !formik.isValid}
            >
              <FormattedMessage id='AUTH.REGISTER.CANCEL' />
            </button>
          </Link>{' '}
        </div>
        {/* end::Form group */}
      </form>
    </>
  )
}
