import axios from 'axios';

const GET_KLAYTN_COLLECTION_DATA_URL = `${process.env.REACT_APP_API_URL}/get-collection-data.php`;
const GET_KLAYTN_COLLECTORS_DATA_URL = `${process.env.REACT_APP_API_URL}/get-paginated-collectors-detail.php`;


export function getCollectionData(collectionID: string) {
    return axios({
      method: 'post',
      url: GET_KLAYTN_COLLECTION_DATA_URL,
      headers: {
        'content-type': 'application/x-www-form-urlencoded',
      },
      data: {
        collectionID
      }
    })
}

export function getKlaytnCollectorsData(collectionID: string, skipNumber: string, tierFilterVal: string) {
  return axios({
    method: 'post',
    url: GET_KLAYTN_COLLECTORS_DATA_URL,
    headers: {
      'content-type': 'application/x-www-form-urlencoded',
    },
    data: {
      collectionID,
      skipNumber,
      tierFilterVal
    }
  })
}

const getTotalTokenQuantityAndOwnershipOffered = (tokens: Array<any>) => {

    let totalQuantity = 0;
    let totalOwnershipOffered = 0;
    let saleAmountInKLAY = 0;

    tokens.map((data: any) => {
        totalQuantity += parseInt(data.totalSupply)
        totalOwnershipOffered += ((parseInt(data.ownership_offered) / 100000) * parseInt(data.totalSupply))

        let tierSaleAmount = (parseFloat(data.totalSupply) - parseFloat(data.currentSupply)) * (parseFloat(data.price) / 10 ** 18)

        saleAmountInKLAY += tierSaleAmount
    })

    console.log(saleAmountInKLAY)

    return [totalQuantity, totalOwnershipOffered, saleAmountInKLAY]
}

export const fetchCollectionData = async (collectionID: string) => {
    const response = await getCollectionData(collectionID);

    const [totalQuantity, totalOwnershipOffered, saleAmountInKLAY] = getTotalTokenQuantityAndOwnershipOffered(response.data.tokens)

    const primarySaleAmountInFiat = Number(saleAmountInKLAY * parseFloat(response.data.klay_fiat_price)).toFixed(2)

    const collectionData = {
        collection: response.data.collection,
        tokensData: response.data.tokens,
        tokenBalances: response.data.token_balances,
        totalQuantity,
        totalOwnershipOffered,
        primarySaleAmountInFiat
    }

    return collectionData
}