import { useEffect, useState } from "react";
import CircularProgress from "@mui/material/CircularProgress/CircularProgress";
import { getCollectionMetaById } from "../yourCollections/crud/collectionCRUD";
import { DropDetailPageEthereum } from "./dropDetailEthereum/DropDetailPageEthereum";
import { DropDetailPageKlaytn } from "./dropDetailKlaytn/DropDetailPageKlaytn";
import { HeaderWrapper } from '../../customlayout/components/header/HeaderWrapper'
import { Footer } from '../../customlayout/components/Footer'
import { ReadMoreDialog } from "./ReadMoreDialog";


const initValues = ["The Royal Discord collector channel is restricted to Royal token holders. You'll get instructions for how to join after purchase."]


export const DropDetailPage = (props: any) => {
    const params = new URLSearchParams(window.location.search);
    const cid: string | null = params.get("id");

    const [collectionMeta, setCollectionMeta] = useState<any>(null)
    const [artistName, setArtistName] = useState<string>("")
    const [artistImage, setArtistImage] = useState<string>("")
    const [loading, setLoading] = useState<boolean>(true)
    const [fiatPrice, setFiatPrice] = useState<any>(null)

    const [readMoreDialogOpen, setReadMoreDialogOpen] = useState<boolean>(false)
    const [readMoreImage, setReadMoreImage] = useState<any>('330.png')
    const [readMoreTier, setReadMoreTier] = useState<any>('0')
    const [readMoreBlockchain, setReadMoreBlockchain] = useState<any>('ethereum')
    const [readMoreBenefits, setReadMoreBenefits] = useState<any>(initValues)

    useEffect(() => {
        getCollectionMetaById(cid)
        .then(async (response) => {
            if(response.data.success) {
                setCollectionMeta(response.data.collectionMeta)
                setFiatPrice(response.data.fiat_price_in_usd)
                setArtistName(response.data.artistName)
                setArtistImage(response.data.artistImage)
                setLoading(false)
            }
        })
    }, [])

    const handleReadMoreClick = (
        imageName: string,
        tier: string,
        blockchain: string,
        extras: Array<any>
    ) => {
        setReadMoreImage(imageName)
        setReadMoreTier(tier)
        setReadMoreBlockchain(blockchain)
        setReadMoreBenefits(extras)
        setReadMoreDialogOpen(true)
    }

    return (
        <>
        {/* <HeaderWrapper /> */}
        {!loading && collectionMeta &&
            <>
                {collectionMeta.blockchain == 'ethereum' && 
                    <DropDetailPageEthereum 
                        collectionID={cid}
                        artistName={artistName}
                        artistImage={artistImage}
                        collectionMeta={collectionMeta}
                        handleReadMoreClick={handleReadMoreClick}
                        fiatPrice={fiatPrice}
                    />
                }
                {collectionMeta.blockchain == 'klaytn' && 
                    <DropDetailPageKlaytn 
                        collectionID={cid}
                        artistName={artistName}
                        artistImage={artistImage}
                        collectionMeta={collectionMeta}
                        handleReadMoreClick={handleReadMoreClick}
                        fiatPrice={fiatPrice}
                    />
                }

                <ReadMoreDialog 
                    readMoreDialogOpen={readMoreDialogOpen}
                    setReadMoreDialogOpen={setReadMoreDialogOpen}
                    readMoreImage={readMoreImage}
                    readMoreTier={readMoreTier}
                    readMoreBlockchain={readMoreBlockchain}
                    readMoreBenefits={readMoreBenefits}
                />
            </>
        }
        {loading && 
            <div className='d-flex w-100 h-100'>
                <div className='my-auto mx-auto'>
                    <div className='d-flex flex-column justify-content-center'>
                        <span className='text-primary fs-3'>Loading</span> <br />
                        <span className='ms-4'>
                            <CircularProgress />
                        </span>
                    </div>
                </div>
            </div>
        }
        {/* <Footer /> */}
        </>
    )
}