import { useEffect, useState } from "react";
import { FormattedMessage } from 'react-intl'
 
interface ITimeLeft {
    days: number
    hours: number
    minutes: number
    seconds: number
}

export const Banner = (props: any) => {



    type ICalculateTimeLeft = (dropReleaseTime: any) => (ITimeLeft)
    const calculateTimeLeft: ICalculateTimeLeft = (dropReleaseTime: any) => {
        let year = new Date().getFullYear();
        const difference = +new Date(dropReleaseTime) - +new Date();
        let timeLeft: ITimeLeft = {days: 0, hours: 0, minutes: 0, seconds: 0};
    
        if (difference > 0) {
          timeLeft = {
            days: Math.floor(difference / (1000 * 60 * 60 * 24)),
            hours: Math.floor((difference / (1000 * 60 * 60)) % 24),
            minutes: Math.floor((difference / 1000 / 60) % 60),
            seconds: Math.floor((difference / 1000) % 60),
          };
        }
    
        return timeLeft;
    };

    const getFormattedDropRealeaDateTime = (dropReleaseTime: any) => {
        var currentdate = new Date(dropReleaseTime); 
        var datetime = currentdate.toDateString() + " @ "  
                        + currentdate.getHours() + ":"  
                        + currentdate.getMinutes() + " KST" 
        return datetime
    }

    const [timeDifference, setTimeDifference] = useState<number>(+new Date(props.featuredCollectionData.drop_release_time) - +new Date())
    const [timeLeft, setTimeLeft] = useState<any>(calculateTimeLeft(props.featuredCollectionData.drop_release_time));
    const [year] = useState(new Date().getFullYear());

    useEffect(() => {
        setTimeout(() => {
          setTimeLeft(calculateTimeLeft(props.featuredCollectionData.drop_release_time));
        }, 1000);
    });

    const timerComponents: any = [];

    Object.keys(timeLeft).forEach((interval) => {
        if (!timeLeft[interval]) {
        return;
        }

        timerComponents.push(
        <span>
            {timeLeft[interval]} {interval}{" "}
        </span>
        );
    });

    return (
        <>
            <div className="vc_row wpb_row vc_row-fluid vc_custom_1552662007387 vc_row-has-fill">
                <div className="wpb_column vc_column_container vc_col-sm-12">
                    <div className="vc_column-inner">
                        <div className="wpb_wrapper">
                            <div className="home-banner">
                                <video autoPlay muted loop id="myVideo">
                                    <source 
                                        src={props.bannerVideoURL}
                                        type="video/mp4" 
                                    />
                                    Your browser does not support HTML5 video.
                                </video>
                                <div className="banner-content d-flex flex-column justify-content-start">
                                    <span className="fs-7 text-white mb-5">
                                        {/* 04.14.22 @ 8PM ET / 5pm PT */}
                                        {getFormattedDropRealeaDateTime(props.featuredCollectionData.drop_release_time)}
                                    </span> <br />
                                    <span style={{ fontSize: '45px' }} className="text-white mb-5">
                                        {/* MY SINGLE FT. ONE */}
                                        {props.featuredCollectionData.collection_name}
                                    </span> <br />

                                    {timeDifference > 0 && 
                                        <>
                                            <span className="text-white d-flex justify-content-start mt-5">
                                                <div className="d-flex flex-column me-3 text-center">
                                                    <span style={{ fontSize: '30px' }}>{timeLeft.days}</span>
                                                    <span className="mt-1" style={{ fontSize: '15px' }}>Days</span>
                                                </div>
                                                <div className="d-flex flex-column mx-3 text-center">
                                                    <span style={{ fontSize: '30px' }}>{timeLeft.hours}</span>
                                                    <span className="mt-1" style={{ fontSize: '15px' }}>HRS</span>
                                                </div>
                                                <div className="d-flex flex-column mx-3 text-center">
                                                    <span style={{ fontSize: '30px' }}>{timeLeft.minutes}</span>
                                                    <span className="mt-1" style={{ fontSize: '15px' }}>MINS</span>
                                                </div>
                                                <div className="d-flex flex-column mx-3 text-center">
                                                    <span style={{ fontSize: '30px' }}>{timeLeft.seconds}</span>
                                                    <span className="mt-1" style={{ fontSize: '15px' }}>SECS</span>
                                                </div>
                                            </span> <br />
                                        </>
                                    } 

                                    {/* <a href="#" className="banner-button"><FormattedMessage id="HOME_PAGE.MORE_DETAILS" /></a> */}
                                    {props.featuredCollectionData.is_minted == '1' && 
                                        <div className="mt-5 text-start vc_btn3-container indie-button vc_btn3-center">
                                            <a 
                                                style={{
                                                    borderColor: '#ca8837',
                                                    color: '#ffffff',
                                                    backgroundColor: '#ca8837',
                                                    borderRadius: '5px'
                                                }} 
                                                className="px-3 py-3"
                                                // className="vc_general vc_btn3 vc_btn3-size-lg vc_btn3-shape-rounded vc_btn3-style-outline-custom" 
                                                href={`drop-detail?id=${props.featuredCollectionData.id}`} 
                                                title=""
                                            >
                                                <FormattedMessage id="HOME_PAGE.VIEW_MORE" />
                                            </a>
                                        </div>
                                    }
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}