import axios from "axios";

const GET_COLLECTION_DATA_URL = `${process.env.REACT_APP_API_URL}/get_collection_data_for_homepage.php`;
const GET_MUSICIAN_META_URL = `${process.env.REACT_APP_API_URL}/get-musician-meta-for-homepage.php`;

export function getCollectionDataForHomePage() {
    return axios({
        method: 'get',
        url: GET_COLLECTION_DATA_URL,
        headers: {
            'content-type': 'application/x-www-form-urlencoded',
        }
    })
}

export function getMusicianMetaForHomePage() {
    return axios({
        method: 'get',
        url: GET_MUSICIAN_META_URL,
        headers: {
            'content-type': 'application/x-www-form-urlencoded',
        }
    })
}