import {Action} from '@reduxjs/toolkit'
import {persistReducer} from 'redux-persist'
import storage from 'redux-persist/lib/storage'
import {put, takeLatest} from 'redux-saga/effects'
import {UserModel} from '../models/UserModel'
import {AccountPreferenceModel} from '../models/AccountPreferenceModel'
import { userInfo } from 'os'

export interface ActionWithPayload<T> extends Action {
  payload?: T
}

export const actionTypes = {
  Login: '[Login] Action',
  Logout: '[Logout] Action',
  Register: '[Register] Action',
  UserRequested: '[Request User] Action',
  UserLoaded: '[Load User] Auth API',
  SetUser: '[Set User] Action',
  SetLanguage: "[Set Language] Action",
  setNotificationsettings: "[Set Notification settings] Action",
  setWalletAddress: "[Set Wallet Address] Action",
  SignOutWallet: '[Sign Out Wallet] Action',
  SetUserImages: '[Set User Image] Action',
  SetDropDetailTierFilterVal: '[Set DropDetail Tier Filter] Action',
  SetUserWalletList: '[Set User Wallet List] Action'
}

const initialAuthState: IAuthState = {
  user: undefined,
  userImages: undefined,
  walletAddress: undefined,
  walletName: undefined,
  chainID: undefined,
  accessToken: undefined,
  language: undefined,
  filterVal: undefined,
  userWalletList: undefined
}

export interface IAuthState {
  user?: UserModel 
  userImages?: any
  walletAddress?: string | null
  walletName?: string
  chainID?: any
  accessToken?: string
  language?: string
  filterVal?: string
  userWalletList?: any
}

export const reducer = persistReducer(
  {storage, key: 'v100-marketplace-auth', whitelist: ['user', 'userImages', 'accessToken', 'accountPreference', 'exchangeRates', 'userWalletList', 'walletAddress', 'walletName', 'chainID']},
  (state: IAuthState = initialAuthState, action: ActionWithPayload<IAuthState>) => {
    switch (action.type) {
      case actionTypes.Login: {
        const accessToken = action.payload?.accessToken
        const user = action.payload?.user
        const userImages = action.payload?.userImages
        const userWalletList = action.payload?.userWalletList
        return {
          accessToken, 
          user: user,
          userImages,
          userWalletList
        }
      }

      case actionTypes.Register: {
        const accessToken = action.payload?.accessToken
        return {accessToken, user: undefined}
      }

      case actionTypes.Logout: {
        return initialAuthState
      }

      case actionTypes.SignOutWallet: {
        return {...state, walletAddress: undefined, walletName: undefined, chainID: undefined}
      }

      case actionTypes.UserRequested: {
        return {...state, user: undefined}
      }

      case actionTypes.UserLoaded: {
        const user = action.payload?.user
        return {...state, user}
      }

      case actionTypes.setWalletAddress: {
        const walletAddress = action.payload?.walletAddress
        const walletName = action.payload?.walletName
        const chainID = action.payload?.chainID
        return {...state, walletAddress, walletName, chainID}
      }

      case actionTypes.SetLanguage: {
        const language = action.payload?.language
        return {...state, language}
      }

      case actionTypes.SetUser: {
        const user = action.payload?.user
        return {...state, user}
      }

      case actionTypes.SetUserImages: {
        const userImages = action.payload?.userImages
        return {...state, userImages}
      }

      case actionTypes.SetDropDetailTierFilterVal: {
        const filterVal = action.payload?.filterVal
        return { ...state, filterVal }
      }

      case actionTypes.SetUserWalletList: {
        const userWalletList = action.payload?.userWalletList
        const walletAddress = action.payload?.walletAddress
        const walletName = action.payload?.walletName
        const chainID = action.payload?.chainID

        return {...state, walletAddress, walletName, chainID, userWalletList}
      }

      default:
        return state
    }
  }
)

export const actions = {
  login: (accessToken: string, user: UserModel, userImages: any, userWalletList: any) => ({
    type: actionTypes.Login, payload: {accessToken, user, userImages, userWalletList}
  }),
  register: (accessToken: string) => ({
    type: actionTypes.Register,
    payload: {accessToken},
  }),
  logout: () => ({type: actionTypes.Logout}),
  requestUser: () => ({
    type: actionTypes.UserRequested,
  }),
  fulfillUser: (user: UserModel) => ({type: actionTypes.UserLoaded, payload: {user}}),
  setUser: (user: UserModel) => ({type: actionTypes.SetUser, payload: {user}}),
  setUserImages: (userImages: any) => ({type: actionTypes.SetUserImages, payload: {userImages}}),
  setWalletAddress: (walletAddress: string | null, walletName: 'metamask' | 'kaikas' | undefined, chainID: any ) => (
    {type: actionTypes.setWalletAddress, payload: {walletAddress, walletName, chainID}}
  ),
  signOutWallet: () => ({ type: actionTypes.SignOutWallet }),
  setUserLanguage: (language: string) => ({ type: actionTypes.SetLanguage, payload: { language } }),
  setDropDetailTier: (filterVal: string | undefined) => ({ type: actionTypes.SetDropDetailTierFilterVal, payload: { filterVal } }),
  setUserWalletList: (userWalletList: any, walletAddress: string, walletName: string, chainID: any) => ({ type: actionTypes.SetUserWalletList, payload: { userWalletList, walletAddress, walletName, chainID } })
}

export function* saga() {
  // yield takeLatest(actionTypes.Login, function* loginSaga() {
  //   yield put(actions.requestUser())
  // })

  yield takeLatest(actionTypes.Register, function* registerSaga() {
    yield put(actions.requestUser())
  })

  // yield takeLatest(actionTypes.UserRequested, function* userRequested() {
  //   const {data: user} = yield getUserByToken()
  //   yield put(actions.fulfillUser(user))
  // })
}
