import { ApolloClient, InMemoryCache, gql } from '@apollo/client'
import axios from "axios";

const APIURL = `${process.env.REACT_APP_GRAPH_URL}`;

const GET_ETH_TOKEN_META_URL = `${process.env.REACT_APP_API_URL}/get-token-profile-data.php`;
const GET_COLLECTOR_DETAIL_URL = `${process.env.REACT_APP_API_URL}/get_collector_details_for_eth_wallets.php`;

export function getEthTokenMeta(tiersList: string) {
    return axios({
        method: 'post',
        url: GET_ETH_TOKEN_META_URL,
        headers: {
            'content-type': 'application/x-www-form-urlencoded',
        },
        data: {
            tiersList
        }
    })
}

export function getCollectorDetails(walletList: Array<any>) {
    return axios({
        method: 'post',
        url: GET_COLLECTOR_DETAIL_URL,
        headers: {
            'content-type': 'application/x-www-form-urlencoded',
        },
        data: {
            walletList
        }
    })
}

const defaultOptions: any = {
    watchQuery: {
      fetchPolicy: 'no-cache',
      errorPolicy: 'ignore',
    },
    query: {
      fetchPolicy: 'no-cache',
      errorPolicy: 'all',
    },
}

const client = new ApolloClient({
    uri: APIURL,
    cache: new InMemoryCache(),
    defaultOptions: defaultOptions,
})

type IGetCollectionQuery = (id: string) => string

export const getCollectionQuery: IGetCollectionQuery = (id) => {
    const collectionQuery = `
        query {
            collections(
                where: {
                    id: ${id}
                }
            ) {
                id
                tiers 
                collectionOwner
            }
        }
    `
    return collectionQuery
}

type ITokenQuery = (id: string) => string

export const getTierQuery: ITokenQuery = (id) => {
    const tokenQuery = `
        query {
            tiers(
                where: {
                    id: "${id}"
                }
            ) {
                id
                tier
                price
                shareData
                creator
                totalSupply
                quantityAvailable
            }
          }
    `
    return tokenQuery
}

type ITokenBalanceQuery = (id: string, skip: number, totalNoOfRecord: number) => string

export const getTierBalanceQuery: ITokenBalanceQuery = (id, skip, totalNoOfRecord) => {
    const tokenBalanceQuery = `
        query {
            tierBalances(
                skip: ${skip},
                first: ${totalNoOfRecord},
                where: {
                    collection: "${id}"
                }
            ) {
                collection {
                    id
                }
                tier {
                    tier
                    totalSupply
                }
                walletAddress
                balance
            }
        }
    `
    return tokenBalanceQuery
}

type ITokenBalanceQueryForTier = (id: string, skip: number, totalNoOfRecord: number, tier: string) => string

export const getTierBalanceForTierQuery: ITokenBalanceQueryForTier = (id, skip, totalNoOfRecord, tier) => {
    const tokenBalanceQuery = `
        query {
            tierBalances(
                skip: ${skip},
                first: ${totalNoOfRecord},
                where: {
                    collection: "${id}",
                    tier: "${id + tier}"
                }
            ) {
                collection {
                    id
                }
                tier {
                    tier
                    totalSupply
                }
                walletAddress
                balance
            }
        }
    `
    return tokenBalanceQuery
}

export const getTierBalancesForWallet = (walletAddress: string) => {
    const tierBalanceQuery = `
        query {
            tierBalances(
                where : {
                    walletAddress: "${walletAddress}"
                }
            ) {
                collection {
                    id
                }
                tier {
                    id
                    tier
                    shareData
                    totalSupply
                }
                walletAddress
                balance
            }
        }
    `
    return tierBalanceQuery
}

export const getTokenBalancesForWallet = (collectionID:string, tierID :string, walletAddress: string) => {
    const tokenBalanceQuery = `
        query {
            tokenBalances(
                where : {
                    collection: "${collectionID}",
                    tier: "${tierID}",
                    walletAddress: "${walletAddress}",
                }
            ) {
                tokenId
                walletAddress
                balance
            }
        }
    `
    return tokenBalanceQuery
}

export const getETHTransactionHistoryQuery = (walletAddress: string, addressType: 'from' | 'to') => {
    const transactionHistoryQuery = `
        query {
            tokenHistories(
                where : {
                    ${addressType}: "${walletAddress}"
                }
            ) {
                id
                collection {
                    id
                }
                tier {
                    tier
                }
                eventType
                from
                to
                quantity
                txHash
            }
        }
    `
    return transactionHistoryQuery
}

export const getETHRoyaltyClaimHistoryQuery = (walletAddress: string) => {
    const royaltyClaimTransactionHistoryQuery = `
        query {
            royaltyClaimHistories(
                where : {
                    claimer: "${walletAddress}"
                }
            ) {
                id
                collection {
                    id
                }
                tier {
                    tier
                }
                claimer
                depositNumber
                claimedAmount
                txHash
                createdAt
            }
        }
    `
    return royaltyClaimTransactionHistoryQuery
}

export const getCollections = async (id: string) => {
    const data = await client
                        .query({
                            query: gql(getCollectionQuery(id)),
                        })
    return data.data.collections[0]
}

export const getTierBalance = async (collectionID: any, skip: number, tierToBeFiltered: string) => {

    if(tierToBeFiltered == 'all') {
        const result = await client.query({ query: gql(getTierBalanceQuery(collectionID, skip, 10))});
        return result.data.tierBalances;
    } else if(tierToBeFiltered == '0' || tierToBeFiltered == '1' || tierToBeFiltered == '2') {
        const result = await client.query({ query: gql(getTierBalanceForTierQuery(collectionID, skip, 10, tierToBeFiltered))});
        return result.data.tierBalances; 
    }
    
    const result = await client.query({ query: gql(getTierBalanceQuery(collectionID, skip, 10))});
    return result.data.tierBalances;
    
}

export const getCollectionTiers = async (tiers: any, collectionID: string, tierToBeFiltered: string) => {
    let tiersData: any = []
    let tiersBalance: any = await getTierBalance(collectionID, 0, tierToBeFiltered);
    let totalTierQuantity = 0;
    let totalOwnershipOffered: any = 0;
    let saleAmountInETH = 0;

    for(let i = 0; i < tiers.length; i++) {
        let result = await client.query({ query: gql(getTierQuery(collectionID + tiers[i])) })
        totalTierQuantity += parseInt(result.data.tiers[0].totalSupply)

        let tierSaleAmount = (parseFloat(result.data.tiers[0].totalSupply) - parseFloat(result.data.tiers[0].quantityAvailable)) * (parseFloat(result.data.tiers[0].price) / 10 ** 18)

        saleAmountInETH += tierSaleAmount
        totalOwnershipOffered += (parseFloat(result.data.tiers[0].shareData) / 100000) * parseInt(result.data.tiers[0].totalSupply)
        
        tiersData.push(result.data.tiers[0])
    }
    return [tiersData, tiersBalance, totalTierQuantity, totalOwnershipOffered, saleAmountInETH];
}

export const extractWalletAddresses = (tierBalanceList: Array<any>) => {
    let walletAddresses: Array<any> = [];

    tierBalanceList.map((data) => {
        walletAddresses.push(data.walletAddress)
    })

    return walletAddresses
}

export const getSubsequentTierBalance = async (collectionID: string, skip: number, filterVal: string) => {

    let result;

    if(filterVal == 'all') {
        result = await client.query({ query: gql(getTierBalanceQuery(collectionID, skip, 10))});
    } else if(filterVal == '0' || filterVal == '1' || filterVal == '2') {
        result = await client.query({ query: gql(getTierBalanceForTierQuery(collectionID, skip, 10, filterVal))});
    } else {
        result = await client.query({ query: gql(getTierBalanceQuery(collectionID, skip, 10))});
    }
    
    
    const tierBalances: Array<any> = result.data.tierBalances
    const collectorDetails: any = await getCollectorDetails(extractWalletAddresses(tierBalances))

    let tokensBalances: Array<any> = [];

    tierBalances.map((data: any) => {
        let temp = data
        temp.collectorName = collectorDetails.data.collectors_data[data.walletAddress].collector_name
        temp.collectorImage = collectorDetails.data.collectors_data[data.walletAddress].collector_image

        tokensBalances.push(temp)
    })

    return tierBalances
}

export const getCollectionData = async (id: string, tierToBeFiltered: string) => {
    const collection = await getCollections(id);
    let [tokensData, tierBalances, totalTokenQuantity, totalOwnershipOffered, saleAmountInETH]: any = [null];

    if(tierToBeFiltered == 'all') {
        [tokensData, tierBalances, totalTokenQuantity, totalOwnershipOffered, saleAmountInETH] = await getCollectionTiers(collection.tiers, id, tierToBeFiltered);
    } else if(tierToBeFiltered == '0' || tierToBeFiltered == '1' || tierToBeFiltered == '2') {
        [tokensData, tierBalances, totalTokenQuantity, totalOwnershipOffered, saleAmountInETH] = await getCollectionTiers(collection.tiers, id, tierToBeFiltered);
    } else {
        [tokensData, tierBalances, totalTokenQuantity, totalOwnershipOffered, saleAmountInETH] = await getCollectionTiers(collection.tiers, id, 'all');
    }
    

    const collectorDetails: any = await getCollectorDetails(extractWalletAddresses(tierBalances))

    let tokensBalances: Array<any> = [];

    tierBalances.map((data: any) => {
        let temp = data
        temp.collectorName = collectorDetails.data.collectors_data[data.walletAddress].collector_name
        temp.collectorImage = collectorDetails.data.collectors_data[data.walletAddress].collector_image

        tokensBalances.push(temp)
    })

    const primarySaleAmountInFiat = Number(saleAmountInETH * parseFloat(collectorDetails.data.eth_fiat_price)).toFixed(2)

    const result = {
        collection,
        tokensData,
        tokensBalances,
        totalTokenQuantity,
        totalOwnershipOffered,
        primarySaleAmountInFiat
    }

    return result
}

const extractTokenIDs = (tierDatas: Array<any>) => {
    const tierList: Array<string> = []
    tierDatas.map((tierData: any) => {
        let temp: any = {}
        temp.collectionID = tierData.collection.id
        temp.tier = tierData.tier.tier
        temp.walletAddress = tierData.walletAddress
        tierList.push(temp)
    })
    return JSON.stringify(tierList)
}

const getOwnedTokenIDsForCollection = async (collectionID: string, tier: string, walletAddress: string) => {

    const tierID = collectionID + tier;
    const tokensData = await client.query({query: gql(getTokenBalancesForWallet(collectionID, tierID, walletAddress))})

    let ownedTokenIDs: Array<any> = [];

    tokensData.data.tokenBalances.map((data: any) => {
        if(data.balance > 0) {
            ownedTokenIDs.push(data.tokenId)
        }
    })

    return ownedTokenIDs;
}

export const tokenBalancesForWallet = async (walletAddress: string) => {
    const balanceData = await client.query({query: gql(getTierBalancesForWallet(walletAddress))})
    const tokenMetaData = await getEthTokenMeta(extractTokenIDs(balanceData.data.tierBalances))
    let result: Array<any> = []
    
    await balanceData.data.tierBalances.map(async (balance: any) => {
        let temp: any = balance
        temp['token_tier'] = tokenMetaData.data.tokensMeta[balance.collection.id + '' + balance.tier.tier].token_tier
        temp['token_image'] = tokenMetaData.data.tokensMeta[balance.collection.id + '' + balance.tier.tier].token_image
        temp['token_quantity'] = tokenMetaData.data.tokensMeta[balance.collection.id + '' + balance.tier.tier].token_quantity
        temp['collection_name'] = tokenMetaData.data.tokensMeta[balance.collection.id + '' + balance.tier.tier].collection_name
        temp['artist_name'] = tokenMetaData.data.tokensMeta[balance.collection.id + '' + balance.tier.tier].artist_name
        temp['ownership_offered'] = tokenMetaData.data.tokensMeta[balance.collection.id + '' + balance.tier.tier].ownership_offered
        temp['drop_date'] = tokenMetaData.data.tokensMeta[balance.collection.id + '' + balance.tier.tier].drop_date
        temp['collection_id'] = tokenMetaData.data.tokensMeta[balance.collection.id + '' + balance.tier.tier].collection_id
        temp['blockchain'] = tokenMetaData.data.tokensMeta[balance.collection.id + '' + balance.tier.tier].blockchain
        result.push(temp)
    })
    return result
} 

