export const getETHTransactionHistoryData = async (walletAddress: string) => {
    const fromResponseData = await client.query({query: gql(getETHTransactionHistoryQuery(walletAddress, 'from'))})
    const toResponseData = await client.query({query: gql(getETHTransactionHistoryQuery(walletAddress, 'to'))})

    let transactionHistoryData = [...fromResponseData.data.tokenHistories, ...toResponseData.data.tokenHistories]

    let tierList: Array<any> = [];

    transactionHistoryData.map((data) => {
        let temp: any = {}
        temp.collectionID = data.collection.id
        temp.tier = data.tier.tier
        tierList.push(temp)
    })

    const tokensMetaData = await getEthTokenMeta(JSON.stringify(tierList))

    let result: Array<any> = [];

    transactionHistoryData.map((data) => {
        let temp: any = data
        temp['token_tier'] = tokensMetaData.data.tokensMeta[data.collection.id + '' + data.tier.tier].token_tier
        temp['token_image'] = tokensMetaData.data.tokensMeta[data.collection.id + '' + data.tier.tier].token_image
        temp['token_quantity'] = tokensMetaData.data.tokensMeta[data.collection.id + '' + data.tier.tier].token_quantity
        temp['collection_name'] = tokensMetaData.data.tokensMeta[data.collection.id + '' + data.tier.tier].collection_name
        temp['blockchain'] = tokensMetaData.data.tokensMeta[data.collection.id + '' + data.tier.tier].blockchain
        result.push(temp)
    })

    return result
}

export const getETHRoyaltyClaimHistoryData = async (walletAddress: string) => {
    const response = await client.query({query: gql(getETHRoyaltyClaimHistoryQuery(walletAddress))})
    return response.data.royaltyClaimHistories
}

