import {Suspense, lazy, FC} from 'react'
import {Navigate, Route, Routes} from 'react-router-dom'
import {DashboardWrapper} from '../pages/dashboard/DashboardWrapper'
import {MenuTestPage} from '../pages/MenuTestPage'
import {VerifyUser} from '../modules/auth/components/VerifyUser'
import { MasterLayout } from '../customlayout/MasterLayout'
import {getCSSVariableValue} from "../../_metronic/assets/ts/_utils";
import TopBarProgress from 'react-topbar-progress-indicator'
import EditArtist from 'app/modules/artistWizard/ArtistEdit'
import { CreateArtist } from '../modules/artistWizard/ArtistCreate'
import { DropDetailPage } from 'app/modules/dropDetail/DropDetailPage'
import { Logout } from 'app/modules/auth'
import { AdminOrderView } from 'app/modules/order-management/AdminOrderView'
import { AdminTransferView } from 'app/modules/transfer-management/AdminTransferDetails'
import { TransactionCancelPage } from '../modules/stripePages/TransactionCancelPage'
import { FAQs } from 'app/modules/royalHomePage/components/FAQs'
import { UserListView } from 'app/modules/user-management/user-list-view/UserListView'
import { MusicianMetadataManagement } from 'app/modules/musician-metadata-management/MusicianMetadataManagement'


export function PrivateRoutes() {
  const BuilderPageWrapper = lazy(() => import('../pages/layout-builder/BuilderPageWrapper'))
  const ProfilePage = lazy(() => import('../modules/profile/ProfilePage'))
  const WizardsPage = lazy(() => import('../modules/wizards/WizardsPage'))
  const AccountPage = lazy(() => import('../modules/accounts/AccountPage'))
  const WidgetsPage = lazy(() => import('../modules/widgets/WidgetsPage'))
  const ExplorePage = lazy(() => import('../modules/explore/collectionListing/ExplorePage'))
  const UserNotVerified = lazy(() => import('../modules/pages/NotVerifiedPage'))
  const YourCollections = lazy(() => import('../modules/yourCollections/YourCollections'))
  // const WalletLogin = lazy(() => import('../modules/wallets/WalletLogin'))
  const WalletLoginNew = lazy(() => import('../modules/wallets/WalletLoginNew'))
  const PreviewCollection = lazy(() => import('../modules/yourCollections/DropDetailPreviewPage'))
  const UpdateCollection = lazy(() => import('../modules/yourCollections/components/editWizards/WizardsPage'))
  const Dashboard = lazy(() => import('../modules/dashboard/Dashboard'))
  const ArtistList = lazy(() => import('../modules/artistWizard/ArtistList'))
  const ArtistDetail = lazy(() => import('../modules/artistWizard/ArtistDetail'))
  const CollectorDetail = lazy(() => import('../modules/collectorDetail/CollectorDetail'))
  const CollectorAssetDetail = lazy(() => import('../modules/collectorDetail/CollectionAssetDeatil'))
  const RoyalHomePage = lazy(() => import('../modules/royalHomePage/RoyalHomePage'))
  const StripeStatusPage = lazy(() => import('../modules/stripePages/StripeStatusPage'))
  const ReceiptPage = lazy(() => import('../modules/stripePages/ReceiptPage'))
  const ViewOrderDetail = lazy(() => import('../modules/order-management/orderDetailView/OrderDetail'))
  const UserDetailView = lazy(() => import('../modules/user-management/user-detail-view/UserDetailView'))

  return (
    <Routes>
      <Route element={<MasterLayout />}>
        <Route path='auth/*' element={<Navigate to='/explore' />} />
        <Route path='/dashboard' element={<Dashboard />} />
        <Route 
          path='/explore' 
          element={
            <SuspensedView>
              <ExplorePage />
            </SuspensedView>
        } 
        />
        <Route path='/homePage' element={<RoyalHomePage />} />
        <Route path='/drop-detail' element={<DropDetailPage />} />
        <Route path='/logout' element={<Logout />} />
        <Route path='/preview-collection' element={<PreviewCollection />} />
        <Route path='/update-collection' element={<UpdateCollection />} />
        {/* <Route path='/my-wallet' element={<WalletLogin />} /> */}
        <Route path='/my-wallet' element={<WalletLoginNew />} />
        <Route path='/verify-user' element={<VerifyUser />} />
        <Route path='/user-not-verified' element={<UserNotVerified />} />
        <Route path='/builder' element={<BuilderPageWrapper />} />
        <Route path='/profile' element={<ProfilePage />} />
        <Route path='/create' element={<WizardsPage />} />
        <Route path='/create-artist' element={<CreateArtist />} />
        <Route path='/edit-artist' element={<EditArtist />} />
        <Route path='/view-artists' element={<ArtistList />} />
        <Route path='/artist-detail' element={<ArtistDetail />} />
        <Route path='/collector-detail' element={<CollectorDetail />} />
        <Route path='/collector-asset-detail' element={<CollectorAssetDetail />} />
        <Route path='/your-collections' element={<YourCollections />} />
        <Route path='/order-status' element={<StripeStatusPage />} />
        <Route path='/transaction-cancelled' element={<TransactionCancelPage />} />
        <Route path='/view-receipt' element={<ReceiptPage />} />
        <Route path='/view-orders' element={<AdminOrderView />} />
        <Route path='/view-users' element={<UserListView />} />
        <Route path='/view-transfers' element={<AdminTransferView />} />
        <Route path='/order-detail' element={<ViewOrderDetail />} />
        <Route path='/user-detail' element={<UserDetailView />} />
        <Route path='/update-metadata' element={<MusicianMetadataManagement />} />
        <Route path='/crafted/widgets' element={<WidgetsPage />} />
        <Route path='/account' element={<AccountPage />} />
        <Route path='/menu-test' element={<MenuTestPage />} />
        <Route path='/faqs' element={<FAQs />} />
      </Route>
    </Routes>
  )
}

const SuspensedView: FC = ({children}) => {
  const baseColor = getCSSVariableValue('--bs-primary')
  TopBarProgress.config({
      barColors: {
          '0': baseColor,
      },
      barThickness: 1,
      shadowBlur: 5,
  });
  return <Suspense fallback={<TopBarProgress/>}>{children}</Suspense>
}
