import axios from "axios";

const GET_ORDER_DATA_URL = `${process.env.REACT_APP_API_URL}/get-all-order-data.php`;
const GET_ORDER_DETAIL_URL = `${process.env.REACT_APP_API_URL}/get-order-detail.php`;
const GET_REFUND_DETAIL_URL = `${process.env.REACT_APP_API_URL}/get-refund-data.php`;
const UPDATE_ORDER_URL = `${process.env.REACT_APP_API_URL}/update-order-data.php`;
const SEARCH_ORDER_BY_ID = `${process.env.REACT_APP_API_URL}/search-order.php`;

export function getAllOrderData(skip: any, noOfRecords: any) {
    return axios({
        method: 'post',
        url: GET_ORDER_DATA_URL,
        headers: {
            'content-type': 'application/x-www-form-urlencoded',
        },
        data: {
            skip,
            noOfRecords
        }
    })
}

export function getOrderDetail(orderCode: string) {
    return axios({
        method: 'post',
        url: GET_ORDER_DETAIL_URL,
        headers: {
            'content-type': 'application/x-www-form-urlencoded',
        },
        data: {
            orderCode
        }
    })
}

export function getRefundDetail(paymentIntent: string) {
    return axios({
        method: 'post',
        url: GET_REFUND_DETAIL_URL,
        headers: {
            'content-type': 'application/x-www-form-urlencoded',
        },
        data: {
            paymentIntent
        }
    })
}

export const updateOrderAfterReplacement = (
    orderCode: string,
    tokenID: string,
    buyer: string,
    txHash: string
) => {
    return axios({
        method: 'post',
        url: UPDATE_ORDER_URL,
        headers: {
            'content-type': 'application/x-www-form-urlencoded',
        },
        data: {
            orderCode,
            tokenID,
            buyer,
            txHash
        }
    })
}

const CREATE_REFUND_URL = `${process.env.REACT_APP_NODE_JS_SERVER_URL}/create-refund`;
const CANCEL_REFUND_URL = `${process.env.REACT_APP_NODE_JS_SERVER_URL}/cancel-refund`;

export const createRefund = (
    userID: string,
    password: string,
    paymentIntent: string
) => {
    return axios({
        method: 'post',
        url: CREATE_REFUND_URL,
        headers: {
            'content-type': 'application/json',
        },
        data: {
            userID,
            password,
            paymentIntent
        }
    })
}

export const cancelRefund = (
    userID: string,
    password: string,
    refundID: string
) => {
    return axios({
        method: 'post',
        url: CANCEL_REFUND_URL,
        headers: {
            'content-type': 'application/json',
        },
        data: {
            userID,
            password,
            refundID
        }
    })
}

export function searchOrderByID(orderCode: string) {
    return axios({
        method: 'post',
        url: SEARCH_ORDER_BY_ID,
        headers: {
            'content-type': 'application/x-www-form-urlencoded',
        },
        data: {
            orderCode
        }
    })
}