import { FormattedMessage } from "react-intl"


export const Bio = (props: any) => {
    return (
        <>
            <div id="bio" style={{ background: '#000' }} className="vc_row wpb_row vc_row-fluid">
                <div className="wpb_column vc_column_container vc_col-sm-12">
                    <div className="vc_column-inner">
                        <div className="wpb_wrapper">
                            <h2 style={{ textAlign: 'center', color: '#ffff' }} className="vc_custom_heading vc_custom_1505769344824 fit_bg_text  fit_bg_center ">
                                <FormattedMessage id="HOME_PAGE.OUR_TOP_ARTIST" />
                            </h2>
                            <div className="vc_row wpb_row vc_inner vc_row-fluid">
                                <div className="wpb_column vc_column_container vc_col-sm-6">
                                    <div className="vc_column-inner vc_custom_1505769882729">
                                        <div className="wpb_wrapper">
                                            <div  
                                                style={{ fontSize: '25px', lineHeight: '32px', textAlign: 'left', color: '#ffff' }}
                                                className="vc_custom_heading color1_txt sr-letter-spacing-6 "
                                            >
                                                <FormattedMessage id="HOME_PAGE.ABOUT_THE_ARTIST" />
                                            </div>
                                            <div 
                                                style={{ color: '#ffff', fontSize: '67px', lineHeight: '73px', textAlign: 'left', fontFamily: 'Yeseva One', fontWeight: '400', fontStyle: 'normal' }}
                                                className="vc_custom_heading color1_txt iron-responsive vc_custom_1506719885211 "
                                            >
                                                {props.featuredArtistData.artist_name}
                                            </div>
                                            <div className="wpb_text_column wpb_content_element " >
                                                <div className="wpb_wrapper">
                                                    <p>
                                                        <span style={{ color: '#ffff' }}>
                                                            {JSON.parse(props.featuredArtistData.about_artist.replace(/&quot;/g, '"')).map((lines: any) => (
                                                                <>
                                                                    <span className='mb-2'>{lines}</span> <br />
                                                                </>
                                                            ))}
                                                        </span>
                                                    </p>
                                                </div>
                                            </div>
                                            {/* <div className="wpb_single_image wpb_content_element vc_align_left  vc_custom_1505769798114 ">
                                                <figure className="wpb_wrapper vc_figure">
                                                    <div className="vc_single_image-wrapper 0   vc_box_border_grey">
                                                        <img width="253" height="57" src="https://soundrise.irontemplates.com/indie/wp-content/uploads/sites/14/2017/03/signature.png" className="vc_single_image-img attachment-full" alt="" loading="lazy" />
                                                    </div>
                                                </figure>
                                            </div> */}
                                        </div>
                                    </div>
                                </div>
                                <div className="wpb_column vc_column_container vc_col-sm-6">
                                    <div className="vc_column-inner">
                                        <div className="wpb_wrapper">
                                            <div className="wpb_single_image wpb_content_element vc_align_left ">
                                                <figure className="wpb_wrapper vc_figure">
                                                    <div className="vc_single_image-wrapper 0   vc_box_border_grey">
                                                        <img 
                                                            className="vc_single_image-img " 
                                                            src={`${process.env.REACT_APP_API_ARTIST_IMAGE_URL}/${props.featuredArtistData.artist_image}`} 
                                                            width="590" 
                                                            height="590" 
                                                            alt="Top Artist Image" 
                                                        />
                                                    </div>
                                                </figure>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}