/* eslint-disable jsx-a11y/anchor-is-valid */
import { CircularProgress } from '@mui/material'
import { useState, useEffect } from 'react'
import { useNavigate } from 'react-router-dom'
import { FormattedMessage, useIntl } from 'react-intl'
import { useSelector } from 'react-redux'
import { RootState } from 'setup'
import { getAllOrderData, searchOrderByID } from './api'
import { OrderListPagination } from './components/Pagination'
import { AlertDanger } from '../alerts/Alerts'

const tokenType: any = {
    '0' : 'Gold',
    '1': 'Platinum',
    '2': 'Diamond'
}

const AdminOrderView: React.FC = () => {

    const { isAdmin }: any = useSelector<RootState>(
        ({auth}) => ({
            isAdmin: auth.user?.isAdmin
        })
    )

    const [orderData, setOrderData] = useState<Array<any>>([])
    const[totNumOfPage, setTotNumOfPage] = useState<any>(0)
    const [pageNumList, setPageNumList] = useState<Array<any>>([])
    const [loading, setLoading] = useState<boolean>(true)
    const [selectedPage, setSelectedPage] = useState<any>(1)
    const [currentPage, setCurrentPage] = useState<any>(1)
    const [numOfRec] = useState<any>(10)
    const intl = useIntl()

    const [warnings, setWarnings] = useState<string>("")
    const [showWarnings, setShowWarnings] = useState<boolean>(false)

    if(warnings != "") {
        setTimeout(() => {
          setShowWarnings(false);
          setTimeout(() => {
            setWarnings("");
          }, 300);
        }, 5000);
    }

    const fetchNextData = (pageNum: any) => {
        console.log(pageNum)
        if(pageNum > 0 && pageNum <= totNumOfPage) {
            setLoading(true)
            const skip = (parseInt(pageNum) - 1) * numOfRec

            getAllOrderData(skip, numOfRec)
            .then((response) => {
                setSelectedPage(pageNum)
                setCurrentPage(pageNum)
                setOrderData(response.data.order_data)
                setTotNumOfPage(response.data.total_no_of_page)
                setLoading(false)
            })
            .catch((error) => console.log(error))
        } else {
            setWarnings('Invalid page number provided, please check it.')
            setShowWarnings(true)
        }
    }

    const navigate = useNavigate()

    const shrinkWalletAddress = (walletAddress: string) => {
        const walletStart = walletAddress.slice(0, 6)
        const walletEnd = walletAddress.slice(35)
    
        return walletStart + '......' + walletEnd
    }

    const preparePageNumList = (size: any) => {
        let pageNumList = []

        for (let index = 1; index <= size; index++) {
            pageNumList.push(index)
        }

        return pageNumList
    }

    useEffect(() => {
        getAllOrderData(0, 10)
        .then((response) => {
            if(response.data.success) {
                setOrderData(response.data.order_data)
                setTotNumOfPage(response.data.total_no_of_page)
                setPageNumList(preparePageNumList(response.data.total_no_of_page))
                setLoading(false)
            } else {

            }
        })
    }, [])

    const getWalletType = (wType: string) => {
        if(wType == 'custodial_wallet_eth' || wType == 'custodial_wallet_klay') return 'Custodial Wallet'
        if(wType == 'kaikas_wallet' || wType == 'metamask_wallet') return 'Owned Wallet'

        return 'Others'
    }

    const handleSearch = (event: any) => {

        let orderCode = event.target.value

        if(event.code == 'Enter' && orderCode.length > 0) {

            searchOrderByID(orderCode)
            .then((response) => {
                if(response.data.success) {
                    setOrderData(response.data.order_data)
                    setTotNumOfPage(response.data.total_no_of_page)
                    setPageNumList(preparePageNumList(response.data.total_no_of_page))
                }
            })
        }
    }

    return (
        <>
            {isAdmin == '1' && 
                <>
                    {!loading && 
                        <>
                            <div className={`card`}>
                                {warnings != "" && 
                                    <AlertDanger message={warnings} checked={showWarnings} />
                                }
                                {/* begin::Header */}
                                <div className='card-header border-0 pt-5'>
                                    {/* <h3 className='card-title align-items-start flex-column'>
                                        <span className='card-label fw-bolder fs-3 mb-1'>Order Histories</span>
                                        <span className='text-muted mt-1 fw-bold fs-7'>Over 500 new products</span>
                                    </h3> */}
                                    <div className="card-title">
                                        <div className="d-flex align-items-center position-relative my-1">
                                            <span className="svg-icon svg-icon-1 position-absolute ms-6">
                                                <svg xmlns="http://www.w3.org/2000/svg" xmlnsXlink="http://www.w3.org/1999/xlink" width="24px" height="24px" viewBox="0 0 24 24" version="1.1">
                                                    <g stroke="none" strokeWidth="1" fill="none" fillRule="evenodd">
                                                        <rect x="0" y="0" width="24" height="24" />
                                                        <path d="M14.2928932,16.7071068 C13.9023689,16.3165825 13.9023689,15.6834175 14.2928932,15.2928932 C14.6834175,14.9023689 15.3165825,14.9023689 15.7071068,15.2928932 L19.7071068,19.2928932 C20.0976311,19.6834175 20.0976311,20.3165825 19.7071068,20.7071068 C19.3165825,21.0976311 18.6834175,21.0976311 18.2928932,20.7071068 L14.2928932,16.7071068 Z" fill="#000000" fillRule="nonzero" opacity="0.3" />
                                                        <path d="M11,16 C13.7614237,16 16,13.7614237 16,11 C16,8.23857625 13.7614237,6 11,6 C8.23857625,6 6,8.23857625 6,11 C6,13.7614237 8.23857625,16 11,16 Z M11,18 C7.13400675,18 4,14.8659932 4,11 C4,7.13400675 7.13400675,4 11,4 C14.8659932,4 18,7.13400675 18,11 C18,14.8659932 14.8659932,18 11,18 Z" fill="#000000" fillRule="nonzero" />
                                                    </g>
                                                </svg>
                                            </span>
                                            <input 
                                                onKeyPress={(e) => handleSearch(e)} 
                                                type="text" 
                                                className="form-control form-control-solid w-250px ps-14" 
                                                placeholder={intl.formatMessage({id: 'ADMIN.ORDER_DETAIL.SEARCH_ORDER'})} 
                                            />
                                        </div>
                                    </div>
                                </div>
                                {/* end::Header */}
                                {/* begin::Body */}
                                <div className='card-body py-3'>
                                    {/* begin::Table container */}
                                    <div className='table-responsive'>
                                    {/* begin::Table */}
                                    <table className='table align-middle gs-0 gy-4'>
                                        {/* begin::Table head */}
                                        <thead>
                                            <tr className='fw-bolder text-muted bg-light'>
                                                <th className='ps-4 min-w-150px rounded-start'>
                                                    <FormattedMessage id="ADMIN.VIEW_USER.ORDER_HISTORY.TOKEN_DETAILS" />
                                                </th>
                                                <th className='text-center min-w-125px'><FormattedMessage id="ADMIN.VIEW_USER.ORDER_HISTORY.USER_ID" /></th>
                                                <th className='text-center min-w-125px'><FormattedMessage id="ADMIN.VIEW_USER.ORDER_HISTORY.ORDER_CODE" /></th>
                                                <th className='text-center min-w-125px'><FormattedMessage id="ADMIN.VIEW_USER.ORDER_HISTORY.BUYER" /></th>
                                                <th className='text-center min-w-125px'><FormattedMessage id="ADMIN.VIEW_USER.ORDER_HISTORY.AMOUNT" /></th>
                                                <th className='text-center min-w-125px'><FormattedMessage id="ADMIN.VIEW_USER.ORDER_HISTORY.PAYMENT_STATUS" /></th>
                                                <th className='text-center min-w-125px'><FormattedMessage id="ADMIN.VIEW_USER.ORDER_HISTORY.MINT_STATUS" /></th>
                                                <th className='text-center min-w-200px'><FormattedMessage id="ADMIN.VIEW_USER.ORDER_HISTORY.ORDER_DATA" /></th>
                                                <th className='text-center min-w-200px text-end rounded-end pe-5'><FormattedMessage id="ADMIN.VIEW_USER.ORDER_HISTORY.VIEW_DETAILS" /></th>
                                            </tr>
                                        </thead>
                                        {/* end::Table head */}
                                        {/* begin::Table body */}
                                        <tbody>
                                            {orderData.map((data: any) => (
                                                <tr>
                                                    <td>
                                                    <div className='d-flex align-items-center'>
                                                        <div className='symbol symbol-50px me-5'>
                                                        <img
                                                            src={`${process.env.REACT_APP_API_TOKEN_IMAGE_URL}/${data.token_image}`}
                                                            className=''
                                                            alt=''
                                                        />
                                                        </div>
                                                        <div className='d-flex justify-content-start flex-column'>
                                                        <span className='text-dark fw-bolder text-hover-primary mb-1 fs-6'>
                                                            {data.collection_name}
                                                        </span>
                                                        <span className='text-muted fw-bold text-muted d-block fs-7'>
                                                            <FormattedMessage id='CREATE_EDIT_COLLECTION.TIER' /> : 
                                                            <span>{tokenType[data.tier]}</span>
                                                        </span>
                                                        </div>
                                                    </div>
                                                    </td>
                                                    <td>
                                                        <span className='text-center text-dark fw-bolder text-hover-primary d-block mb-1 fs-6'>
                                                            {data.user_id}
                                                        </span>
                                                    {/* <span className='text-muted fw-bold text-muted d-block fs-7'>Paid</span> */}
                                                    </td>
                                                    <td>
                                                    <span className='text-center text-dark fw-bolder text-hover-primary d-block mb-1 fs-6'>
                                                        {data.order_code}
                                                    </span>
                                                    {/* <span className='text-muted fw-bold text-muted d-block fs-7'>Paid</span> */}
                                                    </td>
                                                    <td>
                                                    <span className='text-center text-dark fw-bolder text-hover-primary d-block mb-1 fs-6'>
                                                        {shrinkWalletAddress(data.buyer)}
                                                    </span>
                                                    {/* <span className='text-muted fw-bold text-muted d-block fs-7'>Rejected</span> */}
                                                    </td>
                                                    <td>
                                                    <span className='text-center text-dark fw-bolder text-hover-primary d-block mb-1 fs-6'>
                                                        {data.amount_paid} <span className='ms-2'>{data.currency_type}</span>
                                                    </span>
                                                    </td>
                                                    <td>
                                                    <span className='text-center text-dark fw-bolder text-hover-primary d-block mb-1 fs-6'>
                                                        {data.status == 'paid' ? (
                                                            <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-check-circle-fill text-success" viewBox="0 0 16 16">
                                                                <path d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0zm-3.97-3.03a.75.75 0 0 0-1.08.022L7.477 9.417 5.384 7.323a.75.75 0 0 0-1.06 1.06L6.97 11.03a.75.75 0 0 0 1.079-.02l3.992-4.99a.75.75 0 0 0-.01-1.05z"/>
                                                            </svg>
                                                        ) : (
                                                            <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-x-circle-fill text-danger" viewBox="0 0 16 16">
                                                                <path d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0zM5.354 4.646a.5.5 0 1 0-.708.708L7.293 8l-2.647 2.646a.5.5 0 0 0 .708.708L8 8.707l2.646 2.647a.5.5 0 0 0 .708-.708L8.707 8l2.647-2.646a.5.5 0 0 0-.708-.708L8 7.293 5.354 4.646z"/>
                                                            </svg>
                                                        )}
                                                    </span>
                                                    </td>
                                                    <td>
                                                    <span className='text-center text-dark fw-bolder text-hover-primary d-block mb-1 fs-6'>
                                                        {data.minted == '1' ? (
                                                            <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-check-circle-fill text-success" viewBox="0 0 16 16">
                                                                <path d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0zm-3.97-3.03a.75.75 0 0 0-1.08.022L7.477 9.417 5.384 7.323a.75.75 0 0 0-1.06 1.06L6.97 11.03a.75.75 0 0 0 1.079-.02l3.992-4.99a.75.75 0 0 0-.01-1.05z"/>
                                                            </svg>
                                                        ) : (
                                                            <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-x-circle-fill text-danger" viewBox="0 0 16 16">
                                                                <path d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0zM5.354 4.646a.5.5 0 1 0-.708.708L7.293 8l-2.647 2.646a.5.5 0 0 0 .708.708L8 8.707l2.646 2.647a.5.5 0 0 0 .708-.708L8.707 8l2.647-2.646a.5.5 0 0 0-.708-.708L8 7.293 5.354 4.646z"/>
                                                            </svg>
                                                        )}
                                                    </span>
                                                    </td>
                                                    <td>
                                                    <span className='text-center text-dark fw-bolder text-hover-primary d-block mb-1 fs-6'>
                                                        {data.created}
                                                    </span>
                                                    </td>
                                                    {/* <span className='text-muted fw-bold text-muted d-block fs-7'>Insurance</span> */}
                                                    <td className='text-center text-end pe-5'>
                                                        <span
                                                            className='btn btn-bg-light btn-active-color-primary me-1'
                                                            onClick={() => navigate('/order-detail', {state: data.order_code})}
                                                        >
                                                            <FormattedMessage id="ADMIN.VIEW_USER.ORDER_HISTORY.VIEW_DETAILS" />
                                                        </span>
                                                    </td>
                                                </tr>
                                            ))}
                                        </tbody>
                                        {/* end::Table body */}
                                    </table>
                                    {/* end::Table */}
                                    </div>
                                    {/* end::Table container */}
                                </div>
                                {/* begin::Body */}
                            </div>
                            <div className='d-flex justify-content-center mt-5'>
                                <OrderListPagination 
                                    totalNumberOfPages={totNumOfPage}
                                    fetchNextData={fetchNextData}
                                    selectedPage={selectedPage}
                                    setSelectedPage={setSelectedPage}
                                    currentPage={currentPage}
                                    setCurrentPage={setCurrentPage}
                                />
                            </div>
                        </>
                    }

                    {loading && 
                        <div className='d-flex w-100 h-100'>
                            <div className='my-auto mx-auto'>
                                <div className='d-flex flex-column justify-content-center'>
                                    <span className='text-primary fs-3'>Loading</span> <br />
                                    <span className='ms-4'>
                                        <CircularProgress />
                                    </span>
                                </div>
                            </div>
                        </div>
                    }
                </>
            }
        </>
    )
}

export {AdminOrderView}
