import axios from "axios";

const GET_TRANSFER_DATA_URL = `${process.env.REACT_APP_API_URL}/get-all-transfer-data.php`;

export function getAllTransferData(skip: any, noOfRecords: any) {
    return axios({
        method: 'post',
        url: GET_TRANSFER_DATA_URL,
        headers: {
            'content-type': 'application/x-www-form-urlencoded',
        },
        data: {
            skip,
            noOfRecords
        }
    })
}