import {Action} from '@reduxjs/toolkit'
import {persistReducer} from 'redux-persist'
import storage from 'redux-persist/lib/storage'

export interface ActionWithPayload<T> extends Action {
  payload?: T
}

export const actionTypes = {
  saveCategory: '[saveCategory] Action',
  setCollectionMetaData: '[setCollectionData] Action'
}

const initialAuthState: IEcomState = {
  categories: [],
  collectionsMeta: []
}

export interface IEcomState {
  categories: Array<any>,
  collectionsMeta: Array<any>
}

export const reducer = persistReducer(
  {storage, key: 'v100-marketplace-ecom', whitelist: ['categories', 'collectionsMeta']},
  (state: any = initialAuthState, action: ActionWithPayload<IEcomState>) => {
    switch (action.type) {
      case actionTypes.saveCategory: {
        const categories = action.payload?.categories
        return {...state, categories}
      }
      
      case actionTypes.setCollectionMetaData: {
        const newState = {...state, collectionsMeta: action.payload?.collectionsMeta}
        return newState
      }

      default:
        return state
    }
  }
)

export const actions = {
  saveCategories: (categories: Array<any>) => ({
    type: actionTypes.saveCategory, payload: {categories}
  }),
  setCollections: (collectionsMeta: Array<any>) => ({
    type: actionTypes.setCollectionMetaData, payload: {collectionsMeta}
  })
}