import { useEffect, useState } from 'react'
import { Banner } from "./components/Banner"
import { Events } from "./components/Events"
import { Bio } from "./components/Bio"
import { VideoSection } from "./components/VideoSection"
import { ContactSection } from "./components/ContactSection"
import { HomeFooter } from "./components/HomeFooter"
import { HeaderWrapper } from "app/customlayout/components/header/HeaderWrapper"
// import { Gallery } from './components/Gallery'
import { LatestDrops } from "./components/LatestDrops"
import { getMusicianMetaForHomePage } from './api'
import { CircularProgress } from '@mui/material'

// https://v.ftcdn.net/04/61/21/82/700_F_461218290_soisWR35Vjw1kGzRyQdNsRFMUPMg7OJT_ST.mp4
// https://v.ftcdn.net/04/65/14/79/700_F_465147985_1aXayaNk0QQZ1cWm1Ibk8KZO0kpYgiS3_ST.mp4


const RoyalHomePage = () => {

    const [loading, setLoading] = useState<boolean>(true)
    const [featuredCollectionData, setFeaturedCollectionData] = useState<any>(null)
    const [featuredArtistData, setFeaturedArtistData] = useState<any>(null)
    const [bannerVideoName, setBannerVideoName] = useState<any>(null)
    const [youtubeVideoURL, setYoutubeVideoURL] = useState<string>("")
 
    useEffect(() => {
        getMusicianMetaForHomePage()
        .then((response) => {
            setFeaturedCollectionData(response.data.featured_collection_data)
            setFeaturedArtistData(response.data.featured_artist_data)
            setBannerVideoName(response.data.banner_video_name)
            setYoutubeVideoURL(response.data.youtube_video_url)
            setLoading(false)
        })
    }, [])

    return (
        <>
            {/* <HeaderWrapper /> */}
            {!loading && 
                <div 
                    style={{ marginTop: '-95px' }}
                    className="page"
                >
                    <div id="pusher-wrap">
                        <div id="pusher" className="menu-type-classNameic-menu">
                            <div className="pjax-container">
                                <div id="wrapper" className="wrapper">
                                    <div className="homepage-container">
                                        <article id="post-1072" className="sr_it-single-post post-1072 page type-page status-publish hentry">
                                            <div className="entry">
                                                <Banner 
                                                    bannerVideoURL={`${process.env.REACT_APP_ASSET_URL}/${bannerVideoName}`} 
                                                    featuredCollectionData={featuredCollectionData}
                                                />
                                                <section className="vc_section bg-center-top vc_custom_1507322004427 vc_section-has-fill">
                                                    {/* <Events /> */}
                                                    <LatestDrops />
                                                    <div style={{ marginTop: '100px' }}>
                                                        <Bio 
                                                            featuredArtistData={featuredArtistData}
                                                        />
                                                    </div>
                                                    <div>
                                                        <VideoSection 
                                                            youtubeVideoURL={youtubeVideoURL}
                                                        />
                                                    </div>
                                                    <ContactSection />                  
                                                </section>
                                            </div>
                                            <div id="comments" className="comments-area"></div>
                                        </article>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <a href="#" id="back-to-top-mobile" className="footer-wrapper-backtotop-mobile">
                        <i className="fas fa-chevron-up"></i>
                    </a>
                    <a href="#" id="back-to-top" className="footer-wrapper-backtotop">
                        <i className="fas fa-chevron-up"></i>
                    </a>
                </div>
            }
            {loading && 
                <div className='d-flex w-100 h-100'>
                    <div className='my-auto mx-auto'>
                        <div className='d-flex flex-column justify-content-center'>
                            <span className='text-primary fs-3'>Loading</span> <br />
                            <span className='ms-4'>
                                <CircularProgress />
                            </span>
                        </div>
                    </div>
                </div>
            }
        </>
    )
}

export default RoyalHomePage