import axios from "axios";


export const ACCOUNT_INFO_CHANGE_URL = `${process.env.REACT_APP_API_URL}/update-account-info.php`;
export const CHANGE_PASSWORD_URL = `${process.env.REACT_APP_API_URL}/change-password.php`;
export const PROFILE_UPDATE_URL = `${process.env.REACT_APP_API_URL}/profile-update.php`;
export const GET_KLAYTN_TRANSACTION_HISTORY = `${process.env.REACT_APP_API_URL}/get-klaytn-transaction-history.php`;
export const GET_KLAYTN_ROYALTY_CLAIM_TRANSACTION_HISTORY = `${process.env.REACT_APP_API_URL}/get-klaytn-royalty-claim-histories.php`;
export const SUBMIT_REQUEST_URL = `${process.env.REACT_APP_API_URL}/submit-request.php`;


export function changeAccountInfo(userID: string, authToken: string, language: string, timeZone: string, currency: string) {
    return axios({
      method: 'post',
      url: ACCOUNT_INFO_CHANGE_URL,
      headers: {
        'content-type': 'application/x-www-form-urlencoded',
        'Authorization': authToken
      },
      data: {
        userID,
        authToken,
        language,
        timeZone,
        currency
      }
    });
}

export function changePassword(currentPassword: string, password: string, cPassword: string, authToken: string, email: string, locale: string, timeZone: string) {
  return axios({
    method: 'post',
    url: CHANGE_PASSWORD_URL,
    headers: {
      'content-type': 'application/x-www-form-urlencoded',
      'Authorization': authToken
    },
    data: {
      currentPassword,
      password,
      cPassword,
      authToken,
      email,
      locale,
      timeZone
    }
  });
}

export function updateProfile(formData: any) {
  return axios({
    method: 'post',
    url: PROFILE_UPDATE_URL,
    headers: {
      'content-type': 'multipart/form-data',
    },
    data: formData
  });
} 


export function getKlaytnTransactionHistory(walletAddress: string) {
  return axios({
    method: 'post',
    url: GET_KLAYTN_TRANSACTION_HISTORY,
    headers: {
      'content-type': 'application/x-www-form-urlencoded'
    },
    data: {
      walletAddress
    }
  });
}


export function getKlaytnRoyaltyClaimTransactionHistory(walletAddress: string) {
  return axios({
    method: 'post',
    url: GET_KLAYTN_ROYALTY_CLAIM_TRANSACTION_HISTORY,
    headers: {
      'content-type': 'application/x-www-form-urlencoded'
    },
    data: {
      walletAddress
    }
  });
}

export function submitRequest(
  // authToken: string,
  email: string,
  subject: string,
  description: string
) {
  return axios({
    method: 'post',
    url: SUBMIT_REQUEST_URL,
    headers: {
      'content-type': 'application/x-www-form-urlencoded'
    },
    data: {
      // authToken,
      email,
      subject,
      description
    }
  });
}