/* eslint-disable no-script-url,jsx-a11y/anchor-is-valid */
import { FormattedMessage } from "react-intl";
import { useSelector, shallowEqual, useDispatch } from "react-redux";
import { RootState } from "setup";
import { useNavigate } from "react-router-dom";
import * as auth from "../auth/redux/AuthRedux"

export const DropDetailSelectTier = (props: any) => {

    const navigate = useNavigate()
    const dispatch = useDispatch()

    const { tierFilterVal }: any = useSelector<RootState>(
        ({auth}) => ({
            tierFilterVal: auth.filterVal
        })
    )

    const handleTierSelect = (filterVal: string) => {

        if(tierFilterVal != filterVal) {
            props.setFilterLoading(true)
            dispatch(auth.actions.setDropDetailTier(filterVal))
        }
    }

    return (
    <>
        {/*begin::Navigation*/}
        <ul className="navi navi-hover" style={{paddingLeft: 0}}>
            <li 
                className={`navi-item cursor-pointer pt-2 py-2 ${tierFilterVal == undefined || tierFilterVal == 'all' && 'bg-black'}`} 
                style={{ listStyleType: "none" }} 
                onClick={() => handleTierSelect('all')}
            >
                <span className="navi-link">
                    <span 
                        className={`navi-text ms-6 ${tierFilterVal == 'all' ? 'text-white' : 'text-dark'}`}
                    >
                        <FormattedMessage id="DROPDOWN.TIER_SELECT.ALL_TIER" />
                    </span>
                </span>
            </li>
            <div className='separator'></div>
            <li 
                className={`navi-item cursor-pointer py-2 ${tierFilterVal == '0' && 'bg-black'}`} 
                style={{ listStyleType: "none" }} 
                onClick={() => handleTierSelect('0')}
            >
                <span className="navi-link">
                    <span 
                        className={`navi-text ms-6 ${tierFilterVal == '0' ? 'text-white' : 'text-dark'}`}
                    >
                        <FormattedMessage id="CREATE_EDIT_COLLECTION.TIER.GOLD" />
                    </span>
                </span>
            </li>
            <div className='separator'></div>
            <li 
                className={`navi-item cursor-pointer py-2 ${tierFilterVal == '1' && 'bg-black'}`}
                style={{ listStyleType: "none" }} 
                onClick={() => handleTierSelect('1')}
            >
                <span className="navi-link">
                    <span 
                        className={`navi-text ms-6 ${tierFilterVal == '1' ? 'text-white' : 'text-dark'}`}
                    >
                        <FormattedMessage id="CREATE_EDIT_COLLECTION.TIER.PLATINUM" />
                    </span>
                </span>
            </li>
            <div className='separator'></div>
            <li 
                className={`navi-item cursor-pointer py-2 pb-2 ${tierFilterVal == '2' && 'bg-black'}`} 
                style={{ listStyleType: "none" }} 
                onClick={() => handleTierSelect('2')}
            >
                <span className="navi-link">
                    <span 
                        className={`navi-text ms-6 ${tierFilterVal == '2' ? 'text-white' : 'text-dark'}`}
                    >
                        <FormattedMessage id="CREATE_EDIT_COLLECTION.TIER.DIAMOND" />
                    </span>
                </span>
            </li>
        </ul>
        {/*end::Navigation*/}
    </>);
}
