import {useState} from 'react'
import { useSelector, shallowEqual, useDispatch } from 'react-redux'
import {useNavigate} from 'react-router-dom'
import * as Yup from 'yup'
import {useFormik} from 'formik'
import { Button } from '@mui/material';
import { Dialog } from '@mui/material';
import { DialogActions } from '@mui/material';
import { DialogContent } from '@mui/material';
import { DialogTitle } from '@mui/material';
import {toAbsoluteUrl} from '../../../../../_metronic/helpers'
import { languages, timeZones } from '../../../accounts/components/settings/LangAndTimeZone'
import { AddUserDetailModel} from '../models'
import {RootState} from '../../../../../setup'
import { FormattedMessage } from 'react-intl'
import { register } from 'app/modules/auth/redux/AuthCRUD'
import { createUsers } from '../../api'

const userDetailsSchema = Yup.object().shape({
    fullName: Yup.string().required('Username is required'),
    userName: Yup.string().required('Username is required'),
    email: Yup.string()
      .email('Wrong email format')
      .min(3, 'Minimum 3 symbols')
      .max(50, 'Maximum 50 symbols')
      .required('Email is required'),
    password: Yup.string().min(6, 'Require mininmum 6 characters').required('Password is required'),
    cPassword: Yup.string()
        .required('Password confirmation is required')
        .when('password', {
        is: (val: string) => (val && val.length > 0 ? true : false),
        then: Yup.string().oneOf([Yup.ref('password')], "Password and Confirm Password didn't match"),
    }),
    adminPassword: Yup.string().min(6, 'Require mininmum 6 characters').required('Admin Password is required')
})

export const AddUserDialogBox = (props: any) => {
    const [loading, setLoading] = useState(false)
    const [isAdmin, setIsAdmin] = useState<boolean>(false)

    const dispatch = useDispatch()
    const navigate = useNavigate()

    const {adminID, authToken}: any = useSelector<RootState>(
        ({auth}) => ({
            authToken: auth.accessToken,
            adminID: auth.user?.userID
        })
    )

    const handleIsAdminChange = () => {
        setIsAdmin(!isAdmin)
    }

    const initialValues: AddUserDetailModel = {
        fullName: "",
        userName: "",
        email: "",
        password: "",
        cPassword: "",
        adminPassword: ""
    }

    const formik = useFormik<AddUserDetailModel>({
        initialValues,
        validationSchema: userDetailsSchema,
        onSubmit: (values) => {
            setLoading(true)
            setTimeout(() => {
                createUsers(values.fullName, values.userName, values.email, values.password, isAdmin, adminID, values.adminPassword)
                .then((response) => {
                    if(response.data.created) {
                        localStorage.setItem('userSuccessMsg', response.data.message)
                        window.location.reload()
                    } else {
                        if(response.data.message) {
                            localStorage.setItem('userFailureMsg', response.data.message)
                            window.location.reload()
                        } else {
                            localStorage.setItem('userFailureMsg', 'Failed to create an account. Please try again later.')
                            window.location.reload()
                        }
                    }
                })
            }, 1000)
        }
    })

    return (
        <>
            <Dialog open={props.addUserDialogOpen} onClose={() => props.setAddUserDialogOpen(false)} aria-labelledby="form-dialog-title">
                <DialogTitle className='px-5 text-center fw-bolder text-muted' id="form-dialog-title"><FormattedMessage id="ADMIN.USER.ADD_USER.ADD_NEW_USER" /></DialogTitle>
                <div className='separator'></div>
                <DialogContent>
                    <form 
                        className="form" 
                        noValidate 
                        onSubmit={formik.handleSubmit}
                        style={{ padding: '10px 50px' }}
                    >
                        <div 
                            className="d-flex flex-column scroll-y me-n7" 
                            id="kt_modal_add_user_scroll" 
                            data-kt-scroll="true" 
                            data-kt-scroll-activate="{default: false, lg: true}" 
                            data-kt-scroll-max-height="auto" 
                            data-kt-scroll-dependencies="#kt_modal_add_user_header" 
                            data-kt-scroll-wrappers="#kt_modal_add_user_scroll" 
                            data-kt-scroll-offset="300px"
                        >
                            <div className="fv-row mb-7">
                                <label className="required fw-bold fs-6 mb-2"><FormattedMessage id="ADMIN.VIEW_USER.USER_SUMMARY.FULLNAME" /></label>
                                <input 
                                    type="text" 
                                    className="form-control form-control-solid mb-3 mb-lg-0" 
                                    placeholder="Fullname" 
                                    {...formik.getFieldProps('fullName')}
                                />
                                {formik.touched.fullName && formik.errors.fullName && (
                                    <div className='fv-plugins-message-container'>
                                        <div className='fv-help-block text-danger'>{formik.errors.fullName}</div>
                                    </div>
                                )}
                            </div>

                            <div className="fv-row mb-7">
                                <label className="required fw-bold fs-6 mb-2"><FormattedMessage id="ADMIN.VIEW_USER.USER_SUMMARY.USERNAME" /></label>
                                <input 
                                    type="text" 
                                    className="form-control form-control-solid mb-3 mb-lg-0" 
                                    placeholder="Username" 
                                    {...formik.getFieldProps('userName')} 
                                />
                                {formik.touched.userName && formik.errors.userName && (
                                    <div className='fv-plugins-message-container'>
                                        <div className='fv-help-block text-danger'>{formik.errors.userName}</div>
                                    </div>
                                )}
                            </div> 

                            <div className="fv-row mb-7">
                                <label className="required fw-bold fs-6 mb-2"><FormattedMessage id="ADMIN.USER.ADD_USER.EMAIL" /></label>
                                <input 
                                    type="email" 
                                    className="form-control form-control-solid mb-3 mb-lg-0" 
                                    placeholder="example@domain.com" 
                                    {...formik.getFieldProps('email')}
                                />
                                {formik.touched.email && formik.errors.email && (
                                    <div className='fv-plugins-message-container'>
                                        <div className='fv-help-block text-danger'>{formik.errors.email}</div>
                                    </div>
                                )}
                            </div>

                            <div className="fv-row mb-7">
                                <label className='form-check form-check-custom form-check-solid align-items-start'>
                                    <input
                                        className='form-check-input me-3'
                                        type='checkbox'
                                        name='email-preferences[]'
                                        defaultChecked={isAdmin}
                                        onChange={handleIsAdminChange}
                                    />
                                    <span className='d-flex flex-column align-items-start'>
                                        <span className='fw-bold fs-6 mb-0'><FormattedMessage id="ADMIN.USER.ADD_USER.ADMIN" /></span>
                                    </span>
                                </label>
                            </div>

                            <div className="fv-row mb-7">
                                <label className="required fw-bold fs-6 mb-2"><FormattedMessage id="ADMIN.USER.ADD_USER.PASSWORD" /></label>
                                <input 
                                    type="password" 
                                    className="form-control form-control-solid mb-3 mb-lg-0" 
                                    placeholder="Password" 
                                    {...formik.getFieldProps('password')}
                                />
                                {formik.touched.password && formik.errors.password && (
                                    <div className='fv-plugins-message-container'>
                                        <div className='fv-help-block text-danger'>{formik.errors.password}</div>
                                    </div>
                                )}
                            </div>

                            <div className="fv-row mb-7">
                                <label className="required fw-bold fs-6 mb-2"><FormattedMessage id="ADMIN.USER.ADD_USER.CONFIRM_PASSWORD" /></label>
                                <input 
                                    type="password" 
                                    className="form-control form-control-solid mb-3 mb-lg-0" 
                                    placeholder="Password Confirmation" 
                                    {...formik.getFieldProps('cPassword')}
                                />
                                {formik.touched.cPassword && formik.errors.cPassword && (
                                    <div className='fv-plugins-message-container'>
                                        <div className='fv-help-block text-danger'>{formik.errors.cPassword}</div>
                                    </div>
                                )}
                            </div>

                            <div className="fv-row mb-7">
                                <label className="required fw-bold fs-6 mb-2"><FormattedMessage id="ADMIN.USER.ADD_USER.ADMIN_PASSWORD" /></label>
                                <input 
                                    type="password" 
                                    className="form-control form-control-solid mb-3 mb-lg-0" 
                                    placeholder="Admin Password" 
                                    {...formik.getFieldProps('adminPassword')}
                                />
                                {formik.touched.adminPassword && formik.errors.adminPassword && (
                                    <div className='fv-plugins-message-container'>
                                        <div className='fv-help-block text-danger'>{formik.errors.adminPassword}</div>
                                    </div>
                                )}
                            </div>
                        </div>
                        <div className="text-center pt-15">
                            <button type="submit" className="btn btn-primary">
                                <span className="indicator-label"><FormattedMessage id="ADMIN.USER.ADD_USER.SUBMIT" /></span>
                                {loading === true && 
                                    <span className="spinner-border spinner-border-sm align-middle ms-2"></span>
                                }
                            </button>
                        </div>
                    </form>
                </DialogContent>
            </Dialog>
        </>
    )
}