import axios from "axios";

const GET_COLLECTIONS_META_URL = `${process.env.REACT_APP_API_URL}/get-collection-metadata.php`;
const GET_COLLECTION_META_URL = `${process.env.REACT_APP_API_URL}/get-collection-meta-by-id.php`;
const UPDATE_COLLECTION_URL = `${process.env.REACT_APP_API_URL}/update-collection-meta.php`;
const DELETE_COLLECTION_URL = `${process.env.REACT_APP_API_URL}/delete-collection-meta.php`;
const UPDATE_MINT_STATUS_URL = `${process.env.REACT_APP_API_URL}/update-mint-status.php`;
const GET_DEPOSIT_DATA = `${process.env.REACT_APP_API_URL}/get-deposit-data.php`;

export function getCollectionMeta() {
    return axios({
        method: 'get',
        url: GET_COLLECTIONS_META_URL
    })
}

export function updateCollection(
    formData: any
    ) {
      return axios({
        method: 'post',
        url: UPDATE_COLLECTION_URL,
        headers: {
          'content-type': 'multipart/form-data',
        },
        data: formData
      })
}

export function deleteCollection(
    id: any
    ) {
      return axios({
        method: 'post',
        url: DELETE_COLLECTION_URL,
        headers: {
          'content-type': 'application/x-www-form-urlencoded',
        },
        data: {
            id
        }
      })
}

export function getCollectionMetaById(id: string | null) {
    return axios({
        method: 'post',
        url: GET_COLLECTION_META_URL,
        headers: {
            'content-type': 'application/x-www-form-urlencoded',
        },
        data: {
            id
        }
    })
}

export function getKlayDepositData(collectionID: string | null) {
  return axios({
      method: 'post',
      url: GET_DEPOSIT_DATA,
      headers: {
          'content-type': 'application/x-www-form-urlencoded',
      },
      data: {
        collectionID
      }
  })
}

export function updateMintStatus(collectionID: string, txHash: string) {
  return axios({
      method: 'post',
      url: UPDATE_MINT_STATUS_URL,
      headers: {
          'content-type': 'application/x-www-form-urlencoded',
      },
      data: {
          collectionID,
          txHash
      }
  })
}