import {useState} from "react";
import { Button } from '@mui/material';
import { Dialog } from '@mui/material';
import { DialogActions } from '@mui/material';
import { DialogContent } from '@mui/material';
import { DialogTitle } from '@mui/material';
import { toAbsoluteUrl } from "_metronic/helpers";
import { useSelector } from "react-redux";
import { RootState } from "setup";
import { AlertDanger } from "app/modules/alerts/Alerts";
import { FormattedMessage } from "react-intl";


export const PasswordConfirmDialog = (props: any) => {

    const [loading, setLoading] = useState<boolean>(false)
    const [checked, setChecked] = useState<boolean>(false)
    
    const [message, setMessage] = useState<any>('')

    const handlePasswordChange = (event: any) => {
        event.preventDefault()

        props.setPassword(event.target.value)
    }

    const handleSubmit = async () => {
        props.setShowPassConfirmDialog(false)
        
        if(props.password.length > 0) {
            props.handleDeleteUsers()
        } else {
            setMessage('Please enter your password.')
            setChecked(true)
        }
        
    }

    return (
        <>
            <Dialog open={props.showPassConfirmDialog} onClose={() => props.setShowPassConfirmDialog(false)} aria-labelledby="form-dialog-title">
                {message && message != '' && 
                    <AlertDanger message={message} checked={checked} />
                }
                <DialogTitle className='px-5 text-center fw-bolder text-muted' id="form-dialog-title">
                    <FormattedMessage id="PASSWORD_CONFIRM_DIALOG.CONFIRMATION" />
                </DialogTitle>
                <div className='separator'></div>
                <DialogContent>
                    <div 
                        className='d-flex px-5 justify-content-center mb-5'
                    >
                        <span 
                            className='px-2 pb-4 ms-4 fs-5 cursor-pointer'
                        >
                            <FormattedMessage id="PASSWORD_CONFIRM_DIALOG.PHRASE" />
                        </span>
                    </div>

                    <div className='row mb-6'>
                        <label className='col-lg-4 col-form-label required fw-bold fs-6 text-muted'>
                        <FormattedMessage id="ADMIN.USER.ADD_USER.PASSWORD" />
                        </label>
                        <div className='col-lg-8 fv-row'>
                            <input
                                type='password'
                                autoComplete='off'
                                className={'form-control form-control-lg form-control-solid'}
                                value={props.password}
                                onChange={(event) => handlePasswordChange(event)}
                            />
                        </div>
                    </div>
                </DialogContent>

                <div className='separator mb-5'></div>

                <div className="me-5 mt-5 mb-5 text-center">
                    <span 
                        onClick={() => handleSubmit()}
                        className="btn btn-primary"
                    >
                        <FormattedMessage id="PASSWORD_CONFIRM_DIALOG.CONFIRM" />
                    </span>
                </div>
            </Dialog>
        </>
    )
}