import {useState} from "react";
import { useNavigate } from 'react-router-dom'
import { Button } from '@mui/material';
import { Dialog } from '@mui/material';
import { DialogActions } from '@mui/material';
import { DialogContent } from '@mui/material';
import { DialogTitle } from '@mui/material';
import { getCheckoutSessionURL, getOrderNonce, createNewOrder, sendTokenPurchaseEmail, getTokenID } from "./common";
import { toAbsoluteUrl } from "_metronic/helpers";
import { useSelector } from "react-redux";
import { RootState } from "setup";
import Caver from 'caver-js'
import Web3 from "web3"
import { klaytnABI } from '../contract/abi'
import { contractAbi } from 'app/modules/contract/abi'
import { FormattedMessage, useIntl } from "react-intl";

declare var window: any

let klaytn: any = window.klaytn

const caver = new Caver(klaytn);

export const PaymentMethodDialog = (props: any) => {

    const [loading, setLoading] = useState<boolean>(false)
    const [minting, setMinting] = useState<boolean>(false);
    const [openConfirmPopup, setOpenConfirmPopup] = useState(false)
    const navigate = useNavigate()
    const intl = useIntl()
    const contractAddress: any = process.env.REACT_APP_NFT_CONTRACT_ADDRESS_KLAYTN

    const { 
        walletAddress, 
        walletName, 
        userID, 
        custWalletEth, 
        custWalletKlay,
        authToken,
        user,
        email
    }: any = useSelector<RootState>(
        ({auth}) => ({
            walletAddress: auth.walletAddress,
            walletName: auth.walletName,
            userID: auth.user?.userID,
            custWalletEth: auth.userWalletList.ethCustodialWallet,
            custWalletKlay: auth.userWalletList.klayCustodialWallet,
            authToken: auth.accessToken,
            user: auth.user,
            email: auth.user?.email
        })
    )

    const shrinkWalletAddress = (walletAddress: string) => {
        const walletStart = walletAddress.slice(0, 6)
        const walletEnd = walletAddress.slice(35)
    
        return walletStart + '......' + walletEnd
    }

    const getOrderSuffix = (walletAddress: string) => {
        const walletEnd = walletAddress.slice(35)
    
        return walletEnd
    }

    const showPopup = () => {
        var popup = document.getElementById("connectWallet")!;
        popup.classList.toggle("show");
    }

    const openDialog = (id: string) => {
        props.setDialogOpen(false)
        const element: HTMLElement = document.getElementById(id)!
        element.style.display='block'
    }

    const closeDialog = (id: string) => {
        const element: HTMLElement = document.getElementById(id)!
        element.style.display='none'
    }

    const handleCheckoutSession = async () => {
        setLoading(true)

        if(walletName && props.blockchain == 'ethereum' && walletName != 'metamask') {
            navigate('/my-wallet', { state: { message: intl.formatMessage({ id: 'COLLECTION_DETAIL_PAGE.CONN_METAMASK_WALLET_TO_PURCHASE' }) } })
            return
        }

        if(walletName && props.blockchain == 'klaytn' && walletName != 'kaikas') {
            navigate('/my-wallet', { state: { message: intl.formatMessage({ id: 'COLLECTION_DETAIL_PAGE.CONN_KAIKAS_WALLET_TO_PURCHASE' }) } })
            return
        }

        let _walletAddress = walletAddress;

        if(!walletAddress && props.blockchain == 'ethereum' && custWalletEth == '' ) {
            _walletAddress = null
        } else if(!walletAddress && props.blockchain == 'klaytn' && custWalletKlay == '') {
            _walletAddress = null
        }

        const nonceRes = await getOrderNonce(userID)
        const orderCode = nonceRes.data.nonce + '_' + userID

        getCheckoutSessionURL(
            _walletAddress,
            userID,
            props.collectionName, 
            parseFloat(props.tokenPriceFiat), 
            1, 
            props.tokenTier, 
            props.imageName, 
            props.collectionID,
            props.blockchain,
            orderCode,
            props.artistName
        )
        .then((response) => {
            if(response.data.success) {
                window.location.href = response.data.session_url
                setLoading(false)
            } else {
                setLoading(false)
            }
        })
    }

    async function buyTokenUsingCrytoWalletOnEth(collectionID: string, tier: string, price: any) {
        if(user) {

            if(walletName && walletName != 'metamask') {
                navigate('/my-wallet', { state: { message: intl.formatMessage({ id: 'COLLECTION_DETAIL_PAGE.CONN_METAMASK_WALLET_TO_PURCHASE_SHORT' }) } })
                return
            }

            const web3 = new Web3(`${process.env.REACT_APP_INFURA_URL}`);
            // const contractAddress = process.env.REACT_APP_NFT_CONTRACT_ADDRESS;
            const contractAddress = process.env.REACT_APP_NFT_CONTRACT_ADDRESS_ETHEREUM;
            const nftContract = new web3.eth.Contract(contractAbi, contractAddress);

            let ethereum: any = window.ethereum
            const accounts = await ethereum.request({ method: 'eth_requestAccounts' });

            if(walletAddress && accounts[0] === walletAddress && walletName == 'metamask') {
                setMinting(true)

                ethereum
                .request({
                    method: 'eth_sendTransaction',
                    params: [
                        {
                        'from': accounts[0],
                        'to' : contractAddress,
                        'value': web3.utils.toHex(web3.utils.toWei(price.toString(), 'ether')),
                        'data' : nftContract.methods.buyToken(accounts[0], collectionID, tier, 1).encodeABI()
                        },
                    ],
                })
                .then((txHash: any) => {
                    checkTransactionOnEth(txHash, props.collectionName, tier, 'ethereum', price);
                })
                .catch((error: any) => {
                    console.log(error)
                    setMinting(false)
                });
            } else {
                navigate('/my-wallet', { state: { message: intl.formatMessage({ id: 'COLLECTION_DETAIL_PAGE.CONN_METAMASK_WALLET_TO_PURCHASE2' }) } })
            }
        } else {
            navigate('auth/login')
        }
        
    }

    async function checkTransactionOnEth(
        transactionHash: string, 
        collectionName: string, 
        tier: string, 
        blockchain: string,
        price: string
    ) {
        const web3 = new Web3(`${process.env.REACT_APP_INFURA_URL}`);
         const receipt: any = await web3.eth.getTransactionReceipt(transactionHash);

        if(!receipt) {
            checkTransactionOnEth(transactionHash, collectionName, tier, blockchain, price)
        } else {
            if(receipt.status) {

                const nonceRes = await getOrderNonce(userID)
                const orderCode = nonceRes.data.nonce + '_' + userID
                const tokenID = getTokenID(receipt.logs[1].data)

                console.log(receipt)

                createNewOrder(
                    userID,
                    props.collectionID,
                    tier,
                    tokenID,
                    new Date().toDateString(),
                    walletAddress,
                    orderCode,
                    price,
                    'ETH',
                    transactionHash
                )
                .then((response) => {
                    if(response.data.success) {
                        sendTokenPurchaseEmail(
                            authToken, 
                            user.username,
                            user.userID,
                            price,
                            email, 
                            collectionName, 
                            tier, 
                            blockchain, 
                            transactionHash
                        )
                        .then((response) => {
                            if(response.data.success) {
                                setMinting(false)
                                window.location.href = `https://themusician.io/order-status?success=true&tier=${tier}&name=${props.collectionName}&quantity=1&artist=${props.artistName}&orderCode=${orderCode}`
                            } else {

                            }
                        })
                    } else {
                        setMinting(false)
                    }
                })
                
            } else {
                setMinting(false)
            } 
        }
    }

    async function buyTokenUsingCrytoWalletOnKlay(collectionID: string, tier: any, price: any) {

        setMinting(true)

        if(user) {

            if(walletName && walletName != 'kaikas') {
                navigate('/my-wallet', { state: { message: intl.formatMessage({ id: 'COLLECTION_DETAIL_PAGE.CONN_KAIKAS_WALLET_TO_PURCHASE_SHORT' }) } })
                return
            }

            const myContract = new caver.klay.Contract(klaytnABI, contractAddress)

            await klaytn.enable()

            const account = klaytn.selectedAddress

            if(walletAddress && walletAddress == account && walletName == 'kaikas') {
                myContract.methods.buyToken(
                    account, collectionID, tier, 1
                ).send({from: account, value: caver.utils.toPeb(price.toString(), 'KLAY'), gas: 185000},
                function(error: any, transactionHash: any) {
                    if(transactionHash) {
                        checkTransactionOnKlay(transactionHash, props.collectionName, tier, 'klaytn', price.toString())
                    }
                    if(error) {
                        console.log(error)
                        setMinting(false)
                    }
                });
            } else {
                navigate('/my-wallet', { state: { message: intl.formatMessage({ id: 'COLLECTION_DETAIL_PAGE.CONN_KAIKAS_WALLET_TO_PURCHASE2' }) } })
            }   
        } else {
            navigate('/auth/login')
        }
    }

    async function checkTransactionOnKlay(
        transactionHash: any, 
        collectionName: string, 
        tier: string, 
        blockchain: string,
        price: string
    ) {

        const receipt: any = await caver.rpc.klay.getTransactionReceipt(transactionHash)

        if(!receipt) {
            checkTransactionOnKlay(transactionHash, collectionName, tier, blockchain, price);
        } else {
            switch (receipt.status) {
                case '0x1':
                    console.log(receipt)
                    const nonceRes = await getOrderNonce(userID)
                    const orderCode = nonceRes.data.nonce + '_' + userID
                    const tokenID = getTokenID(receipt.logs[1].data)

                    createNewOrder(
                        userID,
                        props.collectionID,
                        tier,
                        tokenID,
                        new Date().toDateString(),
                        walletAddress,
                        orderCode,
                        price,
                        'KLAY',
                        transactionHash
                    )
                    .then((response) => {
                        if(response.data.success) {
                            sendTokenPurchaseEmail(
                                authToken, 
                                user.username,
                                user.userID,
                                price,
                                email, 
                                collectionName, 
                                tier, 
                                blockchain, 
                                transactionHash
                            )
                            .then((response) => {
                                if(response.data.success) {
                                    setMinting(false)
                                    window.location.href = `https://themusician.io/order-status?success=true&tier=${tier}&name=${props.collectionName}&quantity=1&artist=${props.artistName}&orderCode=${orderCode}`
                                } else {
    
                                }
                            })
                        } else {
                            setMinting(false)
                        }
                    })
                    break;
                
                case '0x0':
                    setMinting(false)
                    break
            
                default:
                    break;
            }
        }
    }

    const buyTokenUsingCrypto = (collectionID: string, tier: string, price: any) => {
        if(!walletAddress) return
        
        if(props.blockchain == 'ethereum') {
            buyTokenUsingCrytoWalletOnEth(collectionID, tier, price)
        } else if(props.blockchain == 'klaytn') {
            buyTokenUsingCrytoWalletOnKlay(collectionID, tier, price)
        }
    }

    return (
        <>
            {userID ? (
                <Dialog open={props.dialogOpen} onClose={() => props.setDialogOpen(false)} aria-labelledby="form-dialog-title">
                    <DialogTitle className='px-5 text-center fw-bolder text-muted' id="form-dialog-title">
                        <FormattedMessage id="COLLECTION_DETAIL_PAGE.PAYMENT_METHOD" />
                    </DialogTitle>
                    <div className='separator'></div>
                    <DialogContent>
                        <div 
                            className='d-flex px-5 justify-content-center mb-5'
                        >
                            <span 
                                className='px-2 pb-4 ms-4 fs-5 cursor-pointer'
                            >
                                <FormattedMessage id="COLLECTION_DETAIL_PAGE.SELECT_PAYMENT_METHOD" />
                            </span>
                        </div>
                        <div 
                            className='wallet-select d-flex'
                            style={{ border: '0.5px solid rgb(235 235 235)', boxShadow: 'rgb(0 0 0 / 12%) 0px 0px 4px' }}
                            onClick={() => handleCheckoutSession()}
                        >
                            <div className="d-flex w-100 justify-content-between px-2 py-4 ms-4 cursor-pointer">
                                <div>
                                    <img 
                                        src={toAbsoluteUrl('/media/payment/credit-card.png')} 
                                        alt="Card" 
                                        style={{ width: '20px', marginBottom: '3px' }}
                                    />
                                    <span 
                                        className='ms-4 fs-5'
                                    >
                                        <FormattedMessage id="COLLECTION_DETAIL_PAGE.CREDIT_OR_DEBIT" />
                                    </span>
                                </div>
                                <div style={{ marginBottom: '3px' }}>
                                    {loading && 
                                        <span className='spinner-border spinner-border-sm align-middle me-3'></span>
                                    }
                                </div>
                            </div>
                        </div>
                        <div 
                            className='wallet-select d-flex mt-5'
                            style={{ border: '0.5px solid rgb(235 235 235)', boxShadow: 'rgb(0 0 0 / 12%) 0px 0px 4px' }}
                        >
                            <div className={`d-flex w-100 justify-content-between px-2 py-4 ms-4 ${!walletAddress && 'text-muted'}`}>
                                <div style={{ marginTop: '2px' }}>
                                    <img 
                                        src={toAbsoluteUrl('/media/payment/currency.png')} 
                                        alt="Coin" 
                                        style={{ width: '20px', marginBottom: '3px' }}
                                    />
                                    <span 
                                        onClick={() => buyTokenUsingCrypto(
                                            props.collectionID,
                                            props.tokenTier,
                                            props.tokenPriceCrypto
                                        )} 
                                        className={`ms-4 fs-5 cursor-pointer`}
                                    >
                                        {!minting && intl.formatMessage({id: 'COLLECTION_DETAIL_PAGE.CRYPTO'})}
                                        {minting && intl.formatMessage({ id: 'SETTINGS_PAGE.PLEASE_WAIT' })}
                                    </span>
                                </div>
                                <div style={{ marginBottom: '3px' }}>
                                    {minting && 
                                        <span className='spinner-border spinner-border-sm align-middle me-3'></span>
                                    }
                                </div>
                                {/* <div 
                                    className="popup"
                                    onMouseOver={showPopup}
                                >
                                    <img 
                                        src={toAbsoluteUrl('/media/payment/info.png')} 
                                        className="me-3"
                                        alt="info" 
                                        style={{ width: '15px', marginTop: '6px' }}
                                    />
                                    <span className="popuptext" id="connectWallet">
                                        Your account is not connected with the wallet.
                                    </span>
                                </div> */}
                            </div>
                        </div>

                        <div className="d-flex mt-7">
                            <div>
                                <img 
                                    src={toAbsoluteUrl('/media/payment/info-light.png')} 
                                    alt="info" 
                                    style={{ width: '11px', marginBottom: '2px' }}
                                />
                            </div>
                            <div className="ms-4">
                                <span><FormattedMessage id="COLLECTION_DETAIL_PAGE.PAYMENT.TERMS_AND_COND1" /></span><br/>
                                <span><FormattedMessage id="COLLECTION_DETAIL_PAGE.PAYMENT.TERMS_AND_COND2" /></span><br/>
                                <span><FormattedMessage id="COLLECTION_DETAIL_PAGE.PAYMENT.TERMS_AND_COND3" /></span>
                            </div>
                        </div>
                    </DialogContent>

                    <div className='separator mb-5'></div>

                    <div className="me-5 mt-5 mb-5 text-center">
                        <span 
                            onClick={() => props.setDialogOpen(false)}
                            className="cursor-pointer"
                            style={{ fontWeight: '500' }}
                        >
                            <FormattedMessage id="COLLECTION_DETAIL_PAGE.CENCEL" />
                        </span>
                    </div>

                    <div 
                        className="text-center py-5 mt-5"
                        style={{ background: 'rgb(235 235 235)' }}
                    >
                        <span><FormattedMessage id="COLLECTION_DETAIL_PAGE.DEST_WALLET" /></span><br/>

                        {walletAddress && 
                            <span className="mt-2 text-muted">{shrinkWalletAddress(walletAddress)}</span>
                        }

                        {!walletAddress && custWalletEth != "" && props.blockchain == "ethereum" && 
                            <span className="mt-2 text-muted">{shrinkWalletAddress(custWalletEth)}</span>
                        }

                        {!walletAddress && custWalletKlay != "" && props.blockchain == "klaytn" && 
                            <span className="mt-2 text-muted">{shrinkWalletAddress(custWalletKlay)}</span>
                        }

                        {!walletAddress && props.blockchain == "klaytn" && custWalletKlay == "" && 
                            <span className="mt-2 text-muted"><FormattedMessage id="SETTINGS_PAGE.CUSTODIAL_WALLETS" /></span>
                        }

                        {!walletAddress && props.blockchain == "ethereum" && custWalletEth == "" && 
                            <span className="mt-2 text-muted"><FormattedMessage id="SETTINGS_PAGE.CUSTODIAL_WALLETS" /></span>
                        }
                        
                    </div>
                </Dialog>
            ) : (
                null
            )}

            {/* <div id={`checkout-confirmation`} style={{ display: 'none', zIndex: '2' }} className="modal">
                <span onClick={() => closeDialog(`checkout-confirmation`)} className="close" title="Close Modal">&times;</span>
                <form className="modal-content">
                    <div className="px-7 py-7">
                        <h3>Proceed to checkout</h3>
                        <p className='font-size-15'>
                            Are you sure you want to proceed to the checkout page?
                        </p>

                        <div className="d-flex">
                            <button onClick={() => handleCheckoutSession()} type="button" className="btn btn-primary">
                                Proceed
                                {loading && 
                                    <span className='spinner-border spinner-border-sm align-middle ms-2'></span>
                                }
                            </button>
                            <button onClick={() => closeDialog(`checkout-confirmation`)} type="button" className="btn btn-danger ms-3">
                                Cancel
                            </button>
                        </div>
                    </div>
                </form>
            </div> */}
        </>
    )
}

//""