import { useEffect, useState } from "react";
import { FormattedMessage } from "react-intl";
import { collectionType } from '../../../yourCollections/common/common'
import { calculateTimeLeft } from '../../common'


export const DropDetailTopSection = (props: any) => {

    const [timeDifference, setTimeDifference] = useState<number>(+new Date(props.dropReleaseTime) - +new Date())

    const [timeLeft, setTimeLeft] = useState<any>(calculateTimeLeft(props.dropReleaseTime));

    useEffect(() => {
        setTimeout(() => {
          setTimeLeft(calculateTimeLeft(props.dropReleaseTime));
        }, 1000);
    });

    const timerComponents: any = [];

    Object.keys(timeLeft).forEach((interval) => {
        if (!timeLeft[interval]) {
        return;
        }

        timerComponents.push(
        <span>
            {timeLeft[interval]} {interval}{" "}
        </span>
        );
    });

    return (
        <>
            <div className="mt-5 W-100">
                <hr className="w-100 p-0 m-0 fw-bold" />
                <div className="d-flex md-flex-column W-100">
                    <div className="detail-content-section my-auto">
                        <div className="w-75 mx-auto">
                            {timeDifference > 0 && 
                                <div className="d-flex flex-column">
                                    <span className="text-dark d-flex justify-content-start mt-5">
                                        <div className="d-flex flex-column me-7 text-center">
                                            <span className="mt-1" style={{ fontSize: '15px' }}>Sale Opens in</span>
                                        </div>
                                        <div className="d-flex flex-column me-3 text-center">
                                            <span style={{ fontSize: '20px' }}>{timeLeft.days}</span>
                                            <span className="mt-1" style={{ fontSize: '15px' }}>Days</span>
                                        </div>
                                        <div className="d-flex flex-column mx-3 text-center">
                                            <span style={{ fontSize: '20px' }}>{timeLeft.hours}</span>
                                            <span className="mt-1" style={{ fontSize: '15px' }}>HRS</span>
                                        </div>
                                        <div className="d-flex flex-column mx-3 text-center">
                                            <span style={{ fontSize: '20px' }}>{timeLeft.minutes}</span>
                                            <span className="mt-1" style={{ fontSize: '15px' }}>MINS</span>
                                        </div>
                                        <div className="d-flex flex-column mx-3 text-center">
                                            <span style={{ fontSize: '20px' }}>{timeLeft.seconds}</span>
                                            <span className="mt-1" style={{ fontSize: '15px' }}>SECS</span>
                                        </div>
                                    </span> <br />
                                </div>
                            }
                            <span className="asset-type ms-2">{collectionType[props.collectionType]}</span> <br/>
                            <span className="asset-name">{props.collectionName}</span> <br/>
                            {JSON.parse(props.collectionDescription.replace(/&quot;/g, '"')).map((description: any) => (
                                <p className="asset-description mt-4 text-start">
                                    {description}
                                </p> 
                            ))}
                            <br/>
                            <div className="mt-xl-5">
                                {props.spotifyURL != '' && 
                                    <iframe 
                                        src={props.spotifyURL} 
                                        width="100%" 
                                        height="80" 
                                        frameBorder="0" 
                                        allow="encrypted-media" 
                                        title={props.collectionName}
                                    ></iframe>
                                }
                            </div>
                        </div>
                    </div>
                    <div className="detail-image-section position-relative">
                        <img 
                            style={{ width: '100%', height: '100%' }}
                            src={`${process.env.REACT_APP_API_COLLECTION_IMAGE_URL}/${props.imageName}`} 
                            alt="CollectionImage"
                        />
                        <div 
                            className="d-flex position-absolute"
                            style={{
                                top: '20px',
                                left: '40px'
                            }}
                        >
                            <div className="">
                                <img 
                                    className="w-50px"
                                    style={{ borderRadius: '50%' }}
                                    src={`${process.env.REACT_APP_API_ARTIST_IMAGE_URL}/${props.artistImage}`} 
                                    alt="Artist" 
                                />
                            </div>
                            <div className="d-flex flex-column ms-3">   
                                <span className="text-white text-muted"><FormattedMessage id="CREATE_EDIT_COLLECTION.ARTIST" /></span>
                                <a 
                                    href={`artist-detail?id=${props.artistID}`}
                                    className="text-white text-muted"
                                    style={{ fontWeight: '700' }}
                                >
                                    {props.artistName}
                                </a>
                            </div>
                        </div>
                    </div>
                </div>
                <hr className="w-100 p-0 m-0" />
            </div>
        </>
    )
}