import axios from "axios";
import Web3 from "web3"


export const getTokenID = (rawData: any) => {
    const web3 = new Web3();
  
    const start = (64 * 3) + 2;
    const end = start + 64;
    const tokenIDHex = '0x' + rawData.slice(start, end);
  
    const tokenID = web3.utils.hexToNumberString(tokenIDHex)
  
    return tokenID;
}


export interface ITimeLeft {
    days: number
    hours: number
    minutes: number
    seconds: number
}

type ICalculateTimeLeft = (dropReleaseTime: any) => (ITimeLeft)
export const calculateTimeLeft: ICalculateTimeLeft = (dropReleaseTime: any) => {
    const difference = +new Date(dropReleaseTime) - +new Date();
    let timeLeft: ITimeLeft = {days: 0, hours: 0, minutes: 0, seconds: 0};

    if (difference > 0) {
        timeLeft = {
        days: Math.floor(difference / (1000 * 60 * 60 * 24)),
        hours: Math.floor((difference / (1000 * 60 * 60)) % 24),
        minutes: Math.floor((difference / 1000 / 60) % 60),
        seconds: Math.floor((difference / 1000) % 60),
        };
    }

    return timeLeft;
};


const SEND_PURCHASE_EMAIL = `${process.env.REACT_APP_API_URL}/send-token-purchase-success-email.php`;
const GET_CHECKOUT_SESSION_URL = `${process.env.REACT_APP_API_URL}/create-checkout-session.php`;
const GET_ORDER_NONCE_URL = `${process.env.REACT_APP_API_URL}/get-order-nonce-for-wallet.php`;
const CREATE_NEW_ORDER_URL = `${process.env.REACT_APP_API_URL}/create-new-order.php`;

export function sendTokenPurchaseEmail(
    authToken: string,
    buyerName: string,
    buyerID: string,
    price: string,
    email: string,
    collectionName: string,
    tier: string,
    blockchain: string,
    txHash: string
    ) {
      return axios({
        method: 'post',
        url: SEND_PURCHASE_EMAIL,
        headers: {
          'content-type': 'application/x-www-form-urlencoded',
        },
        data: {
            authToken,
            buyerName,
            buyerID,
            price,
            email,
            collectionName,
            tier,
            blockchain,
            txHash
        }
      })
}

