export function getCheckoutSessionURL(
  walletAddress: string,
  userID: string,
  productName: string, 
  price: number, 
  quantity: number, 
  tier: any, 
  imageName: any, 
  colID: string,
  blockchain: string,
  orderCode: string,
  artistName: string
) {
  return axios({
    method: 'post',
    url: GET_CHECKOUT_SESSION_URL,
    headers: {
      'content-type': 'application/x-www-form-urlencoded',
    },
    data: {
      walletAddress,
      userID,
      productName,
      price,
      quantity,
      tier,
      imageName,
      colID,
      blockchain,
      orderCode,
      artistName
    }
  })
}

export function getOrderNonce(userID: string) {

  return axios({
    method: 'post',
    url: GET_ORDER_NONCE_URL,
    headers: {
      'content-type': 'application/x-www-form-urlencoded',
    },
    data: {
      userID
    }
  })
}

export function createNewOrder(
  userID: string,
  collectionID : string,
  tier : string,
  tokenID : string,
  created : string,
  buyer : string,
  orderCode : string,
  amountPaid : string,
  currencyType : string,
  txHash : string
) {

  return axios({
    method: 'post',
    url: CREATE_NEW_ORDER_URL,
    headers: {
      'content-type': 'application/x-www-form-urlencoded',
    },
    data: {
      userID,
      collectionID,
      tier,
      tokenID,
      created,
      buyer,
      orderCode,
      amountPaid,
      currencyType,
      txHash
    }
  })
}