import { MasterLayout } from 'app/customlayout/MasterLayout'
import {Suspense, lazy, FC} from 'react'
import {Navigate, Route, Routes} from 'react-router-dom'
import TopBarProgress from 'react-topbar-progress-indicator'
import { getCSSVariableValue } from '_metronic/assets/ts/_utils'
import { AuthPage, Logout } from '.'
import { DropDetailPage } from '../dropDetail/DropDetailPage'
import ProductList from '../explore/collectionListing/ExplorePage'
import { FAQs } from '../royalHomePage/components/FAQs'
import RoyalHomePage from '../royalHomePage/RoyalHomePage'

export function PublicPages() {
  return (
    <Routes>
      <Route element={<MasterLayout />}>
        {/* <Route path='/homePage' element={<RoyalHomePage />} /> */}
        <Route path='/explore' element={<ProductList />} />
        <Route path='/homePage' element={<RoyalHomePage />} />
        <Route path='faqs' element={<FAQs />} />
        <Route path='/drop-detail' element={<DropDetailPage />} />
        <Route path='/logout' element={<Logout />} />
        <Route path='auth/*' element={<AuthPage />} />
        <Route path='*' element={<Navigate to={'auth/'} />} />
      </Route>
    </Routes>
  )
}

const SuspensedView: FC = ({children}) => {
  const baseColor = getCSSVariableValue('--bs-primary')
  TopBarProgress.config({
      barColors: {
          '0': baseColor,
      },
      barThickness: 1,
      shadowBlur: 5,
  });
  return <Suspense fallback={<TopBarProgress/>}>{children}</Suspense>
}
