import clsx from 'clsx'
import React, {FC, useState, useEffect} from 'react'
import {useSelector, shallowEqual} from 'react-redux'
import {KTSVG, toAbsoluteUrl} from '../../../../_metronic/helpers'
import {HeaderUserMenu} from '../../../custompartials/HeaderUserMenu'
import {useLayout} from '../../../../_metronic/layout/core'
import {RootState} from '../../../../setup'
import { MenusHeader } from './MenusHeader'
import {MenuItem} from './MenuItem'
import { FormattedMessage, useIntl } from 'react-intl'
import { useLocation } from 'react-router-dom'

const toolbarButtonMarginClass = 'ms-1 ms-lg-3',
  toolbarUserAvatarHeightClass = 'symbol-30px symbol-md-40px'

const Topbar: FC = () => {

  const {config} = useLayout()
  const intl = useIntl()
  const { pathname } = useLocation()

  const isAuthorized = useSelector<RootState>(({auth}) => auth.user, shallowEqual)

  const { userImage } : any = useSelector<RootState>(
    ({auth}) => ({
      userImage: auth.userImages ? auth.userImages.userImage : null
    })
  )

  return (
    <div className='d-flex align-items-stretch flex-shrink-0'>


      <MenusHeader />

      {isAuthorized && 
        <>
          <div
              className={clsx('d-flex align-items-center', toolbarButtonMarginClass)}
              id='kt_header_user_menu_toggle'
            >
              {/* begin::Toggle */}
              <div
                className={clsx('cursor-pointer symbol', toolbarUserAvatarHeightClass)}
                data-kt-menu-trigger='click'
                data-kt-menu-attach='parent'
                data-kt-menu-placement='bottom-end'
                data-kt-menu-flip='bottom'
              >
                <img src={`${process.env.REACT_APP_API_USER_IMAGE_URL}/${userImage ? userImage : 'blank.png'}`} alt='userImage' />
              </div>
              <HeaderUserMenu />
            </div>
        </>
      }

      {/* begin::Aside Toggler */}
      {config.header.left === 'menu' && (
        <div className='d-flex align-items-center d-lg-none ms-2 me-n3' title='Show header menu'>
          <div
            className='btn btn-icon btn-active-light-primary w-30px h-30px w-md-40px h-md-40px'
            id='kt_header_menu_mobile_toggle'
          >
            <KTSVG path='/media/icons/duotone/Text/Toggle-Right.svg' className='svg-icon-1' />
          </div>
        </div>
      )}
    </div>
  )
}

export {Topbar}
