import React, {FC, useEffect, useRef, useState} from 'react'
import {useDispatch, useSelector, shallowEqual} from 'react-redux'
import {RootState} from '../../../setup'
import {AlertDanger, AlertSuccess} from '../alerts/Alerts'
import { createArtist } from './api'
import * as auth from '../auth/redux/AuthRedux'
import { FormattedMessage, useIntl } from 'react-intl'

const CreateArtist: FC = () => {

    const [loading, setLoading] = useState<boolean>(false)
    const [artistName, setArtistName] = useState<string>('')
    const [aboutArtist, setAboutArtist] = useState<string>('')
    const [artistImage, setArtistImage] = useState<any>('')
    const [artistCoverImage, setArtistCoverImage] = useState<any>('')
    const [successMessage, setSuccessMessage] = useState<string>("")
    const [errorMessage, setErrorMessage] = useState<string>("")
    const [checked, setChecked] = useState<boolean>(false)
    const dispatch = useDispatch()
    const intl = useIntl()

    const { isAdmin }: any = useSelector<RootState>(
        ({auth}) => ({
            isAdmin: auth.user?.isAdmin
        }), shallowEqual
    )

    if(successMessage !== "") {
        setTimeout(() => {
          setChecked(false);
          setTimeout(() => {
            setSuccessMessage("");
          }, 200);
        }, 5000);
    }
    
    if(errorMessage !== "") {
        setTimeout(() => {
            setChecked(false);
            setTimeout(() => {
            setErrorMessage("");
            }, 200);
        }, 5000);
    }

    const handleArtistNameChange = (event: any) => {
        event.preventDefault()
        setArtistName(event.target.value)
    }

    const handleAboutArtistChange = (event: any) => {
        event.preventDefault()

        // const newValue = event.target.value.replace('\n', '\\n')
        setAboutArtist(event.target.value)
    }

    const handleArtistImageChange = (e: any) => {
        setArtistImage(e.target.files[0])
    };

    const handleArtistCoverImageChange = (e: any) => {
        setArtistCoverImage(e.target.files[0])
    };

    const validateFormData = () => {
        if(artistName == '' || artistImage == '' || artistCoverImage == '' || aboutArtist == '') {
            return false
        }
        return true
    }

    const handleSubmit = () => {
        let formData = new FormData()
        formData.append('artistImage', artistImage)
        formData.append('artistCoverImage', artistCoverImage)
        formData.append('artistName', artistName)

        const aboutArtistLineBreaks = aboutArtist.split('\n')
        formData.append('aboutArtist', JSON.stringify(aboutArtistLineBreaks))

        if(validateFormData()) {
            setLoading(true)
            createArtist(formData)
            .then((response) => {
                if(response.data.success) {
                    window.scrollTo(0,0)
                    setSuccessMessage(response.data.message)
                } else {
                    if(response.data.message) {
                        window.scrollTo(0,0)
                        setErrorMessage(response.data.message)
                    } else {
                        window.scrollTo(0,0)
                        setErrorMessage(intl.formatMessage({ id: "CREATE_EDIT_ARTIST.ARTIST_CREATION_FAILED" }))
                    }
                }
                setChecked(true)
                setLoading(false)
            }).catch((error) => {
                localStorage.setItem('sessionTimeOut', intl.formatMessage({ id: "SESSION.TIMED_OUT" }))
                dispatch(auth.actions.logout())
              })
        } else {
            window.scrollTo(0,0)
            setErrorMessage(intl.formatMessage({ id: "CREATE_EDIT_COLLECTION.FILL_OUT_DETAILS" }))
            setChecked(true)
            setLoading(false)
        }
        
    }

    return (
        <>
            {isAdmin == '1' &&
                <>
                {successMessage !== "" ? (
                    <AlertSuccess message={successMessage} checked={checked} />
                ) : null }
        
                {errorMessage !== "" ? (
                <AlertDanger message={errorMessage} checked={checked} />
                ) : null }
        
                <div className='card'>
                    <div className='card-body'>
        
                        <div
                            className='stepper stepper-links d-flex flex-column pt-15'
                            id='kt_create_account_stepper'
                        >
                            <div className='stepper-nav mb-5'>
                                <div className='stepper-item current'>
                                    <h3 className='stepper-title'><FormattedMessage id="CREATE_EDIT_ARTIST.CREATE_ARTIST" /></h3>
                                </div>
                            </div>
                        </div>
                        <div className='mx-auto mw-600px w-100 pt-15 pb-10'>
                            <div className='w-100'>
        
                                <div className='mb-10 fv-row'>
                                    <label className='form-label mb-3'>
                                        <FormattedMessage id="CREATE_EDIT_ARTIST.ARTIST_IMAGE" />
                                        <span className='ms-1'>(Recommended size: 500 x 500)</span>
                                    </label>
        
                                    {artistImage !== "" &&
                                        <div>
                                            <img src={URL.createObjectURL(artistImage)} className='w-300px mb-3' />
                                        </div>
                                    }
        
                                    <div>
                                        <input type="file" id="image" accept="image/png, image/jpeg" onChange={(e) => handleArtistImageChange(e)} />
                                    </div>
                                </div>  
        
                                <div className='mb-10 fv-row'>
                                    <label className='form-label mb-3'>
                                        <FormattedMessage id="CREATE_EDIT_ARTIST.ARTIST_COVER_IMAGE" />
                                        <span className='ms-1'>(Recommended size: 1500 x 300)</span>
                                    </label>
        
                                    {artistCoverImage !== "" &&
                                        <div>
                                            <img src={URL.createObjectURL(artistCoverImage)} className='w-600px mb-3' />
                                        </div>
                                    }
        
                                    <div>
                                        <input type="file" id="image" accept="image/png, image/jpeg" onChange={(e) => handleArtistCoverImageChange(e)} />
                                    </div>
                                </div>  
        
                                <div className='mb-10 fv-row'>
                                    <label className='form-label mb-3 required'><FormattedMessage id="CREATE_EDIT_ARTIST.ARTIST_NAME" /></label>
        
                                    <input
                                        type='text'
                                        className='form-control form-control-lg form-control-solid'
                                        name='artistName'
                                        placeholder=''
                                        value={artistName}
                                        onChange={(event) => handleArtistNameChange(event)}
                                    />
                                </div>
        
                                <div className='mb-10 fv-row'>
                                    <label className='form-label mb-3 required'><FormattedMessage id="CREATE_EDIT_ARTIST.ABOUT_ARTIST" /></label>
        
                                    <textarea
                                        rows={5}
                                        className='form-control form-control-lg form-control-solid'
                                        value={aboutArtist}
                                        onChange={(event) => handleAboutArtistChange(event)}
                                        // onKeyPress={(event) => handleEnterPress(event)}
                                    />
                                </div>
                                </div>
                                <div className='d-flex flex-stack pt-15'>
                                <div className='mr-2'>
                                </div>
        
                                <div>
                                {loading && (
                                    <button type='submit' style={{ background: '#ffd600', color: '#000', borderColor: '#0000' }} className='px-4 py-2 me-3 rounded'>
                                        <FormattedMessage id="CREATE_EDIT_COLLECTION.SUBMITTING" />..
                                        <span className='spinner-border spinner-border-sm align-middle ms-2'></span>
                                    </button>
                                )
                                }
        
                                {!loading && (
                                    <button 
                                        onClick={() => handleSubmit()}
                                        style={{ background: '#ffd600', color: '#000', borderColor: '#0000' }} 
                                        className='py-2 px-4 me-3 rounded'
                                    >
                                        <FormattedMessage id="CREATE_EDIT_COLLECTION.SUBMIT" />
                                    </button>
                                )}
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                </>
            } 
        </>
    )
}

export {CreateArtist}
