import { useState, useEffect } from 'react'
import { useSelector, shallowEqual } from 'react-redux'
import { useNavigate } from 'react-router-dom'
import Snackbar from '@mui/material/Snackbar';
import Caver from 'caver-js'
import { RootState } from '../../../../../setup'
import { klaytnABI } from '../../../contract/abi'
import { sendTokenPurchaseEmail } from '../../common'
import { FormattedMessage } from 'react-intl';
import { PaymentMethodDialog } from '../../PaymentMethodDialog';

declare var window: any

let klaytn: any = window.klaytn

const caver = new Caver(klaytn);

export const DropDetailTokenSectionLarge = (props: any) => {

    const [snackBarContent, setSnackBarContent] = useState<string>('')
    const [snackbarState, setSnackbarState]: any = useState({
        open: false,
        vertical: 'bottom',
        horizontal: 'right'
    });
    const [txHash, setTxHash] = useState<string>('');
    const navigate = useNavigate()

    const defaultButtonStatus = [false, false, false]
    const defaultBtnNames = ['Buy Now', 'Buy Now', 'Buy Now']

    const [buttonStatus, setButtonStatus] = useState(defaultButtonStatus)
    const [timeDifference, setTimeDifference] = useState<number>(+new Date(props.dropReleaseTime) - +new Date())
    const [btnNames, setBtnNames] = useState(defaultBtnNames)

    const { vertical, horizontal, open } = snackbarState;

    const {authToken, email, walletAddress, walletName, user}: any = useSelector<RootState>(
        ({auth}) => ({
            authToken: auth.accessToken,
            email: auth.user?.email,
            walletAddress: auth.walletAddress,
            walletName: auth.walletName,
            user: auth.user
        }), shallowEqual
    )

    function displaySnackbar(content: any, _txHash: string) {
        setSnackBarContent(content)
        setTxHash(_txHash)
        setSnackbarState({ open: true, vertical: 'bottom', horizontal: 'right'});
        setTimeout(() => {
            setSnackbarState({ open: false, vertical: 'bottom', horizontal: 'right'});
        }, 20000);
    }

    const renderSnackBar = () => {
        return (
            <Snackbar
                anchorOrigin={{ vertical, horizontal }}
                key={`${vertical},${horizontal}`}
                open={open}
                ContentProps={{
                'aria-describedby': 'message-id',
                }}
                message={
                <span id="message-id">
                    <label className="px-4">{ snackBarContent } 
                        <a href={`https://baobab.scope.klaytn.com/tx/${txHash}?tabId=nftTransfer`}>
                            <FormattedMessage id="COLLECTION_DETAIL_PAGE.HERE" />
                        </a>
                    </label>
                </span>
                }
            />
        )
    }

    const toggleBtnNames = (index: number) => {
        setBtnNames(names => {
            names[index] = 'Buying..'
            return names
        }) 
    }

    const toggleBtnStatus = (index: number) => {
        setButtonStatus(status => {

            let newStatus: any = [];
            status.map((s, i) => {
                if(i != index) {
                    newStatus[i] = true
                } else {
                    newStatus[i] = false
                }
            })
            return newStatus
        })
    }

    const showDialogBox = (
        tier: any,
        colName: any,
        colID: any,
        fiatPrice: any,
        cryptoPrice: any,
        imageName: any
    ) => {
        if(props.userID) {
            props.setSelectedTier(tier)
            props.setSelectedColID(colID)
            props.setSelectedColName(colName)
            props.setPrice(fiatPrice)
            props.setPriceCrypto(cryptoPrice)
            props.setDialogOpen(true)
            props.setSelectedImageName(imageName)
        } else {
            navigate('/auth/login')
        }
    }

    const getClass = (tokenNum: number) => {
        switch (tokenNum) {
            case 1:
                return 'justify-content-center'
            case 2:
                return 'justify-content-around'
            case 3:
                return 'justify-content-between'
            default:
                return 'justify-content-center';
        }
    }

    const optimizeTokenBenefitsList = (jsonList: any) => {

        let stdTknBftList = JSON.parse(jsonList.tier_extra_benefits.replace(/&quot;/g, '"')).stdBenefits
        let custTknBftList = JSON.parse(jsonList.tier_extra_benefits.replace(/&quot;/g, '"')).custBenefits
        let tknBnfts = []

        if(stdTknBftList.length > 3) {
            stdTknBftList.splice(3)
        }


        if(custTknBftList.length > 2) {
            custTknBftList.splice(2)
        }

        tknBnfts = [...stdTknBftList, ...custTknBftList]

        console.log(tknBnfts)

        return tknBnfts
    }

    const renderGoldSection = () => {
        return (
            <figcaption 
                className="px-4 py-1" 
                style={{ background: 'linear-gradient(87.65deg, rgb(234, 209, 168) -1.34%, rgba(234, 209, 168, 0.2) 73.33%, rgb(234, 209, 168) 104.02%)' }}
            >
                <div className="d-flex justify-content-between">
                    <h6>GOLD</h6>
                    <h6>SINGLE</h6>
                </div>
            </figcaption>
        )
    }

    const renderPlatinumSection = () => {
        return (
            <figcaption 
                className="px-4 py-1" 
                style={{ background: 'linear-gradient(87.65deg, rgb(207, 216, 226) -1.34%, rgba(207, 216, 226, 0.2) 73.33%, rgb(207, 216, 226) 104.02%)' }}
            >
                <div className="d-flex justify-content-between">
                    <h6>PLATINUM</h6>
                    <h6>SINGLE</h6>
                </div>
            </figcaption>
        )
    }

    const renderDiamondSection = () => {
        return (
            <figcaption 
                className="px-4 py-1" 
                style={{ background: 'linear-gradient(87.65deg, rgb(193, 222, 228) -1.34%, rgba(193, 222, 228, 0.2) 73.33%, rgb(193, 222, 228) 104.02%)' }}
            >
                <div className="d-flex justify-content-between">
                <h6>Diamond</h6>
                <h6>SINGLE</h6>
                </div>
            </figcaption>
        )
    }

    return (
        <>
            <div className='bg-dark'>
                <div 
                    className="flex-column mx-auto drop-detail-large"
                    style={{ paddingTop: '50px', width: '70%' }}
                >
                    <div className='d-flex justify-content-between'>   
                        <span className="asset-type" style={{ color: '#ffff' }}><FormattedMessage id='ADMIN_COLLECTION_PREVIEW.TOKENS' /></span>
                        <button 
                            className='py-2 px-4 rounded'
                            style={{ background: '#ffd600', color: '#000', borderColor: '#0000' }}
                            onClick={() => props.refreshData()}
                        >
                            <FormattedMessage id='COLLECTION_DETAIL_PAGE.REFRESH_DATA' />
                            {props.refBtnLoading && 
                                <span className='spinner-border spinner-border-sm align-middle ms-2'></span>
                            }
                        </button>
                    </div>
                    <span className="asset-description my-3" style={{ width: '70%', color: '#ffff' }}>
                        TheMusician tokens give you partial ownership of a song's streaming royalty rights, plus access to 
                        exclusive content and other benefits. Ownership percentage and extras vary across each tier. 
                        TheMusician will notify collectors when royalties are available to claim after the artist gets paid.
                    </span>
    
                    <div 
                        // className={`d-flex flex-column flex-wrap ${getClass(props.tokensData.length)}`}
                        className={`d-flex flex-column`}
                        style={{ marginTop:'30px' }}
                    >
                        {props.tokensData.map((tokenData: any, index: any) => (
                            <div 
                                key={tokenData.id} 
                                className="token-card d-flex flex-column"
                                style={{ marginTop: '40px', background: '#ffff' }}
                            >
                                <div className="shadow-xl">
                                    <div className="d-flex">
                                        <div 
                                            className="d-flex flex-column" 
                                            style={{ width: '33%', height: '340px' }}
                                        >
                                            <div 
                                                className="d-flex flex-column my-auto"
                                                style={{ height: '320px', borderRight: '0.5px solid #a19d9d' }}
                                            >
                                                <figure 
                                                    className="mt-2 mx-auto" 
                                                    style={{ width: '200px' }}
                                                >
                                                    <img 
                                                        className="w-200px h-200px" 
                                                        src={`${process.env.REACT_APP_API_TOKEN_IMAGE_URL}/${props.tokensMeta[index].tier_image}`} 
                                                        // srcSet={dummyData[index].srcSet}
                                                    />
                                                    {tokenData.tier == '0' && 
                                                        renderGoldSection()
                                                    }
                                                    {tokenData.tier == '1' && 
                                                        renderPlatinumSection()
                                                    }
                                                    {tokenData.tier == '2' && 
                                                        renderDiamondSection()
                                                    }
                                                </figure>

                                                <div className="d-flex flex-column mx-auto">
                                                    <div className="d-flex ms-3">
                                                        <span className="fs-5 text-primary fw-bolder">{parseInt(tokenData.ownership_offered) / 10**5} %</span>
                                                        <span className='mx-2 fw-bolder'> | </span>
                                                        <span className="fs-5 text-primary fw-bolder"><FormattedMessage id='ADMIN_COLLECTION_PREVIEW.OWNERSHIP_PER_TOKEN' /></span>
                                                    </div> 
                                                    <div className="d-flex ms-3">
                                                        <span className="fs-5 text-primary fw-bolder">
                                                            {tokenData.price / (10**18)} KLAY
                                                        </span>
                                                        <span className='mx-2 fw-bolder'>|</span>
                                                        <span className="fs-5 text-primary fw-bolder">
                                                            {/* {Math.ceil((tokenData.price / (10**18)) * parseFloat(props.fiatPrice))} USD */}
                                                            {Number(((tokenData.price / (10**18)) * parseFloat(props.fiatPrice))).toFixed(2)} USD
                                                        </span>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>

                                        <div className="d-flex my-auto" style={{ width: '33%', height: '340px' }}>
                                            <div 
                                                className="w-100 d-flex flex-column my-auto"
                                                style={{ 
                                                    height: '320px', 
                                                    borderRight: '0.5px solid #a19d9d' 
                                                }}
                                            >
                                                {/* <span className="w-100 text-center fw-bolder text-primary">Collector Benefits</span>
                                                    <hr className="w-20 text-dark mx-auto" /> */}
                                                <div 
                                                    className="d-flex flex-column my-auto mx-auto"
                                                    style={{ 
                                                        overflow: 'auto', 
                                                        width: '80%', 
                                                    }}
                                                >
                                                    <span className="w-100 fs-5 text-center fw-bolder text-primary">Collector Benefits</span>
                                                    <hr className="w-20 text-dark mx-auto" />
                                                    {/* {JSON.parse(props.tokensMeta[index].tier_extra_benefits.replace(/&quot;/g, '"')).stdBenefits.map((content: any) => (
                                                        <div className="d-flex my-2">
                                                            <span style={{ marginTop: '2px' }} className="fs-6 me-2">{content.symbol}</span>
                                                            <span className="asset-description">{content}</span>
                                                        </div>
                                                    ))}  
                                                    {JSON.parse(props.tokensMeta[index].tier_extra_benefits.replace(/&quot;/g, '"')).custBenefits.map((content: any) => (
                                                        <div className="d-flex my-2">
                                                            <span style={{ marginTop: '2px' }} className="fs-6 me-2">{content.symbol}</span>
                                                            <span className="asset-description">{content}</span>
                                                        </div>
                                                    ))} */}

                                                    {optimizeTokenBenefitsList(props.tokensMeta[index]).map((content: any) => (
                                                        <div className="d-flex my-2">
                                                            {/* <span style={{ marginTop: '2px' }} className="fs-6 me-2">{content.symbol}</span> */}
                                                            <span className="asset-description">{content}</span>
                                                        </div>
                                                    ))}
                                                </div>
                                            </div>
                                        </div>

                                        <div className="d-flex flex-column my-auto" style={{ width: '33%' }}>
                                            <div className="mx-auto my-4">
                                                <a 
                                                    className="text-decoration-none fs-5 fw-bolder" 
                                                    href="#"
                                                    onClick={() => props.handleReadMoreClick(
                                                            props.tokensMeta[index].tier_image, 
                                                            tokenData.tier, 
                                                            'klaytn', 
                                                            [
                                                                ...JSON.parse(props.tokensMeta[index].tier_extra_benefits.replace(/&quot;/g, '"')).stdBenefits,
                                                                ...JSON.parse(props.tokensMeta[index].tier_extra_benefits.replace(/&quot;/g, '"')).custBenefits
                                                            ]
                                                        )
                                                    }
                                                >
                                                    <FormattedMessage id='ADMIN_COLLECTION_PREVIEW.SUMMARY' />
                                                </a>
                                            </div>
                                            <div className="text-center">
                                                {timeDifference <= 0 && 
                                                    <>
                                                        {parseInt(tokenData.currentSupply) > 0 && 
                                                            <button 
                                                                className={`black-button py-2 ${buttonStatus[index] ? 'token-btn-disabled' : ''}`}
                                                                onClick={() => showDialogBox(
                                                                    tokenData.tier,
                                                                    props.collectionName,
                                                                    props.collectionID,
                                                                    Number((tokenData.price / (10**18)) * parseFloat(props.fiatPrice)).toFixed(2),
                                                                    (tokenData.price / (10**18)),
                                                                    props.tokensMeta[index].tier_image
                                                                )}
                                                                disabled={buttonStatus[index]}
                                                            >
                                                                {btnNames[index]}
                                                            </button>
                                                        }
                                                        {parseInt(tokenData.currentSupply) <= 0 && 
                                                            <button 
                                                                className='mx-auto mb-4 black-button py-2'
                                                                onClick={() => {window.location.href = 'https://testnets.opensea.io/collection/themusician-musicasset'}}
                                                            >
                                                                <FormattedMessage id='COLLECTION_DETAIL_PAGE.BUY_ON_OPENSEA' />
                                                            </button>
                                                        }
                                                    </>
                                                }
                                                <div className="mt-4">
                                                    <span className="text-center py-3">{tokenData.currentSupply} / {tokenData.totalSupply} <FormattedMessage id='COLLECTION_DETAIL_PAGE.TOKENS_LEFT' /></span>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        ))}

                        {renderSnackBar()}
                
                    </div>
                    <hr style={{ marginTop: '50px' }}/> 
                </div>
                {props.userID && 
                    <PaymentMethodDialog
                        dialogOpen={props.dialogOpen} 
                        setDialogOpen={props.setDialogOpen}
                        // buyToken={buyToken}
                        tokenTier={props.selectedTier}
                        tokenPriceFiat={props.price}
                        tokenPriceCrypto={props.priceCrypto}
                        collectionName={props.selectedColName}
                        imageName={props.selectedImageName}
                        collectionID={props.selectedColID}
                        blockchain={props.blockchain}
                        artistName={props.artistName}
                    />
                }
            </div>
        </>
    )
}