import { AlertDanger, AlertSuccess } from "app/modules/alerts/Alerts"
import { useState } from "react"
import { FormattedMessage, useIntl } from "react-intl"
import { submitRequest } from '../../accounts/components/settings/_redux/accountCrud'

export const ContactSection = () => {
    const intl = useIntl()
    const [checked, setChecked] = useState<boolean>(false)
    const [submitting, setSubmitting] = useState<boolean>(false)
    const [resSuccessMessage, setResSuccessMessage] = useState("");
    const [resFailureMessage, setResFailureMessage] = useState("");

    const [name, setName] = useState<string>('')
    const [email, setEmail] = useState<string>('')
    const [subject, setSubject] = useState<string>('')
    const [description, setDescription] = useState<string>('')

    if(resSuccessMessage !== "") {
        setTimeout(() => {
            setChecked(false);
            setTimeout(() => {
                setResSuccessMessage("");
            }, 300);
        }, 5000);
    }
    
      if(resFailureMessage !== "") {
        setTimeout(() => {
            setChecked(false);
            setTimeout(() => {
                setResFailureMessage("");
            }, 300);
        }, 5000);
    }

    const handleNameChange = (event: any) => {
        event.preventDefault()

        setName(event.target.value)
    }

    const handleEmailChange = (event: any) => {
        event.preventDefault()

        setEmail(event.target.value)
    }

    const handleSubjectChange = (event: any) => {
        event.preventDefault()

        setSubject(event.target.value)
    }

    const handleDescriptionChange = (event: any) => {
        event.preventDefault()

        setDescription(event.target.value)
    }

    const isValidFormData = () => {
        if(email == "" || subject == "" || description == "") return false
        return true
    }

    const handleSubmit = () => {
        setSubmitting(true)

        if(isValidFormData()) {
            submitRequest(
                email,
                subject,
                description
            )
              .then((response) => {
                if(response.data.success) {
                    setChecked(true)
                    setResSuccessMessage(response.data.message)
                    setSubmitting(false)
                } else {
                    if(response.data.message) {
                        setResFailureMessage(response.data.message);
                        setSubmitting(false);
                        setChecked(true);
                    } else {
                        setResFailureMessage("Request Submission failed");
                        setSubmitting(false);
                        setChecked(true);
                    }
                }
              })
        } else {
            setResFailureMessage("Please fill out the required field");
            setSubmitting(false);
            setChecked(true);
        }
    }

    return (
        <>
            <div id="contact" style={{ background: '#000' }} className="vc_row wpb_row vc_row-fluid">
                <div className="wpb_column vc_column_container vc_col-sm-12">
                    <div className="vc_column-inner">
                        <div className="wpb_wrapper">
                            <div className="vc_row wpb_row vc_inner vc_row-fluid">
                                <div className="wpb_column vc_column_container vc_col-sm-12">
                                    <div className="vc_column-inner">
                                        <div className="wpb_wrapper">
                                            <h2 style={{ textAlign: 'center', color: '#ffff' }} className="vc_custom_heading vc_custom_1505847685561 fit_bg_text  fit_bg_center ">
                                                <FormattedMessage id="HOME_PAGE.CONTACT_US" />
                                            </h2>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div 
                                className="vc_row wpb_row vc_inner vc_row-fluid" 
                                style={{ display: 'flex', flexDirection: 'column' }}
                            >
                                <div className="text-center w-100 wpb_column vc_column_container vc_col-sm-6">
                                    <span className="text-white fs-2">
                                        Are you an artist? Do you want to release and share your own NFT to the fans worldwide? Then please fill in the blank and send to us.
                                    </span>
                                </div>
                                
                                <div 
                                    className="w-100 light-form wpb_column vc_column_container vc_col-sm-6"
                                    style={{ marginTop: '50px' }}
                                >
                                    {resSuccessMessage &&resSuccessMessage != "" && 
                                        <AlertSuccess message={resSuccessMessage} checked={checked} />
                                    }

                                    {resFailureMessage && resFailureMessage != "" && 
                                        <AlertDanger message={resFailureMessage} checked={checked} />
                                    }
                                    <div className="vc_column-inner">
                                        <div className="wpb_wrapper" style={{ width: '100%' }}>
                                            <div className="wpcf7-form init">
                                                {/* <p>
                                                    <label style={{ width: '100%' }}> <FormattedMessage id="HOME_PAGE.YOUR_NAME" /><br />
                                                        <span className="wpcf7-form-control-wrap your-name">
                                                            <input 
                                                                type="text" 
                                                                name="your-name" 
                                                                value={name}
                                                                onChange={(event) => handleNameChange(event)}
                                                                size={40} 
                                                                className="wpcf7-form-control wpcf7-text wpcf7-validates-as-required" 
                                                            />
                                                        </span>
                                                    </label>
                                                </p> */}
                                                <p>
                                                    <label style={{ width: '100%' }}> <FormattedMessage id="HOME_PAGE.YOUR_EMAIL" /><br />
                                                        <span className="wpcf7-form-control-wrap your-email">
                                                            <input 
                                                                type="email" 
                                                                name="your-email" 
                                                                value={email}
                                                                onChange={(event) => handleEmailChange(event)}
                                                                size={40} 
                                                                className="wpcf7-form-control wpcf7-text wpcf7-email wpcf7-validates-as-required wpcf7-validates-as-email" 
                                                            />
                                                        </span> 
                                                    </label>
                                                </p>
                                                <p>
                                                    <label style={{ width: '100%' }}> <FormattedMessage id="HOME_PAGE.SUBJECT" /><br />
                                                        <span className="wpcf7-form-control-wrap your-subject">
                                                            <input 
                                                                type="text" 
                                                                name="your-subject" 
                                                                value={subject}
                                                                onChange={(event) => handleSubjectChange(event)}
                                                                size={40} 
                                                                className="wpcf7-form-control wpcf7-text"
                                                            />
                                                        </span>
                                                    </label>
                                                </p>
                                                <p>
                                                    <label style={{ width: '100%' }}> <FormattedMessage id="HOME_PAGE.YOUR_MESSAGE" /><br />
                                                        <span className="wpcf7-form-control-wrap your-message">
                                                            <textarea 
                                                                name="your-message" 
                                                                cols={40} 
                                                                rows={10} 
                                                                value={description}
                                                                onChange={(event) => handleDescriptionChange(event)}
                                                                className="wpcf7-form-control wpcf7-textarea" 
                                                            />
                                                        </span>
                                                    </label>
                                                </p>
                                                <p className="d-flex justify-content-center">
                                                    <button 
                                                        style={{
                                                            borderColor: '#ffd600',
                                                            color: '#000',
                                                            backgroundColor: '#ffd600'
                                                        }}
                                                        className="wpcf7-form-control rounded wpcf7-submit" 
                                                        onClick={handleSubmit}
                                                    >
                                                        {submitting ? intl.formatMessage({id: "CREATE_EDIT_COLLECTION.SUBMITTING"}) : intl.formatMessage({id: "HOME_PAGE.SUBMIT"})}
                                                    </button>
                                                </p>
                                            </div>
                                            <div className="vc_empty_space" style={{ height: '200px' }}>
                                                <span className="vc_empty_space_inner"></span>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}