import axios from "axios";

const GET_USERS_DATA_URL = `${process.env.REACT_APP_API_URL}/get-users-detail-for-admin.php`;
const SEARCH_USERS_DATA_URL = `${process.env.REACT_APP_API_URL}/search-user.php`;
const DELETE_USERS_URL = `${process.env.REACT_APP_API_URL}/delete-users-for-admin.php`;
const CREATE_USERS_URL = `${process.env.REACT_APP_API_URL}/create-user-for-admin.php`;
const UPDATE_USER_FOR_ADMIN_URL = `${process.env.REACT_APP_API_URL}/update-user-for-admin.php`;
const UPDATE_PASSWORD_URL = `${process.env.REACT_APP_API_URL}/update-password-for-admin.php`;
const GET_USER_DETAIL = `${process.env.REACT_APP_API_URL}/get-user-detail-for-admin.php`


export function getUsersData(offset: number, limit: number) {
    return axios({
        method: 'post',
        url: GET_USERS_DATA_URL,
        headers: {
            'content-type': 'application/x-www-form-urlencoded',
        },
        data: {
            offset,
            limit
        }
    })
}

export function getUserDetail(userID: any) {
    return axios({
        method: 'post',
        url: GET_USER_DETAIL,
        headers: {
            'content-type': 'application/x-www-form-urlencoded',
        },
        data: {
            userID
        }
    })
}

export function searchUserData(searchString: string, pageNum: number, limit: number) {
    return axios({
        method: 'post',
        url: SEARCH_USERS_DATA_URL,
        headers: {
            'content-type': 'application/x-www-form-urlencoded',
        },
        data: {
            searchString,
            pageNum,
            limit
        }
    })
}

export function deleteUsers(userList: Array<string>, adminID: string, adminPassword: string){
    return axios({
        method: 'post',
        url: DELETE_USERS_URL,
        headers: {
            'content-type': 'application/x-www-form-urlencoded',
        },
        data: {
            userList: JSON.stringify(userList),
            adminID,
            adminPassword
        }
    })
}

export function createUsers(
    fullName: string, 
    userName: string, 
    email: string, 
    password: string, 
    isAdmin: boolean,
    adminID: string, 
    adminPassword: string
){
    return axios({
        method: 'post',
        url: CREATE_USERS_URL,
        headers: {
            'content-type': 'application/x-www-form-urlencoded',
        },
        data: {
            fullName,
            userName,
            email,
            password,
            isAdmin: isAdmin ? '1' : '0',
            adminID,
            adminPassword
        }
    })
}

export const updateUserForAdmin = (formData: any) => {
    return axios({
        method: 'post',
        url: UPDATE_USER_FOR_ADMIN_URL,
        headers: {
            'content-type': 'multipart/form-data',
        },
        data: formData
    })
}

export const updatePasswordForAdmin = (
    adminID: string,
    adminPassword: string,
    userID: string,
    newPassword: string
) => {
    return axios({
        method: 'post',
        url: UPDATE_PASSWORD_URL,
        headers: {
            'content-type': 'application/x-www-form-urlencoded',
        },
        data: {
            adminID,
            adminPassword,
            userID,
            newPassword
        }
    })
}