import { FormattedMessage } from "react-intl"

const DeleteConfirmationBox = (props: any) => {
    return (
        <div id={`delete-users`} style={{ display: 'none' }} className="modal">
            <span onClick={() => props.closeDialog(`delete-users`)} className="close" title="Close Modal">&times;</span>
            <form className="modal-content">
                <div className="px-7 py-7">
                    <h3 className='text-center'><FormattedMessage id="ADMIN.USER.DELETE_DIALOG.DELETE_USERS" /></h3>
                    {/* <p className='font-size-15 text-center'>Are you sure you want to delete the selected users permanently?</p> */}

                    <p className='font-size-15 text-center'><FormattedMessage id="ADMIN.USER.DELETE_DIALOG.PHRASE" /></p>

                    <div className="d-flex justify-content-center">
                        <button type="button" className="btn btn-primary"><FormattedMessage id="COLLECTION_DELETE_BOX.CANCEL" /></button>
                        <button 
                            onClick={() => props.passwordConfirmPopup()} 
                            type="button" 
                            className="btn btn-danger ms-3"
                        >
                            <FormattedMessage id="COLLECTION_DELETE_BOX.DELETE" />

                            {props.deleting && 
                                <span className='spinner-border spinner-border-sm align-middle ms-2'></span>
                            }
                        </button>
                    </div>
                </div>
            </form>
        </div>
    )
}

export {DeleteConfirmationBox}