import React, {useState, useEffect} from "react";
import { useSelector, shallowEqual, connect, useDispatch } from "react-redux";
import { Route, Navigate } from "react-router-dom";
import ProductsSideBar from "./components/CanvasSideBar";
import { CollectionCard } from "./components/CollectionCard";
import SortSelect from "./components/SortSelect";
import Drawer from "./components/MobCnvsSideBar";
import { actions } from "../../auth/redux/AuthRedux";
import {RootState} from '../../../../setup'
import {IProductModel} from './models/ProductModel'
import {toAbsoluteUrl} from '../../../../_metronic/helpers'
import CircularProgress from "@mui/material/CircularProgress";


function ExplorePageSmall(props: any) {

    const getTotalNumberOfTokens = (tiersArray: Array<any>) => {
        let tierQuantity = 0

        tiersArray.map((tier) => {
            tierQuantity += parseInt(tier.tier_quantity)
        })
        return tierQuantity
    }


    return (
        <>
        <div className="content page-small flex-column flex-column-fluid" id="kt_content">
            <div className="d-flex mb-5">
                <Drawer 
                    artistList={props.artistList}
                    blockchainFilterVal={props.blockchainFilterVal}
                    setBlockchainFilterVal={props.setBlockchainFilterVal}
                    artistFilterVal={props.artistFilterVal}
                    setArtistFilterVal={props.setArtistFilterVal}
                    blockchainFilterAdded={props.blockchainFilterAdded}
                    setBlockchainFilterAdded={props.setBlockchainFilterAdded}
                    artistFilterAdded={props.artistFilterAdded}
                    setArtistFilterAdded={props.setArtistFilterAdded}
                />
            </div>
            {/* <!--begin::Entry--> */}
            <div className="d-flex flex-column-fluid">
                <ProductsSideBar 
                    artistList={props.artistList}
                    blockchainFilterVal={props.blockchainFilterVal}
                    setBlockchainFilterVal={props.setBlockchainFilterVal}
                    artistFilterVal={props.artistFilterVal}
                    setArtistFilterVal={props.setArtistFilterVal}
                    blockchainFilterAdded={props.blockchainFilterAdded}
                    setBlockchainFilterAdded={props.setBlockchainFilterAdded}
                    artistFilterAdded={props.artistFilterAdded}
                    setArtistFilterAdded={props.setArtistFilterAdded}
                />
                {/* <!--begin::Container--> */}
                <div id="main" className="p-container m-top">
                    {/* <!--begin::Page Layout--> */}
                    <div className="d-flex flex-row">
                        {/* <!--begin::Layout--> */}
                        <div className="flex-row-fluid ml-lg-6">
                            <div className="product-contain">
                                {props.collectionsMeta.map((collection: any) => (
                                    <>
                                        {collection.is_minted == '1' && 
                                            <>
                                                <CollectionCard 
                                                    key={collection.id}
                                                    id={collection.id}
                                                    imagePath={`${process.env.REACT_APP_API_COLLECTION_IMAGE_URL}/${collection.collection_image}`}
                                                    collectionName={collection.collection_name}
                                                    collectionType={collection.collection_type}
                                                    blockchain={collection.blockchain}
                                                    noOfTiers={collection.tiersData.length}
                                                    artistName={collection.artistName}
                                                    artistImagePath={`${process.env.REACT_APP_API_ARTIST_IMAGE_URL}/${collection.artistImage}`}
                                                    totalNumOfTokens={getTotalNumberOfTokens(collection.tiersData)}
                                                />
                                            </>
                                        }
                                    </>
                                ))}
                                {/* {!props.loading && 
                                    <>
                                        {props.collectionsMeta.map((collection: any) => (
                                            <>
                                                {collection.is_minted == '1' && 
                                                    <>
                                                        <CollectionCard 
                                                            key={collection.id}
                                                            id={collection.id}
                                                            imagePath={`${process.env.REACT_APP_API_COLLECTION_IMAGE_URL}/${collection.collection_image}`}
                                                            collectionName={collection.collection_name}
                                                            collectionType={collection.collection_type}
                                                            blockchain={collection.blockchain}
                                                            noOfTiers={collection.tiersData.length}
                                                            artistName={collection.artistName}
                                                            artistImagePath={`${process.env.REACT_APP_API_ARTIST_IMAGE_URL}/${collection.artistImage}`}
                                                            totalNumOfTokens={getTotalNumberOfTokens(collection.tiersData)}
                                                        />
                                                    </>
                                                }
                                            </>
                                        ))}
                                    </>
                                } */}

                                {/* {props.loading && 
                                
                                    <div className='d-flex w-100 h-100'>
                                        <div className='my-auto mx-auto'>
                                            <div className='d-flex flex-column justify-content-center'>
                                                <span className='text-primary fs-3'>Loading</span> <br />
                                                <span className='ms-4'>
                                                    <CircularProgress />
                                                </span>
                                            </div>
                                        </div>
                                    </div>
                                } */}
                            </div>
                        </div>
                        {/* <!--end::Layout--> */}
                    </div>
                    {/* <!--end::Page Layout--> */}
                </div>
                {/* <!--end::Container--> */}
            </div>
            {/* <!--end::Entry--> */}
        </div>
        </>
    )
}

export {ExplorePageSmall};