/* eslint-disable jsx-a11y/anchor-is-valid */
import React, {useState} from 'react'
import {useDispatch} from 'react-redux'
import {useFormik} from 'formik'
import * as Yup from 'yup'
import clsx from 'clsx'
import * as auth from '../redux/AuthRedux'
import {register} from '../redux/AuthCRUD'
import {Link} from 'react-router-dom'
import {toAbsoluteUrl} from '../../../../_metronic/helpers'
import {AlertDanger} from '../../alerts/Alerts'
import { FormattedMessage, useIntl } from 'react-intl'

const initialValues = {
  fullname: '',
  email: '',
  username: '',
  password: '',
  changepassword: '',
  acceptTerms: false,
}

const API_KEY = `${process.env.REACT_APP_EXCHANGE_RATES_API_KEY}`;

export function Registration() {
  const [loading, setLoading] = useState(false)
  const [checked, setChecked] = useState(true);
  const [errorMessage, setErrorMessage] = useState("");
  const intl = useIntl()

  const registrationSchema = Yup.object().shape({
    fullname: Yup.string()
      .min(3, intl.formatMessage({id: 'PROFILE_DETAIL_SCHEMA.EMAIL.MIN'}))
      .max(50, intl.formatMessage({id: 'PROFILE_DETAIL_SCHEMA.EMAIL.MAX'}))
      .required(intl.formatMessage({id: 'AUTH.REGISTER.SCHEMA.FULLNAME.REQUIRED'})),
    email: Yup.string()
      .email(intl.formatMessage({id: 'PROFILE_DETAIL_SCHEMA.EMAIL.WRONG_FORMAT'}))
      .min(3, intl.formatMessage({id: 'PROFILE_DETAIL_SCHEMA.EMAIL.MIN'}))
      .max(50, intl.formatMessage({id: 'PROFILE_DETAIL_SCHEMA.EMAIL.MAX'}))
      .required(intl.formatMessage({id: 'PROFILE_DETAIL_SCHEMA.EMAIL.REQUIRED'})),
    username: Yup.string()
      .min(3, intl.formatMessage({id: 'PROFILE_DETAIL_SCHEMA.EMAIL.MIN'}))
      .max(50, intl.formatMessage({id: 'PROFILE_DETAIL_SCHEMA.EMAIL.MAX'}))
      .required(intl.formatMessage({id: 'AUTH.REGISTER.SCHEMA.USERNAME.REQUIRED'})),
    password: Yup.string()
      .min(3, intl.formatMessage({id: 'PROFILE_DETAIL_SCHEMA.EMAIL.MIN'}))
      .max(50, intl.formatMessage({id: 'PROFILE_DETAIL_SCHEMA.EMAIL.MAX'}))
      .required(intl.formatMessage({id: 'AUTH.REGISTER.SCHEMA.PASSWORD.REQUIRED'})),
    changepassword: Yup.string()
      .required(intl.formatMessage({ id: "AUTH.REGISTER.SCHEMA.CPASSWORD.REQUIRED" }))
      .when('password', {
        is: (val: string) => (val && val.length > 0 ? true : false),
        then: Yup.string().oneOf([Yup.ref('password')], intl.formatMessage({id: 'AUTH.REGISTER.SCHEMA.CPASS.MISMATCH'})),
      }),
    acceptTerms: Yup.bool().required(intl.formatMessage({id: 'AUTH.REGISTER.SCHEMA.ACCEPT_TERM'})),
  })

  if(errorMessage !== "") {
    setTimeout(() => {
      setChecked(false);
      setTimeout(() => {
        setErrorMessage("");
      }, 1000);
    }, 5000);
  }

  const dispatch = useDispatch()
  const formik = useFormik({
    initialValues,
    validationSchema: registrationSchema,
    onSubmit: (values, {setStatus, setSubmitting}) => {
      setLoading(true)
      setTimeout(() => {
        register(values.email, values.fullname, values.username, values.password)
          .then((response) => {
            const message = response.data.message;
            if(response.data.created) {
              const authToken = response.data.token;
              const user = {
                userID: response.data.user.id,
                userImage: response.data.user.avatar,
                fullname: response.data.user.full_name,
                username: response.data.user.username,
                email: response.data.user.email,
                description: response.data.user.description,
                isVerified: response.data.is_verified,
                isAdmin: response.data.is_admin
              }
              const userImages = {
                coverImage: response.data.user.cover_image,
                userImage: response.data.user.avatar
              }
              const userWalletList = {
                ethCustodialWallet: response.data.user.custWalletEth,
                klayCustodialWallet: response.data.user.custWalletKlay,
                metamaskWallet: response.data.user.metamask_wallet,
                kaikasWallet: response.data.user.kaikas_wallet
              }
              dispatch(auth.actions.login(authToken, user, userImages, userWalletList))
              // dispatch(auth.actions.setUserImages(userImages))
            } else {
              if(message) {
                setChecked(true)
                setErrorMessage(message);
              } else {
                setChecked(true);
                setErrorMessage("Something went wrong");
              }
            }
            setLoading(false);
            setSubmitting(false);
          })
          .catch(() => {
            setLoading(false)
            setSubmitting(false)
            setStatus('Registration process has broken')
          })
      }, 1000)
    },
  })

  return (
    <form
      className='form w-100 fv-plugins-bootstrap5 fv-plugins-framework'
      noValidate
      id='kt_login_signup_form'
      onSubmit={formik.handleSubmit}
    >
      {/* begin::Heading */}
      <div className='mb-10 text-center'>
        {/* begin::Title */}
        <h1 className='text-dark mb-3'><FormattedMessage id='AUTH.SIGNIN.CREATE_ACCOUNT' /></h1>
        {/* end::Title */}

        {/* begin::Link */}
        <div className='text-gray-400 fw-bold fs-4'>
          <FormattedMessage id='AUTH.REGISTER.ALREADY_HAVE_ACCOUNT' />
          <Link to='/auth/login' className='link-primary fw-bolder' style={{marginLeft: '5px'}}>
            <FormattedMessage id='AUTH.SIGNIN.FORGOT_PASSWORD' />
          </Link>
        </div>
        {/* end::Link */}
      </div>
      {/* end::Heading */}

      {formik.status && (
        <div className='mb-lg-15 alert alert-danger'>
          <div className='alert-text font-weight-bold'>{formik.status}</div>
        </div>
      )}

      {errorMessage !== "" ? (
          <AlertDanger message={errorMessage} checked={checked} />
        ) : null} 

     {/* begin::Form group Fullname */}
     <div className='fv-row mb-7'>
        <label className='form-label fw-bolder text-dark fs-6'>
          <FormattedMessage id='AUTH.REGISTER.FULLNAME' />
        </label>
        <input
          placeholder={intl.formatMessage({ id: 'AUTH.REGISTER.FULLNAME' })}
          type='text'
          autoComplete='off'
          {...formik.getFieldProps('fullname')}
          className={clsx(
            'form-control form-control-lg form-control-solid',
            {'is-invalid': formik.touched.fullname && formik.errors.fullname},
            {
              'is-valid': formik.touched.fullname && !formik.errors.fullname,
            }
          )}
        />
        {formik.touched.fullname && formik.errors.fullname && (
          <div className='fv-plugins-message-container'>
            <div className='fv-help-block'>
              <span role='alert'>{formik.errors.fullname}</span>
            </div>
          </div>
        )}
      </div>
     {/* end::Form group */}

      {/* begin::Form group Email */}
      <div className='fv-row mb-7'>
        <label className='form-label fw-bolder text-dark fs-6'>
          <FormattedMessage id='AUTH.REGISTER.EMAIL' />
        </label>
        <input
          placeholder={intl.formatMessage({ id: 'AUTH.REGISTER.EMAIL' })}
          type='email'
          autoComplete='off'
          {...formik.getFieldProps('email')}
          className={clsx(
            'form-control form-control-lg form-control-solid',
            {'is-invalid': formik.touched.email && formik.errors.email},
            {
              'is-valid': formik.touched.email && !formik.errors.email,
            }
          )}
        />
        {formik.touched.email && formik.errors.email && (
          <div className='fv-plugins-message-container'>
            <div className='fv-help-block'>
              <span role='alert'>{formik.errors.email}</span>
            </div>
          </div>
        )}
      </div>
      {/* end::Form group */}

      {/* begin::Form group Username */}
     <div className='fv-row mb-7'>
        <label className='form-label fw-bolder text-dark fs-6'>
          <FormattedMessage id='AUTH.REGISTER.USERNAME' />
        </label>
        <input
          placeholder={intl.formatMessage({ id: 'AUTH.REGISTER.USERNAME' })}
          type='text'
          autoComplete='off'
          {...formik.getFieldProps('username')}
          className={clsx(
            'form-control form-control-lg form-control-solid',
            {'is-invalid': formik.touched.username && formik.errors.username},
            {
              'is-valid': formik.touched.username && !formik.errors.username,
            }
          )}
        />
        {formik.touched.username && formik.errors.username && (
          <div className='fv-plugins-message-container'>
            <div className='fv-help-block'>
              <span role='alert'>{formik.errors.username}</span>
            </div>
          </div>
        )}
      </div>
     {/* end::Form group */}

      {/* begin::Form group Password */}
      <div className='mb-10 fv-row' data-kt-password-meter='true'>
        <div className='mb-1'>
          <label className='form-label fw-bolder text-dark fs-6'>
            <FormattedMessage id='AUTH.REGISTER.PASSWORD' />
          </label>
          <div className='position-relative mb-3'>
            <input
              type='password'
              placeholder={intl.formatMessage({ id: 'AUTH.REGISTER.PASSWORD' })}
              autoComplete='off'
              {...formik.getFieldProps('password')}
              className={clsx(
                'form-control form-control-lg form-control-solid',
                {
                  'is-invalid': formik.touched.password && formik.errors.password,
                },
                {
                  'is-valid': formik.touched.password && !formik.errors.password,
                }
              )}
            />
            {formik.touched.password && formik.errors.password && (
              <div className='fv-plugins-message-container'>
                <div className='fv-help-block'>
                  <span role='alert'>{formik.errors.password}</span>
                </div>
              </div>
            )}
          </div>
        </div>
      </div>
      {/* end::Form group */}

      {/* begin::Form group Confirm password */}
      <div className='fv-row mb-5'>
        <label className='form-label fw-bolder text-dark fs-6'>
          <FormattedMessage id='AUTH.REGISTER.CONFIRM_PASSWORD' />
        </label>
        <input
          type='password'
          placeholder={intl.formatMessage({ id: 'AUTH.REGISTER.PASSWORD_CONFIRMATION' })}
          autoComplete='off'
          {...formik.getFieldProps('changepassword')}
          className={clsx(
            'form-control form-control-lg form-control-solid',
            {
              'is-invalid': formik.touched.changepassword && formik.errors.changepassword,
            },
            {
              'is-valid': formik.touched.changepassword && !formik.errors.changepassword,
            }
          )}
        />
        {formik.touched.changepassword && formik.errors.changepassword && (
          <div className='fv-plugins-message-container'>
            <div className='fv-help-block'>
              <span role='alert'>{formik.errors.changepassword}</span>
            </div>
          </div>
        )}
      </div>
      {/* end::Form group */}

      {/* begin::Form group */}
      <div className='fv-row mb-10'>
        <div className='form-check form-check-custom form-check-solid'>
          <input
            className='form-check-input'
            type='checkbox'
            id='kt_login_toc_agree'
            {...formik.getFieldProps('acceptTerms')}
          />
          <label
            className='form-check-label fw-bold text-gray-700 fs-6'
            htmlFor='kt_login_toc_agree'
          >
            I Agree the{' '}
            <Link to='/terms-and-conditions' className='ms-1 link-primary'>
              terms and conditions
            </Link>
            .
          </label>
          {formik.touched.acceptTerms && formik.errors.acceptTerms && (
            <div className='fv-plugins-message-container'>
              <div className='fv-help-block'>
                <span role='alert'>{formik.errors.acceptTerms}</span>
              </div>
            </div>
          )}
        </div>
      </div>
      {/* end::Form group */}

      {/* begin::Form group */}
      <div className='text-center'>
        <button
          type='submit'
          id='kt_sign_up_submit'
          className='btn btn-lg custom-btn w-100 mb-5'
          disabled={formik.isSubmitting || !formik.isValid || !formik.values.acceptTerms}
        >
          {!loading && <span className='indicator-label'><FormattedMessage id='CREATE_EDIT_COLLECTION.SUBMIT' /></span>}
          {loading && (
            <span className='indicator-progress' style={{display: 'block'}}>
              <FormattedMessage id='SETTINGS_PAGE.PLEASE_WAIT' />...{' '}
              <span className='spinner-border spinner-border-sm align-middle ms-2'></span>
            </span>
          )}
        </button>
        <Link to='/auth/login'>
          <button
            type='button'
            id='kt_login_signup_form_cancel_button'
            className='btn btn-lg btn-light-primary w-100 mb-5'
          >
            <FormattedMessage id='AUTH.REGISTER.CANCEL' />
          </button>
        </Link>
      </div>
      {/* end::Form group */}
    </form>
  )
}
