import React, { useState } from 'react'
import { useSelector, shallowEqual } from 'react-redux'
import { Alert } from 'react-bootstrap';
import {Footer} from './components/Footer'
import {Outlet} from 'react-router-dom'
import {HeaderWrapper} from './components/header/HeaderWrapper'
import {ScrollTop} from './components/ScrollTop'
import {Content} from './components/Content'
import {PageDataProvider} from '../../_metronic/layout/core'
import { RootState } from 'setup'
import { resendVerificationEmail } from '../modules/auth/redux/AuthCRUD'
import {AlertDanger, AlertSuccess} from '../modules/alerts/Alerts'
import { ReadMore } from '../modules/dropDetail/dropDetailEthereum/components/Readmore'

const MasterLayout: React.FC = ({children}) => {

  const { isVerified, email, authToken }: any = useSelector<RootState>(
    ({auth}) => ({
      isVerified: auth.user?.isVerified,
      email: auth.user?.email,
      authToken: auth.accessToken
    })
  )
  const [sendingEmail, setSendingStatus] = useState<boolean>(false)
  const [checked, setChecked] = useState<boolean>(false)
  const [successMessage, setSuccessMessage] = useState<string>("")
  const [errorMessage, setErrorMessage] = useState<string>("")

  if(successMessage !== "") {
    setTimeout(() => {
      setChecked(false);
      setTimeout(() => {
        setSuccessMessage("");
      }, 200);
    }, 5000);
  }

  if(errorMessage !== "") {
    setTimeout(() => {
      setChecked(false);
      setTimeout(() => {
        setErrorMessage("");
      }, 200);
    }, 5000);
  }

  const sendVerificationEamil = () => {
    setSendingStatus(true)
    resendVerificationEmail(email, authToken)
    .then((response) => {
      if(response.data.success) {
        setSendingStatus(false)
        setChecked(true)
        setSuccessMessage(response.data.message)
      } else {
        if(response.data.message) {
          setSendingStatus(false)
          setChecked(true)
          setErrorMessage(response.data.message)
        } else {
          setSendingStatus(false)
          setChecked(true)
          setErrorMessage('Failed to send verification email, please try again later')
        }
      }
    })
  }

  return (
    <>
      <PageDataProvider>
        <div className='d-flex flex-column flex-root'>
          <div className='page d-flex flex-row flex-column-fluid'>
            <div className='wrapper d-flex flex-column flex-row-fluid' id='kt_wrapper'>
              <HeaderWrapper />

              <div id='kt_content' className='content d-flex flex-column flex-column-fluid'>

                {successMessage !== "" ? (
                  <AlertSuccess message={successMessage} checked={checked} />
                ) : null }

                {errorMessage !== "" ? (
                  <AlertDanger message={errorMessage} checked={checked} />
                ) : null }

                {isVerified == '0' && 
                  <div className='alert alert-warning show' >
                    <div className='d-flex justify-content-center'>
                      <p className='my-auto me-3'>Verify your email address to receive email notifications. Didn't received verification email?</p>
                      <button 
                        className='btn btn-primary'
                        onClick={() => sendVerificationEamil()}
                      >
                        Resend

                        {sendingEmail && 
                            <span className='spinner-border spinner-border-sm align-middle ms-3'></span>
                        }
                      </button>
                    </div>
                  </div>
                }

                <div className='post d-flex flex-column-fluid' id='kt_post'>
                  <Content>
                    <Outlet />
                  </Content>
                </div>
              </div>
              <Footer />
            </div>
          </div>
        </div>
        <ScrollTop />
        <ReadMore />
      </PageDataProvider>
    </>
  )
}

export {MasterLayout}


