/* eslint-disable jsx-a11y/anchor-is-valid */
import {FC} from 'react'
import {shallowEqual, useSelector} from 'react-redux'
import {Link} from 'react-router-dom'
import {UserModel} from '../modules/auth/models/UserModel'
import {RootState} from '../../setup'
import { FormattedMessage } from 'react-intl'

const HeaderUserMenu: FC = () => {
  const user: UserModel = useSelector<RootState>(({auth}) => auth.user, shallowEqual) as UserModel
  const { userImage, isAdmin } : any = useSelector<RootState>(
    ({auth}) => ({
      userImage: auth.userImages.userImage,
      isAdmin: auth.user?.isAdmin
    })
  )
 
  return (
    <div
      className='menu menu-sub menu-sub-dropdown menu-column menu-rounded menu-gray-600 menu-state-bg menu-state-primary fw-bold py-4 fs-6 w-275px'
      data-kt-menu='true'
    >
      <div className='menu-item px-3'>
        <div className='menu-content d-flex align-items-center px-3'>
          <div className='symbol symbol-50px me-5'>
            <img alt='Logo' src={`${process.env.REACT_APP_API_USER_IMAGE_URL}/${userImage ? userImage : 'blank.png'}`} />
          </div>

          <div className='d-flex flex-column'>
            <div className='fw-bolder d-flex align-items-center fs-5'>
              {user.fullname}
            </div>
            <span className='fw-bold text-muted text-hover-primary fs-7'>
              {user.email}
            </span>
          </div>
        </div>
      </div>

      <div className='separator my-2'></div>

      {isAdmin == '1' && 
        <>
          <div className='menu-item px-5'>
            <Link to={'/your-collections'} className='menu-link px-5'>
              <FormattedMessage id="HEADER_USER_MUENU.COLLECTIONS" />
            </Link>
          </div>

          <div className='menu-item px-5'>
            <Link to={'/view-artists'} className='menu-link px-5'>
              <FormattedMessage id="HEADER_USER_MUENU.ARTISTS" />
            </Link>
          </div>
        </>
      }

      <div className='menu-item px-5'>
        <Link to={'/profile'} className='menu-link px-5'>
          <FormattedMessage id="HEADER_USER_MUENU.PROFILE" />
        </Link>
      </div>

      <div className='menu-item px-5'>
        <Link to={'/account?tab=settings'} className='menu-link px-5'>
          <FormattedMessage id="HEADER_USER_MUENU.SETTINGS" />
        </Link>
      </div>

      {isAdmin == '1' && 
        <>
          <div className='menu-item px-5'>
            <Link to={'/view-orders'} className='menu-link px-5'>
              View Orders
            </Link>
          </div>
          <div className='menu-item px-5'>
            <Link to={'/view-transfers'} className='menu-link px-5'>
              View Transfers
            </Link>
          </div>
          <div className='menu-item px-5'>
            <Link to={'/view-users'} className='menu-link px-5'>
              View Users
            </Link>
          </div>
          <div className='menu-item px-5'>
            <Link to={'/update-metadata'} className='menu-link px-5'>
              Update Metadata
            </Link>
          </div>
        </>
      }

      <div className='menu-item px-5'>
        <Link to='/logout' className='menu-link px-5 text-danger'>
          <FormattedMessage id="HEADER_USER_MUENU.SIGNOUT" />
        </Link>
      </div>
    </div>
  )
}

export {HeaderUserMenu}
