import { FormattedMessage } from "react-intl"
import { useNavigate } from "react-router-dom"


export const AboutAndBreakdownSection = (props: any) => {
    const navigate = useNavigate()
    return (
        <>
            <div 
                className="mx-auto d-flex about-and-breakdown flex-wrap justify-content-between"
                style={{ marginTop: '50px', width: '70%' }}
            >
                <div className="collection-breakdown mb-10">
                    <div 
                        className="d-flex flex-column"
                        style={{ padding: '40px 40px' }}
                    >
                        <span className="flex-start asset-type"><FormattedMessage id="COLLECTION_DETAIL_PAGE.BREAKDOWN" /></span>

                        <hr className="w-25" />

                        <div className="d-flex flex-column my-7">
                            <h4 className="fs-breakdown text-muted uppercase lt-spacing"><FormattedMessage id="ARTIST_DETAIL.TOTAL_TOKENS" /></h4>
                            <h4 className="pt-2 font-compressedsans text-heading-h4 antialiased font-light text-contrast-default">{props.totalTokenQuantity}</h4>
                            <h4 className="pt-2 fs-breakdown text-muted uppercase lt-spacing">
                                {props.totalTiers} 
                                <span className="ms-2"><FormattedMessage id="COLLECTION_DETAIL_PAGE.TIERS" /></span>
                            </h4>
                        </div>

                        <div className="d-flex flex-column mb-7">
                            <h4 className="fs-breakdown text-muted uppercase lt-spacing">
                                <FormattedMessage id="COLLECTION_DETAIL_PAGE.TOTAL_OWNERSHIP_OFFERED" />
                            </h4>
                            <h4 className="pt-2 font-compressedsans text-heading-h4 antialiased font-light text-contrast-default">{props.totalOwnershipOffered}%</h4>
                            <h4 className="pt-2 fs-breakdown text-muted uppercase lt-spacing">
                                <FormattedMessage id="COLLECTION_DETAIL_PAGE.STREAMING_ROYALTY" />
                            </h4>
                        </div>

                        <div className="d-flex flex-column mb-7">
                            <h4 className="fs-breakdown text-muted uppercase lt-spacing">
                                <FormattedMessage id="COLLECTION_DETAIL_PAGE.AMOUNT_RAISED" />
                            </h4>
                            <h4 className="pt-2 font-compressedsans text-heading-h4 antialiased font-light text-contrast-default">${props.primarySaleAmount}</h4>
                            <h4 className="pt-2 fs-breakdown text-muted lt-spacing">USD</h4>
                        </div>
                    </div>
                </div>

                <div className="collection-about mb-10">
                    <div style={{ padding: '40px 40px' }}>
                        <span className="flex-start asset-type"><FormattedMessage id="ADMIN_COLLECTION_PREVIEW.ABOUT" /></span>

                        <hr className="w-25" />

                        <div className="d-flex flex-column mt-5">
                            <div className="d-flex justify-content-between mt-2">
                                <span className="fs-6"><FormattedMessage id="ADMIN_COLLECTION_PREVIEW.EDITION" /></span>
                                <span className="fs-6">{props.collectionName}</span>
                            </div> 
                            <div className="d-flex justify-content-between mt-2">
                                <span className="fs-6"><FormattedMessage id="ADMIN_COLLECTION_PREVIEW.BLOCKCHAIN" /></span>
                                <span className="fs-6">Ethereum Goerli Testnet</span>
                            </div>
                            <div className="d-flex justify-content-between mt-2">
                                <span className="fs-6"><FormattedMessage id="COLLECTION_DETAIL_PAGE.CONTRACT_ADDRESS" /></span>
                                <a 
                                    href="https://goerli.etherscan.io/address/0xfaC99d78d5Be34bAE059BF1643539621dB935B3e"
                                    className="text-decoration-none cursor-pointer fs-6"
                                >
                                    0xfaC9...935B3e
                                    <span>
                                    <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke="currentColor" aria-hidden="true" className="cursor-pointer ml-1 h-svg w-svg">
                                        <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M13 7l5 5m0 0l-5 5m5-5H6"></path>
                                    </svg>
                                    </span>
                                </a>
                            </div>
                            <div className="d-flex justify-content-between mt-2">
                                <span className="fs-6"><FormattedMessage id="ADMIN_COLLECTION_PREVIEW.LEGAL" /></span>
                                <a 
                                    className="text-decoration-none cursor-pointer fs-6"
                                    href={`${process.env.REACT_APP_LEGAL_DOCUMENTS_URL}/${props.collectionID + '_legal_docs.pdf'}`}
                                    // onClick={() => navigate('/legal-documents', { state: props.collectionID })}
                                >
                                    <FormattedMessage id="ADMIN_COLLECTION_PREVIEW.READ_NOW" />
                                    <span>
                                    <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke="currentColor" aria-hidden="true" className="cursor-pointer ml-1 h-svg w-svg">
                                        <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M13 7l5 5m0 0l-5 5m5-5H6"></path>
                                    </svg>
                                    </span>
                                </a>
                            </div>

                            <div style={{ marginTop: '50px' }}>
                                <span className="asset-description">
                                    <FormattedMessage id="COLLECTION_DETAIL_PAGE.TOKENS.PHRASE.LINE1" /> 
                                    <FormattedMessage id="COLLECTION_DETAIL_PAGE.TOKENS.PHRASE.LINE2" /> 
                                    <FormattedMessage id="COLLECTION_DETAIL_PAGE.TOKENS.PHRASE.LINE3" /> 
                                </span>
                                <hr style={{ marginTop: '50px' }}/>
                            </div>
                        </div>
                        {/* <hr className="w-25 mx-auto" style={{ marginTop: '50px' }} /> */}
                    </div>
                </div>
            </div>
            <hr style={{ marginTop: '50px' }}/>
        </>
    )
}