import { useEffect, useState } from "react"
import CircularProgress from '@mui/material/CircularProgress';
import { DropDetailTopSection } from "./components/TopSection"
import { DropDetailTokenSection } from "./components/TokenSection"
import { DropDetailOwnersSection } from "./components/OwnersSection"
import { DropDetailInfoSection } from "./components/InfoSection"
import { DropDetailBreakdownSection } from "./components/BreakdownSection"
import { DropDetailAboutSection } from "./components/AboutSection"
import { DropDetailTokenSectionLarge } from "./components/TokenSectionLarge";
import { AboutAndBreakdownSection } from "./components/AboutAndBreakdownSection";
import { getCollectionData, getSubsequentTierBalance } from './queries/Queries'
import { useSelector, shallowEqual, useDispatch } from "react-redux";
import { RootState } from "setup";
import * as auth from '../../auth/redux/AuthRedux'


export const DropDetailPageEthereum = (props: any) => {

    const [collectionData, setCollectionData] = useState<any>({})
    const [tierBalanceData, setTierBalanceData] = useState<any>([])
    const [loading, setLoading] = useState<boolean>(true)
    const [skipNumber, setSkipNumber] = useState<number>(0)
    const [refBtnLoading, setRefBtnLoading] = useState<boolean>(false)
    const [filterLoading, setFilterLoading] = useState<boolean>(false)
    const [hideSeeMoreBtn, setHideSeeMoreBtn] = useState<boolean>(false)

    const [dialogOpen, setDialogOpen] = useState<boolean>(false)
    const [selectedTier, setSelectedTier] = useState<any>('')
    const [price, setPrice] = useState<any>('')
    const [priceCrypto, setPriceCrypto] = useState<any>('')
    const [selectedColID, setSelectedColID] = useState<any>('')
    const [selectedColName, setSelectedColName] = useState<any>('')
    const [selectedImageName, setSelectedImageName] = useState<string>('')

    const dispatch = useDispatch()

    const { tierFilterVal, userID }: any = useSelector<RootState>(
        ({auth}) => ({
            tierFilterVal: auth.filterVal,
            userID: auth.user?.userID
        }), shallowEqual
    )

    useEffect(() => {
        getCollectionData(props.collectionID, 'all')
        .then((collection) => {
            setCollectionData(collection)
            setTierBalanceData(collection.tokensBalances)
            setLoading(false)
            setSkipNumber(num => {
                return num + collection.tokensBalances.length
            })
        })
        .catch((err) => console.log(err))

        // return () => {dispatch(auth.actions.setDropDetailTier(undefined))}
    }, [])

    console.log(skipNumber)

    useEffect(() => {
        if(tierFilterVal) {
            getCollectionData(props.collectionID, tierFilterVal)
            .then((collection) => {
                setCollectionData(collection)
                setTierBalanceData(collection.tokensBalances)
                setFilterLoading(false)
                setSkipNumber(num => {
                    return collection.tokensBalances.length
                })
                if(collection.tokensBalances.length < 1) {
                    setHideSeeMoreBtn(true)
                } else {
                    setHideSeeMoreBtn(false)
                }
            })
            .catch((err) => console.log(err))
        }
    }, [tierFilterVal])

    const refreshData = () => {
        setRefBtnLoading(true)
        getCollectionData(props.collectionID, tierFilterVal)
        .then((collection) => {
            setCollectionData(collection)
            setTierBalanceData(collection.tokensBalances)
            setRefBtnLoading(false)
            setSkipNumber(num => {
                return num + collection.tokensBalances.length
            })
        })
        .catch((err) => console.log(err))
    }

    const getTier = (id: string) => {
        const tier = collectionData.tokensData.find((data: any) => data.id == id).tier;
        return tier
    }

    const getTotalSupply = (id: string) => {
        const totalSupply = collectionData.tokensData.find((data: any) => data.id == id).totalSupply;
        return totalSupply
    }
     
    return (
        <>
            {!loading && 
                <div className="container-fluid bg-white">
                    <div style={{ marginTop: '-20px' }} className="d-flex flex-column justify-content-center">
                        <DropDetailTopSection 
                            dropReleaseTime={props.collectionMeta.drop_release_time}
                            imageName={props.collectionMeta.collection_image}
                            collectionName={props.collectionMeta.collection_name}
                            collectionType={props.collectionMeta.collection_type}
                            collectionDescription={props.collectionMeta.collection_description}
                            spotifyURL={props.collectionMeta.spotify_url}
                            artistName={props.artistName}
                            artistImage={props.artistImage}
                            artistID={props.collectionMeta.artist_id}
                        />

                    
                        <div className="mx-auto" style={{ width: '100%' }}>
                            
                            <DropDetailTokenSection 
                                collectionID={props.collectionMeta.id}
                                collectionOwner={props.collectionMeta.collection_owner}
                                collectionName={props.collectionMeta.collection_name}
                                artistName={props.artistName}
                                blockchain={props.collectionMeta.blockchain}
                                dropReleaseTime={props.collectionMeta.drop_release_time}
                                tokensMeta={props.collectionMeta.tiersData}
                                tokensData={collectionData.tokensData}
                                setCollectionData={setCollectionData}
                                setLoading={setLoading}
                                refreshData={refreshData}
                                refBtnLoading={refBtnLoading}
                                handleReadMoreClick={props.handleReadMoreClick}
                                setSelectedTier={setSelectedTier}
                                setSelectedColID={setSelectedColID}
                                setSelectedColName={setSelectedColName}
                                setPrice={setPrice}
                                setSelectedImageName={setSelectedImageName}
                                setDialogOpen={setDialogOpen}
                                dialogOpen={dialogOpen}
                                selectedTier={selectedTier}
                                price={price}
                                priceCrypto={priceCrypto}
                                setPriceCrypto={setPriceCrypto}
                                selectedColID={selectedColID}
                                selectedColName={selectedColName}
                                selectedImageName={selectedImageName}
                                fiatPrice={props.fiatPrice}
                                userID={userID}
                            />

                            <DropDetailTokenSectionLarge 
                                collectionID={props.collectionMeta.id}
                                collectionOwner={props.collectionMeta.collection_owner}
                                collectionName={props.collectionMeta.collection_name}
                                artistName={props.artistName}
                                blockchain={props.collectionMeta.blockchain}
                                dropReleaseTime={props.collectionMeta.drop_release_time}
                                tokensMeta={props.collectionMeta.tiersData}
                                tokensData={collectionData.tokensData}
                                setCollectionData={setCollectionData}
                                setLoading={setLoading}
                                refreshData={refreshData}
                                refBtnLoading={refBtnLoading}
                                handleReadMoreClick={props.handleReadMoreClick}
                                setSelectedTier={setSelectedTier}
                                setSelectedColID={setSelectedColID}
                                setSelectedColName={setSelectedColName}
                                setPrice={setPrice}
                                setSelectedImageName={setSelectedImageName}
                                setDialogOpen={setDialogOpen}
                                dialogOpen={dialogOpen}
                                selectedTier={selectedTier}
                                price={price}
                                priceCrypto={priceCrypto}
                                setPriceCrypto={setPriceCrypto}
                                selectedColID={selectedColID}
                                selectedColName={selectedColName}
                                selectedImageName={selectedImageName}
                                fiatPrice={props.fiatPrice}
                                userID={userID}
                            />

                            <AboutAndBreakdownSection 
                                totalTiers={collectionData.tokensData.length}
                                totalTokenQuantity={collectionData.totalTokenQuantity}
                                totalOwnershipOffered={collectionData.totalOwnershipOffered}
                                primarySaleAmount={collectionData.primarySaleAmountInFiat}
                                collectionName={props.collectionMeta.collection_name}
                                collectionID={props.collectionMeta.id}
                            />

                            {/* <DropDetailBreakdownSection 
                                totalTiers={collectionData.tokensData.length}
                                totalTokenQuantity={collectionData.totalTokenQuantity}
                                totalOwnershipOffered={collectionData.totalOwnershipOffered}
                                primarySaleAmount={collectionData.primarySaleAmountInFiat}
                            />

                            <DropDetailAboutSection />

                            <DropDetailInfoSection /> */}

                            <DropDetailOwnersSection 
                                getTier={getTier}
                                getTotalSupply={getTotalSupply}
                                balancesData={tierBalanceData}
                                collectionOwner={collectionData.collection.collectionOwner}
                                setCollectionData={setCollectionData}
                                setTierBalanceData={setTierBalanceData}
                                setSkipNumber={setSkipNumber}
                                skipNumber={skipNumber}
                                tierFilterVal={tierFilterVal}
                                collectionID={props.collectionMeta.id}
                                blockchain={props.collectionMeta.blockchain}
                                refreshData={refreshData}
                                refBtnLoading={refBtnLoading}
                                filterLoading={filterLoading}
                                setFilterLoading={setFilterLoading}
                                hideSeeMoreBtn={hideSeeMoreBtn}
                                setHideSeeMoreBtn={setHideSeeMoreBtn}
                            />
                        </div>
                    </div>
                </div>
            }
            {loading && 
                <div className='d-flex w-100 h-100'>
                    <div className='my-auto mx-auto'>
                        <div className='d-flex flex-column justify-content-center'>
                            <span className='text-primary fs-3'>Loading</span> <br />
                            <span className='ms-4'>
                                <CircularProgress />
                            </span>
                        </div>
                    </div>
                </div>
            }
        </>
    )
}