
import { FormattedMessage, useIntl } from 'react-intl'
import { collectionType } from '../../../yourCollections/common/common'

export const CollectionCard = (props: any) => {
  const intl = useIntl()
  return (
      <>
         <div 
              className="card card-custom gutter-b card-stretch mx-9 my-4 product-col-width"
              style={{ 
                  width: '330px', 
                  borderRadius: '5px'
              }}
          >
              <a 
                href={`drop-detail?id=${props.id}`}
                style={{ padding: '0px' }}
                className="card-body d-flex flex-column bg-white rounded product-card"
              >
                  <div className="text-center w-100 h-100">
                      <img 
                        src={props.imagePath} 
                        className="w-100 h-100" 
                        style={{ 
                            borderTopLeftRadius: '5px',
                            borderTopRightRadius: '5px',
                            textAlign: 'center'
                        }}
                      />
                  </div>
                  <div className="bg-dark py-2 ps-2">
                        <div className='d-flex justify-content-between px-2' style={{ color: 'white' }}>
                          <div className='d-flex'>
                            <span>
                                {props.noOfTiers} 
                                <span className='ms-1' style={{ letterSpacing: '1px' }}>
                                  {parseInt(props.noOfTiers) > 1 ? intl.formatMessage({ id: 'CREATE_EDIT_COLLECTION.TIER_PLURAL' }) : intl.formatMessage({ id: 'CREATE_EDIT_COLLECTION.TIER' })}
                                </span>
                            </span>
                            <span className='mx-2'> | </span>
                            <span>
                              {props.totalNumOfTokens} 
                              <span className='ms-1' style={{ letterSpacing: '1px' }}>
                                <FormattedMessage id='ADMIN_COLLECTION_PREVIEW.TOKENS_LC' />
                              </span>
                            </span>
                          </div>
                          <span style={{ letterSpacing: '1px' }}>{props.blockchain == 'ethereum' ? 'Ethereum' : 'Klaytn'}</span>
                        </div>
                  </div>
                  <div className="d-flex justify-content-around py-3">
                      <div>
                          <img 
                              className="w-50px"
                              style={{ borderRadius: '50%' }}
                              src={props.artistImagePath} 
                              alt={props.artistName}
                          />
                      </div>
                      <div className="mt-2">
                          <span style={{ color: 'black' }}>{props.collectionName}</span><br/>
                          <span className="text-muted">{props.artistName}</span>
                      </div>
                      <div className="mt-2">
                          <span className="text-muted">{collectionType[props.collectionType]}</span>
                      </div>
                  </div>
              </a>
          </div>
      </>
  )
}
 