import { useState } from 'react'
import { Document, Page } from 'react-pdf/dist/esm/entry.webpack'
import { useLocation } from 'react-router-dom';
import { toAbsoluteUrl } from '_metronic/helpers';


export const LegalDocuments = () => {

    const { state }: any = useLocation()

    const [numPages, setNumPages] = useState<any>(null);

    const onDocumentLoadSuccess = ({numPages}: any) => {
        setNumPages(numPages);
    }

    return (
        <>
            <div className='d-flex justify-content-center w-100 bg-dark'>
                <Document file={`${process.env.REACT_APP_LEGAL_DOCUMENTS_URL}/${state + '_legal_docs.pdf'}`} onLoadSuccess={onDocumentLoadSuccess}>
                    <div className='w-70 mx-auto my-7'>
                        {Array.from(
                            new Array(numPages),
                            (el,index) => (
                                <Page 
                                    key={`page_${index+1}`}
                                    pageNumber={index+1}
                                    className='w-100 my-7'
                                />
                            )
                        )}
                    </div>
                </Document>
            </div>
        </>
    )
}