import React, { useState} from "react";
import { FormattedMessage } from "react-intl";
import { useSelector, shallowEqual} from "react-redux";
import {RootState} from "../../../../../setup"

interface IMinMax {
    min: string
    max: string
}

function changeAction(id: string, height: string, display: string) {
    var element: HTMLElement = document.getElementById(id)!
    if(element.style.height !== "0px") {
        element.style.height = "0px";
        setTimeout(() => {
            var element: HTMLElement = document.getElementById(id)!
            element.style.display = "none";
        }, 500);
    } else {
        element.style.display = display;
        setTimeout(() => {
            var element: HTMLElement = document.getElementById(id)!
            element.style.height = height;
        }, 100);
    }
    
}

function openNav() {
    const element1: HTMLElement = document.getElementById("open-sidenav-sub")!
    const element2: HTMLElement = document.getElementById("mySidenav")!
    const element3: HTMLElement = document.getElementById("main")!
    element1.style.width = "0";
    element2.style.width = "320px";
    element3.style.marginLeft = "320px";
}

function closeNav() {
    const element1: HTMLElement = document.getElementById("mySidenav")!
    const element2: HTMLElement = document.getElementById("main")!
    const element5: HTMLElement = document.getElementById("open-sidenav-sub")!
    element1.style.width = "0";
    element2.style.marginLeft= "40px";
    setTimeout(() => {
        element5.style.width = "45px";
    }, 300);
}

function ProductsSideBar(props: any) {
    const initialValues: IMinMax = {
        min: "", 
        max: ""
    }
    const [minMaxValue , setMinMaxValue] = useState(initialValues)
    
    const { categories } : any = useSelector<RootState>(
        ({ecom}) => ({
            categories: ecom.categories
        }), shallowEqual
    )

    function handleChange(event: any) {
        event.preventDefault();
        if(event.target.name === "min-amount") {
            setMinMaxValue({...minMaxValue, min : event.target.value})
        } else {
            setMinMaxValue({...minMaxValue, max : event.target.value})
        }
    }
 
    function handleSubmit(event: any) {
        event.preventDefault();
        props.maxMinFilter(minMaxValue.min, minMaxValue.max)
        setMinMaxValue(initialValues);
    }

    const handleBlockchainFilterValueChange = (filterVal: string) => {
        props.setLoading(true)
        if(props.blockchainFilterAdded) {
            if(props.blockchainFilterVal != filterVal) {
                props.setBlockchainFilterVal(filterVal)
            } else {
                props.setBlockchainFilterVal(null)
                props.setBlockchainFilterAdded(false)
            }
        } else {
            props.setBlockchainFilterVal(filterVal)
            props.setBlockchainFilterAdded(true)
        }
    }

    const handleArtistFilterValueChange = (filterVal: string) => {
        props.setLoading(true)
        if(props.artistFilterAdded) {
            if(props.artistFilterVal != filterVal) {
                props.setArtistFilterVal(filterVal)
            } else {
                props.setArtistFilterVal(null)
                props.setArtistFilterAdded(false)
            }
        } else {
            props.setArtistFilterVal(filterVal)
            props.setArtistFilterAdded(true)
        }
    }
     
    return (
        <>
        <div id="mySidenav" className="canvas-sidenav">
            <div className="d-flex justify-content-between my-3 mx-3">
                <label className="font-size-lg text-dark-75 font-weight-bold mt-2">
                    <svg xmlns="http://www.w3.org/2000/svg" width="25" height="25" fill="currentColor" className="bi bi-filter mb-2 me-2" viewBox="0 0 16 16">
                        <path d="M6 10.5a.5.5 0 0 1 .5-.5h3a.5.5 0 0 1 0 1h-3a.5.5 0 0 1-.5-.5zm-2-3a.5.5 0 0 1 .5-.5h7a.5.5 0 0 1 0 1h-7a.5.5 0 0 1-.5-.5zm-2-3a.5.5 0 0 1 .5-.5h11a.5.5 0 0 1 0 1h-11a.5.5 0 0 1-.5-.5z"/>
                    </svg> 
                    <span 
                        className="ml-2 fw-bold fs-5"
                        // style={{ 
                        //     fontFamily: 'Playball, cursive', 
                        //     fontSize: '22px'
                        // }} 
                    >
                        <FormattedMessage id="EXPLORE.SIDE_NAV.FILTER" />
                    </span>
                </label>
                <span className="mt-2" style={{ cursor: "pointer" }} onClick={closeNav}>
                    <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-arrow-left" viewBox="0 0 16 16">
                        <path fillRule="evenodd" d="M15 8a.5.5 0 0 0-.5-.5H2.707l3.147-3.146a.5.5 0 1 0-.708-.708l-4 4a.5.5 0 0 0 0 .708l4 4a.5.5 0 0 0 .708-.708L2.707 8.5H14.5A.5.5 0 0 0 15 8z"/>
                    </svg>
                </span>
            </div>
            {/* Accordion starts */}
            <div className="d-flex flex-column" style={{ width: "100%" }}>
                <div className="d-flex justify-content-between cursor-pointer summary-small border-bg-201" onClick={() => changeAction("kt_patent_sidebar_large", "70px", "block")}>
                    <span 
                        className="fw-bold" 
                        // style={{ fontFamily: 'Playball, cursive', fontSize: '20px'}} 
                    >
                        <FormattedMessage id="EXPLORE.SIDE_NAV.BLOCKCHAIN" />
                    </span>
                    <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-chevron-down mt-2" viewBox="0 0 16 16">
                        <path fillRule="evenodd" d="M1.646 4.646a.5.5 0 0 1 .708 0L8 10.293l5.646-5.647a.5.5 0 0 1 .708.708l-6 6a.5.5 0 0 1-.708 0l-6-6a.5.5 0 0 1 0-.708z"/>
                    </svg>
                </div>
                <div id="kt_patent_sidebar_large" style={{ height: "0px", display: "none" }}  className="abstract-box abstract-scroll">   
                    <div className="d-flex flex-column h-130px border-bg-201 bg-content">
                        <div className="d-flex flex-row justify-content-around py-4 px-3">
                            <button 
                                onClick={() => handleBlockchainFilterValueChange('ethereum')} 
                                className={`btn px-9 text-dark ${props.blockchainFilterVal && props.blockchainFilterVal == 'ethereum' ? 'active-btn' : 'not-active-btn'}`} 
                            >
                                Ethereum
                            </button>
                            <button 
                                onClick={() => handleBlockchainFilterValueChange('klaytn')} 
                                className={`btn text-dark ${props.blockchainFilterVal && props.blockchainFilterVal == 'klaytn' ? 'active-btn' : 'not-active-btn'}`}
                            >
                                Klaytn
                            </button>
                        </div>
                    </div>
                </div>
                <div className="d-flex justify-content-between cursor-pointer summary-small border-bg-201" onClick={() => changeAction("kt_categories_sidebar_large", "150px", "block")}>
                    <span 
                        className="fw-bold" 
                        // style={{ fontFamily: 'Playball, cursive', fontSize: '20px'}} 
                    >
                        <FormattedMessage id="EXPLORE.SIDE_NAV.ARTISTS" />
                    </span>
                    <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-chevron-down mt-2" viewBox="0 0 16 16">
                        <path fillRule="evenodd" d="M1.646 4.646a.5.5 0 0 1 .708 0L8 10.293l5.646-5.647a.5.5 0 0 1 .708.708l-6 6a.5.5 0 0 1-.708 0l-6-6a.5.5 0 0 1 0-.708z"/>
                    </svg>
                </div>
                <div id="kt_categories_sidebar_large" style={{ height: "0px", display: "none" }}  className="abstract-box abstract-scroll bg-content">   
                    <div className="h-150px border-bg-201">
                        <div className="checkbox-list category-list">
                            {props.artistList.map((artist: any) => (
                                <span 
                                    id={`category-${artist.id}`} 
                                    key={artist.id} 
                                    onClick={() => handleArtistFilterValueChange(artist.id)} 
                                    style={{ cursor: "pointer" }} 
                                    className={`fs-6 py-2 ps-3 ${props.artistFilterVal === artist.id && 'active-category'}`}
                                >
                                    {artist.artist_name}
                                </span>
                            ))}
                        </div>
                    </div>
                </div>
                {/* <div className="d-flex justify-content-between cursor-pointer summary-small border-bg-201" onClick={() => changeAction("kt_price_sidebar_large", "150px", "block")}>
                    <span 
                        className="fw-bold" 
                        // style={{ fontFamily: 'Playball, cursive', fontSize: '20px'}} 
                    >
                        Price
                    </span>
                    <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-chevron-down mt-2" viewBox="0 0 16 16">
                        <path fillRule="evenodd" d="M1.646 4.646a.5.5 0 0 1 .708 0L8 10.293l5.646-5.647a.5.5 0 0 1 .708.708l-6 6a.5.5 0 0 1-.708 0l-6-6a.5.5 0 0 1 0-.708z"/>
                    </svg>
                </div>
                <div id="kt_price_sidebar_large" style={{ height: "0px", display: "none" }}  className="abstract-box abstract-scroll">   
                    <div style={{ position : "relative" }} className="h-150px border-bg-201 bg-content">
                        <div className="d-flex flex-column px-3 py-7">
                            <form onSubmit={(event) => handleSubmit(event)}>
                                <div className="d-flex form-group mb-6 flex-row">
                                    <input onChange={(event) => handleChange(event)} style={{ border: "1px solid rgb(184, 169, 169)" }} className="w-100px h-30px text-center rounded" type="text" name="min-amount" value={minMaxValue.min} placeholder="Min" />
                                    <label className="text-center mt-2 mx-2">To</label>
                                    <input onChange={(event) => handleChange(event)} style={{ border: "1px solid rgb(184, 169, 169)" }} className="w-100px h-30px text-center rounded" type="text" name="max-amount" value={minMaxValue.max} placeholder="Max" />
                                </div>
                                <button type="submit" className="btn bg-cust-green font-weight-bolder mr-2 px-8">Apply</button>
                            </form>
                        </div>
                    </div>
                </div> */}
            </div>
            {/* Accordion Ends */}
        </div>
        <div id="open-sidenav-sub" className="open-canvas-sidenav" onClick={openNav} style={{ width: '0px' }}>
            <span id="arrow-button-1" style={{ fontSize: "30px", cursor: "pointer", marginLeft: "10px" }}>
                <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-arrow-right arrow-button" viewBox="0 0 16 16">
                    <path fillRule="evenodd" d="M1 8a.5.5 0 0 1 .5-.5h11.793l-3.147-3.146a.5.5 0 0 1 .708-.708l4 4a.5.5 0 0 1 0 .708l-4 4a.5.5 0 0 1-.708-.708L13.293 8.5H1.5A.5.5 0 0 1 1 8z"/>
                </svg>
            </span>
        </div>
    </>
    )
}

export default ProductsSideBar;
