import React, {useEffect} from 'react'
import {useSelector, shallowEqual, useDispatch} from 'react-redux'
import {useNavigate} from 'react-router-dom'
import {RootState} from '../../../../setup/'
import {verifyUser} from '../redux/AuthCRUD'
import * as auth from '../redux/AuthRedux'
import {UserModel} from '../models/UserModel'

const params = new URLSearchParams(window.location.search);

const token: string | null = params.get("token");

function VerifyUser() {
    const {authToken}: any = useSelector<RootState>(
        ({auth}) => ({
            authToken: auth.accessToken
    }), shallowEqual)

    const dispatch = useDispatch()
    const navigate = useNavigate()

    useEffect(() => {
        verifyUser(token, authToken)
            .then((response) => {
                if(response.data.success) {
                    const user: UserModel = {
                        userID: response.data.user.id,
                        userImage: response.data.user.avatar,
                        fullname: response.data.user.full_name,
                        username: response.data.user.username,
                        email: response.data.user.email,
                        isVerified: response.data.is_verified,
                        description: response.data.user.description,
                        isAdmin: response.data.user.is_admin
                    }
                    dispatch(auth.actions.setUser(user))
                    localStorage.setItem('verifyComplete', response.data.message);
                    if(localStorage.getItem('emailNotVerified')) {
                      localStorage.removeItem('emailNotVerified')
                    }
                    navigate('/');
                } else {
                    if(response.data.message) {
                        localStorage.setItem('verifynotcomplete', response.data.message);
                        navigate('/user-not-verified');
                    } else {
                        localStorage.setItem('verifynotcomplete', "Email verification failed");
                        navigate('/user-not-verified');
                    }
                }
            })
            .catch((error) => {
                localStorage.setItem('sessionTimeOut', error);
                auth.actions.logout();
            })
    }, [])

    return (
        <></>
    )
}

export {VerifyUser}